.interactive :global {
  animation: fade-in-out 0.3s var(--breadcrumb-text-anim-tf-exit) forwards;
}

.current {
  font-weight: var(--breadcrumb-text-font-weight-thick);
}

.interactive:hover {
  text-decoration: underline;
  cursor: pointer;
}

.item-text {
  user-select: none;
  font-size: var(--breadcrumb-text-font-size-m);
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  text-decoration: none;
  cursor: default;
  color: var(--breadcrumb-text-fg);
  padding: var(--breadcrumb-squish-xs);
  display: block;
  font-family: var(--breadcrumb-text-font-family);
}

.single-item {
  display: flex;
  align-items: center;
  max-inline-size: 100%;
}

.single-item-focus {
  outline: var(--breadcrumb-text-focus);
  outline-offset: calc(-1 * var(--focus-width));
}

.exit-animation :global {
  animation: slide-left var(--breadcrumb-text-anim-dur-enter) var(--breadcrumb-text-anim-tf-enter);
}

@keyframes :global(fade-in-out) {
  from {
    opacity: 0.6;
  }
  to {
    opacity: 1;
  }
}

