/**
 * Importing the normalize.css in the :root scope breaks in the case of the
 * `html` selector. This renders out as `:root html` which does not actually
 * select the `html` element in the DOM since `html` is not a descendant of
 * `:root` (they are the same element). To remedy this, we re-specify certain
 * styles that we would have received from the `@import` below.
 */
:root {
  // This differs from normalize.css which sets the line-height 1.15. 'normal' is more appropriate for our use.
  line-height: normal;
  -webkit-text-size-adjust: 100%;
}

:root {
  @import '../../../../node_modules/normalize.css/normalize';

  // These elements have line-height set to 1.15 in normalize.css. 'normal' is more appropriate for our use.
  button,
  input,
  optgroup,
  select,
  textarea {
    line-height: inherit;
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    line-height: inherit;
    margin: 0;
  }

  ul,
  ol {
    margin: 0;
  }
  hr {
    margin: 0;
  }

  a {
    color: inherit;
  }

  white-space: normal;

  :focus-visible {
    outline: none;
  }
}
