@use 'utils/animation';

%node-icon {
  flex-shrink: 0;
  margin-inline-end: var(--tree-spacing-xs);
}

.item {
  display: flex;
  align-items: center;
  max-width: 100%;
  position: relative;
  padding: var(--tree-spacing-xxs) 0;

  > .node-label,
  > .node-icon,
  > .expand-icon {
    min-width: 0;
    z-index: 0;
  }

  > .node-label {
    align-items: baseline;
    font-size: var(--tree-dnd-font-size-m);
    user-select: none;
  }

  .node-icon {
    @extend %node-icon;
    cursor: default;
  }

  .expand-icon {
    @extend %node-icon;
    margin-block-start: var(--tree-spacing-xs);
  }

  .expand-icon-cursor-pointer {
    cursor: pointer;
  }

  .expand-icon:focus {
    outline: none;
  }
}

.item:focus {
  outline: var(--focus-light-bg);
  outline-offset: calc(-1 * var(--focus-width));
}

.selected {
  background-color: var(--tree-selected-bg);
  filter: var(--tree-selected-filter);
}
