$token-height: 1.5rem;
$token-margin: 0.25rem;

.container {
  padding: 0 $token-margin;
}

.wrap {
  display: flex;
  flex-wrap: wrap;
  overflow: auto;
  // this formula makes the container overflow after displaying maxinum rows (3.5 rows by default) of tokens
  max-height: calc((#{$token-height} + #{$token-margin} * 2) * var(--tokenizer-max-lines, 3.5));
  white-space: pre-wrap;
}

.children-container {
  display: inline-flex;
  flex: 1;
}

.separator {
  margin: 0 0.5rem;
}

.token {
  margin: $token-margin;
  display: inline-flex;
  max-width: var(--token-max-width, min(calc(100% - 2 * #{$token-margin}), 25rem));
  // note min-width is needed to stablized the scrollable when resizing smaller
  min-width: 3.5rem;
  vertical-align: middle;
}

.busy-state-container {
  max-width: var(--token-max-width, min(100%, 25rem));
}
