.content {
  padding: var(--message-popover-pd-squish-s);
  font-size: var(--message-popover-font-size-m);
  white-space: normal;
  overflow-wrap: anywhere;
  word-break: break-word;
  display: block;
  background-color: var(--message-popover-bg);
  border: var(--message-popover-bd);
  color: var(--message-popover-fg);
}

.message-text {
  font-size: var(--message-popover-font-size);
}
