.label {
    word-break: unset;
    display: block;
    white-space: nowrap;
    font-size: var(--selections-font-size);
    color: var(--selections-title-fg);
    font-weight: bold;
}

.value {
    word-break: unset;
    display: block;
    white-space: nowrap;
    font-size: var(--selections-font-size);
    color: var(--selections-title-fg);
}

.title {
    word-break: unset;
    white-space: nowrap;
    font-size: var(--selections-font-size);
    color: var(--selections-title-fg);
    font-weight: bold;
    padding: 0.25rem 0.375rem;
}

.divider {
    height: 1px;
    background-color:  var(--selections-divider-bd);
}

.selections-container {
    margin: .5rem;
}