@use 'utils/offscreen';

.container {
  position: relative;
  display: inline-flex;
  font-size: 0.875rem;
  max-width: 100%;
}

.input {
  width: 100%;
  min-height: 1.75rem;
  min-width: 2rem;
  padding: var(--input-pd-squish-s);
  flex-grow: 1;
  background: var(--input-bg);
  color: var(--input-fg);
  cursor: text;
  border: 1px solid var(--input-bd);
  box-sizing: border-box;

  &:focus {
    outline: var(--input-focus-outline, var(--focus-light-bg));
    outline-offset: calc(-2px + var(--focus-width) * -1);
    &:not(.has-input-icon):not(.readonly):not(.disabled) {
      border-color: var(--input-focus-bd);
    }
  }

  &:invalid {
    box-shadow: none;
  }

  &::placeholder {
    color: var(--input-placeholder-fg);
    /* Override default opacity value in FireFox (** https://developer.mozilla.org/en-US/docs/Web/CSS/::placeholder#opaque_text **) */
    opacity: 1;
  }

  &::selection {
    color: var(--input-selection-fg);
    background-color: var(--input-selection-bg);
  }

  &::-ms-clear {
    display: none;
  }
}

.hide-border {
  border: none;
}

.input-icon {
  position: absolute;
  inset-block-start: 1px;
  inset-block-end: 1px;
  inset-inline-end: 1px;
  display: flex;
  align-items: center;
  margin: 0 var(--input-mg-xs);
  cursor: default;
}

.has-input-icon {
  padding-inline-end: 1.25rem;
}

.message {
  padding: var(--input-pd-squish-s);
  font-size: 0.668rem;
  white-space: normal;
  display: block;
}

.alert-message {
  @extend .message;
  background-color: var(--input-alert-message-bg);
  border: var(--input-alert-message-bd);
  color: var(--input-alert-message-fg);
}

.warning-message {
  @extend .message;
  background-color: var(--input-warning-message-bg);
  border: var(--input-warning-message-bd);
  color: var(--input-warning-message-fg);
}

.disabled {
  opacity: var(--input-disabled-opacity);
  cursor: default;
  color: var(--input-disabled-fg);
  border-color: var(--input-disabled-bd);
  &::placeholder {
    color: var(--input-disabled-fg);
  }
}

.readonly:not(.has-input-icon) {
  background-color: var(--input-readonly-bg);
  border-color: var(--input-readonly-bd);
  color: var(--input-readonly-fg);
  text-overflow: ellipsis;
  &:focus {
    outline-offset: calc(var(--focus-width) * -1);
  }
  &.disabled {
    color: var(--input-disabled-fg);
  }
}

.non-editable-input {
  background-color: var(--input-noneditable-bg);
  border-color: var(--input-noneditable-bd);
}

.readonly:not(.composite):not(.non-editable-input) {
  padding-inline-start: 0;
}

.composite {
  caret-color: transparent;

  &,
  &:hover {
    background-color: var(--input-noneditable-bg);
    border-color: var(--input-noneditable-bd);
    color: var(--input-noneditable-fg);
  }

  &:focus {
    border-color: var(--input-focus-noneditable-bd, var(--input-focus-bd));
  }
  &.disabled {
    color: var(--input-disabled-fg);
  }
}

.composite.readonly:not(.has-input-icon) {
  background-color: var(--input-noneditable-bg);
  border-color: var(--input-noneditable-bd);
  color: var(--input-noneditable-fg);
}

.rtl {
  text-align: right;
}

.offscreen {
  @extend %offscreen;
}
