.item {
  cursor: var(--list-row-cursor);
  display: flex;
  align-items: center;
  // TODO: List items should have squish-s padding, but Table does not adapt cell height to content
  // padding: var(--list-spacing-squish-s);
  box-sizing: border-box;
  text-decoration: var(--list-item-text-decoration);
  font-weight: var(--list-item-font-weight-default);
  text-decoration-color: var(--list-item-fg);
  font-style: var(--list-item-font-style);
  font-family: var(--list-item-font-family);

  .disabled & {
    text-decoration-color: var(--list-item-disabled-fg);
  }

  &.unread {
    font-weight: var(--list-item-font-weight-thick);
  }
}

%content-box {
  box-sizing: border-box;
  display: flex;
  min-height: 0.875rem;
  color: var(--list-item-fg);
  flex-grow: 1;
  min-width: 0;
}

.single-line {
  @extend %content-box;
  flex-direction: row;
  align-items: center;
  gap: var(--list-item-gap);
}

.double-lines {
  @extend %content-box;
  flex-direction: column;
}

.sub-section {
  display: flex;
  align-items: center;
  font-size: var(--list-item-font-size-s);
}

.base-text {
  min-width: 1.5rem;
  color: var(--list-item-fg);

  .disabled & {
    color: var(--list-item-disabled-fg);
  }
}

.item-text {
  @extend .base-text;

  font-size: var(--list-item-font-size-m);

  &.bold {
    font-weight: var(--list-item-font-weight-thick);

    .read & {
      font-weight: var(--list-item-font-weight-default);
    }
  }

  &.description {
    flex: auto;
    flex-grow: 1;
    font-size: var(--list-item-font-size-m);
  }

  &:not(.wrap) {
    white-space: var(--list-item-white-space, pre);
  }

  .infoHasPriority & {
    flex-shrink: 99999;
  }
}

.item-info-text {
  @extend .base-text;
  font-size: var(--list-item-font-size-s);
  flex-grow: 1;
  flex-basis: min-content;
  flex-shrink: 99999;
  color: var(--list-item-description-fg);

  &:not(.concat) {
    text-align: end;
  }

  .infoHasPriority & {
    flex: 1 1 auto;
  }
}

.item-description-text {
  @extend .base-text;
  font-size: var(--list-item-font-size-s);
  color: var(--list-item-description-fg);

  .infoHasPriority & {
    flex-shrink: 99999;
  }
}

.item-text-separator {
  color: var(--list-item-fg);
  flex-shrink: 0;
  padding-inline-start: var(--list-spacing-s);
  padding-inline-end: var(--list-spacing-s);
  font-size: var(--list-item-font-size-s);
}

.item-icon {
  flex-shrink: 0;

  // .iconColor is set inside SVGs for themeing purposes and is not specified
  // using `cn.iconColor`. Using :global() avoids this class name from becoming
  // a module (list scoped class name) and will, therefore, use the non-module
  // class name specified.
  :global(.iconColor) {
    --icon-default-fg: var(--list-item-fg);
  }
}

.item-avatar {
  flex-shrink: 0;
}

.counter {
  flex-shrink: 0;
  color: var(--list-item-fg);
  font-size: var(--list-item-font-size-m);
  margin-inline-start: var(--list-spacing-m);
  position: relative; // this will resolve selection text hidden issue in mac safari https://bugs.webkit.org/show_bug.cgi?id=200251

  .disabled & {
    color: var(--list-item-disabled-fg);
  }
}

.content-container {
  align-items: center;
  flex-grow: 1;
  min-width: 2rem;
  position: relative; // this will resolve selection text hidden issue in mac safari https://bugs.webkit.org/show_bug.cgi?id=200251

  .read & {
    // Align read/unread list items
    margin-inline-start: calc(0.875rem + var(--list-spacing-xs));
  }

  .infoHasPriority & {
    min-width: 1.5rem;
    flex-shrink: 99999;
  }

  .infoHasPriority &.hasItemIcon {
    min-width: 3rem;
  }

  .infoHasPriority &.hasItemAvatar {
    min-width: 3.5rem;
  }
}

.dot {
  align-self: flex-start;
  margin: var(--list-spacing-xxs) var(--list-spacing-xs) var(--list-spacing-xxs) 0;
  flex-shrink: 0;
}
