@use 'utils/offscreen';

.container {
  position: relative;
  display: inline-flex;
  font-size: var(--input-font-size);
  min-inline-size: var(--input-min-width);
  max-inline-size: 100%;
  width: 10.5rem;
}

.input {
  width: 100%;
  min-block-size: var(--input-min-height);
  padding: var(--input-pd);
  flex-grow: 1;
  background: var(--input-bg);
  color: var(--input-fg);
  cursor: text;
  border: 1px solid var(--input-bd);
  box-sizing: border-box;
  line-height: var(--input-line-height);
  font-family: var(--input-fontFamily, inherit);
  font-size: var(--input-fontSize, inherit);
  font-style: var(--input-fontStyle, inherit);
  font-weight: var(--input-fontWeight, inherit);
  text-decoration: var(--input-textDecoration, inherit);

  &:focus {
    outline: var(--input-focus-outline, var(--focus-light-bg));
    outline-offset: calc(-2px + var(--focus-width) * -1);

    &:not(.has-input-icon):not(.readonly):not(.disabled) {
      border-color: var(--input-focus-bd);
    }
  }

  &:invalid {
    box-shadow: none;
  }

  &::placeholder {
    color: var(--input-placeholder-fg);
    /* Override default opacity value in FireFox (** https://developer.mozilla.org/en-US/docs/Web/CSS/::placeholder#opaque_text **) */
    opacity: 1;
  }

  &::selection {
    color: var(--input-selection-fg);
    background-color: var(--input-selection-bg);
  }

  &::-ms-clear {
    display: none;
  }
}

.hide-border {
  border: none;
}

.input-icon {
  position: absolute;
  inset-block-start: 1px;
  inset-block-end: 1px;
  inset-inline-end: 1px;
  display: flex;
  align-items: center;
  margin: 0 var(--input-spacing-xs);
  cursor: default;
}

.has-status-and-clear {
  .input {
    padding-inline-end: calc(2 * (0.875rem + 2 * var(--input-spacing-xs)));
  }

  .input-icon {
    margin-inline-end: calc(0.875rem + var(--input-spacing-xs) + 2 * var(--input-spacing-xs));
  }
}

.has-status-xor-clear {
  .input {
    padding-inline-end: calc(0.875rem + 2 * var(--input-spacing-xs));
  }
}

.disabled {
  opacity: 1;
  cursor: default;
  color: var(--input-disabled-fg);
  border-color: var(--input-disabled-bd);

  &::placeholder {
    color: var(--input-disabled-fg);
  }
}

.readonly:not(.has-input-icon) {
  background-color: var(--input-readonly-bg);
  border-color: var(--input-readonly-bd);
  color: var(--input-readonly-fg);
  text-overflow: ellipsis;
  overflow: hidden;

  &:focus:not(.composite) {
    outline-offset: calc(var(--focus-width) * -1);
  }

  &.disabled {
    color: var(--input-disabled-fg);
  }
}

.readonly:not(.composite) {
  padding-inline-start: 0;
}

.composite {
  background-color: transparent;
  border-width: 0;

  &:not(.has-input-icon),
  &:not(.has-input-icon):hover {
    border-color: transparent;
  }

  &:not(.has-input-icon):focus {
    border-color: transparent;
  }

  &.disabled {
    color: var(--input-disabled-fg);
  }

  &.readonly:not(.disabled) {
    cursor: pointer;
  }

  &:focus {
    outline: none;
  }
}

.rtl {
  text-align: right;
}

.inplace-edit {
  padding: var(--input-pd);
  border-width: 1px;
  border-style: solid;
  border-color: transparent;
  box-sizing: border-box;
  line-height: normal;
  width: 100%;
  min-height: var(--input-min-height);
  color: var(--input-inplace-edit-text-fg);

  &:hover {
    border-color: var(--input-hover-bd);
  }

  &.disabled {
    color: var(--input-disabled-fg);
  }
}

.composite-input-icon {
  flex-shrink: 0;
  display: inline-flex;
  padding: var(--input-icon-padding);
  align-items: center;
}

.offscreen {
  @extend %offscreen;
}

.clear-button {
  position: absolute;
  inset-inline-end: 1px;
  block-size: 100%;
  padding: 0 var(--input-spacing-xs);
}
