@use 'utils/animation';

.container {
  width: 100%;
  position: relative;
  box-sizing: border-box;
}

.section {
  display: flex;
  flex-direction: column;
  position: relative;
}

.section-header {
  display: flex;
  align-items: center;
}

.heading-layout {
  flex: 1 1 0;
  min-width: 3rem;
}

.heading {
  font-size: var(--windowshade-font-size-h5);
  font-weight: var(--windowshade-font-weight-default);
}

.header-action {
  min-width: 2rem;
  display: flex;
  justify-content: flex-end;
  flex-basis: auto;
  margin-inline-start: var(--windowshade-spacing-s);
}

// TODO - Check with Visual design if fixed height is really needed, as using top-bottom spacing it might not required here
// TODO - check on placement for  arrow and  action control when text is wrapped.

.header {
  height: 2.25rem;
  display: flex;
  align-items: center;
  user-select: none;
  cursor: pointer;
  position: relative;
  padding: var(--windowshade-spacing-s) 0;
  box-sizing: border-box;
  overflow: hidden;

  &:focus {
    outline: var(--windowshade-header-focus);
    outline-offset: calc(-1 * var(--focus-width) - 2px);
  }

  &-wrap {
    height: auto; // 'auto' when header text is wrapped
    min-height: 2rem;
  }
}

.disabled {
  cursor: default;
}

.header-icon {
  display: flex;
  justify-content: center;
  flex-shrink: 0;
  flex-grow: 0;
  color: var(--windowshade-header-fg);
  position: absolute;

  &-open {
    transform: translate3d(0, 0, 0) rotate(90deg);

    [dir='rtl'] & {
      transform: translate3d(0, 0, 0) rotate(-90deg);
    }
  }

  &-closed {
    transform: translate3d(0, 0, 0) rotate(0deg);
  }

  &-transitioning {
    transition: transform animation.duration(250ms) animation.$default-timing-function 0ms;
  }
}

.header-optional-icon {
  padding-inline-start: var(--windowshade-spacing-m);
  flex-shrink: 0;

  :global(.iconColor) {
    --icon-default-fg: var(--windowshade-header-fg);
  }
}

.indicator {
  display: inline-flex;
  padding-inline-start: var(--windowshade-spacing-xs);
}

.indicator-no-icon {
  padding-inline-start: var(--windowshade-spacing-m);
}

.indicator-multi-state {
  padding-inline-start: var(--windowshade-spacing-xs);
}

.count-indicator {
  display: inline-flex;
  margin-inline-start: var(--windowshade-spacing-xs);
}

.count-indicator-no-icon {
  margin-inline-start: var(--windowshade-spacing-m);
}

.count-indicator-multi-state {
  margin-inline-start: var(--windowshade-spacing-xs);
}

.header-text {
  font-size: var(--windowshade-header-font-size-m);
  min-width: 2rem;
  color: var(--windowshade-header-fg);
  padding-inline-start: var(--windowshade-padding-start);
}

.header-Count {
  flex-shrink: 0;
  font-size: var(--windowshade-header-font-size-m);
  min-width: 2rem;
  color: var(--windowshade-header-fg);
  white-space: nowrap;
  padding-inline-start: var(--windowshade-spacing-xs);
}

.content {
  overflow: auto;
  flex-basis: 100%;
  padding-inline-start: var(--windowshade-spacing-m);

  &-transitioning {
    // Note: Resetting to 0 is necessary here to prevent issues with the animation calculations.
    padding-block-end: 0;
    transition: height animation.duration(250ms) animation.$default-timing-function 0ms;
  }
}

.section-container {
  padding-block-start: var(--windowshade-spacing-xs);
  margin-block-end: var(--windowshade-spacing-s);
}

.required-mark {
  color: var(--windowshade-header-asterisk-fg);
  margin-inline-start: var(--windowshade-spacing-xxs);
}

.arrow-cursor {
  & .header-icon {
    cursor: default;
  }
}
