@use 'utils/radiobutton';
@use 'utils/animation';

// This is the visual of the radio input
.input-container {
  position: relative;
  height: radiobutton.$size;
  width: radiobutton.$size;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  margin-block-start: var(--radio-spacing-xxs);
  margin-inline-end: radiobutton.$marginInlineEnd;
  box-sizing: border-box;
  background-color: var(--radio-bg);
  border: radiobutton.$borderWidth solid var(--radio-bd);
  transition: animation.duration(var(--radio-trans-duration)) var(--radio-trans-tf-ease-in-out);
  transition-property: background-color, border-color;

  &:after {
    background-color: var(--radio-bg);
    transition: background-color animation.duration(var(--radio-trans-duration))
      var(--radio-trans-tf-ease-out);
  }

  // Selected state
  .selected & {
    background-color: var(--radio-selected-bg);
    border-color: var(--radio-selected-bd);

    &:after {
      content: '';
      display: block;
      height: 0.375rem;
      width: 0.375rem;
      border-radius: 50%;
      position: absolute;
      // This is the color of the selected indicator (the colored dot), so we use 'fg'
      background-color: var(--radio-selected-fg);
      transition: background-color animation.duration(var(--radio-trans-duration))
        var(--radio-trans-tf-ease-in);
    }
  }

  // Disabled & Disabled Read-Only shared styles
  .disabled &,
  .disabled.read-only & {
    background-color: var(--radio-disabled-bg);
    border-color: var(--radio-disabled-bd);
  }

  // Disabled & Selected State
  .disabled.selected & {
    background-color: var(--radio-disabled-selected-bg);
    border-color: var(--radio-disabled-selected-bd);

    &:after {
      background-color: var(--radio-disabled-selected-fg);
    }
  }

  // Read-Only State
  .read-only & {
    background-color: var(--radio-read-only-bg);
    border-color: var(--radio-read-only-bd);
  }

  // Read-only selected state
  .read-only.selected &:after {
    background-color: var(--radio-read-only-fg);
  }

  // Disabled Read-Only Selected State
  .disabled.read-only.selected & {
    background-color: var(--radio-disabled-bg);
    border-color: var(--radio-disabled-bd);

    &:after {
      background-color: var(--radio-disabled-fg);
    }
  }
}

.label {
  min-width: 0;
}

.normal {
  display: flex;
  align-items: flex-start;
  width: max-content;
  max-width: 100%;
  box-sizing: border-box;
  // Padding is added to allow room for the focus indicator which is not defined in rems, so this shouldn't be either.
  // 'logical' shorthand refers to 'block-start inline-start block-end inline-end'. In LTR this is 'top left bottom right'.
  padding: logical radiobutton.$focusMinimumPadding radiobutton.$focusMinimumPadding
    radiobutton.$focusMinimumPadding var(--radio-spacing-xs);
}

.normal.no-label {
  // Padding is added to allow room for the focus indicator which is not defined in rems, so this shouldn't be either.
  padding: radiobutton.$focusMinimumPadding;

  .input-container {
    margin: 0;
  }
}

.focus-visible {
  position: relative;

  &:after {
    content: '';
    pointer-events: none;
    position: absolute;
    inset-inline-start: 0;
    inset-block-start: 0;
    height: 100%;
    width: 100%;
    outline: var(--focus-light-bg);
    outline-offset: calc(var(--focus-width) * -1);
  }
}

.description {
  // vertically align the description with the label based on the size of the radio button input.
  margin-inline-start: radiobutton.$indentation;
  margin-block-start: var(--radio-spacing-xs);
}

.description-text {
  font-size: var(--radio-font-size-s);
  vertical-align: top;

  &:not(.disabled) {
    color: var(--radio-description-fg);
  }
}

// Cursor handling
.normal,
.selected {
  .label label,
  .input {
    cursor: pointer;
  }
  .input-container {
    &:after {
      cursor: pointer;
    }
  }
}

.disabled,
.read-only {
  .label label,
  .input {
    cursor: default;
  }
  .input-container {
    &:after {
      cursor: default;
    }
  }
}
