.zero-state {
  align-items: center;
  justify-content: center;
  display: flex;
  flex-direction: column;
  height: 100%;
  position: relative;
  width: 100%;
  overflow: hidden;
}

.align-top {
  justify-content: flex-start;
}

.heading {
  margin-block-start: var(--zero-state-mg-s);
  max-width: 24rem;
  text-align: center;
  font-weight: var(--zero-state-font-weight-thick);
  font-size: var(--zero-state-heading-font-size);
  color: var(--zero-state-heading-fg);
}

.text {
  margin-block-start: var(--zero-state-mg-s);
  max-width: 24rem;
  text-align: center;
  font-size: var(--zero-state-text-font-size);
  color: var(--zero-state-text-fg);

  &:first-child {
    margin-block-start: var(--zero-state-text-space);
  }
}

.button-group {
  padding-block-start: var(--zero-state-mg-s);
  justify-content: center;
  width: 100%;
}
