%container {
  box-sizing: border-box;
  display: inline-grid;
}

.container_vertical {
  @extend %container;
  height: 100%;
  grid-template:
    '. . .' 1rem
    '. track .' 10px
    'tick-labels track labels' 1fr
    '. track. ' 7px
    '. . .' 1.5rem
    / minmax(min-content, 0.25rem) 1rem min-content;
  column-gap: var(--slider-spacing-xs);

  &.no-ticks {
    grid-template-rows: 0 0 1fr 0 0;
    overflow: visible;
  }
}

.container_horizontal {
  @extend %container;
  width: 100%;
  grid-template:
    '. . labels . .' min-content
    '. track track track .' 0.5rem
    '. . tick-labels . .' minmax(min-content, 0.25rem)
    / 0.75rem 0px minmax(0, 1fr) 0px 0.75rem;
  row-gap: var(--slider-spacing-xs);
  height: fit-content;

  &.no-ticks {
    grid-template-columns: 0.75rem 0 1fr 0 0.5rem;
  }
}

%track {
  position: relative;
  box-sizing: border-box;
  background-color: var(--slider-track-bg);
  border-radius: 2px;
  border: 1px solid var(--slider-track-bd);
}

.track_vertical {
  @extend %track;
  display: inline-block;
  justify-self: center;
  width: 0.25rem;
  height: 100%;
  border-inline-start-color: var(--slider-track-bd);
  border-inline-end-color: var(--slider-track-top-bd);
  grid-row: 3;
  grid-column: 2;
}

.track_horizontal {
  @extend %track;
  align-self: center;
  height: 0.25rem;
  border-top-color: var(
    --slider-track-top-bd
  ); // TODO: 'border-block-start-color' falls on border-top
  border-bottom-color: var(
    --slider-track-bd
  ); // TODO: 'border-block-end-color' falls on border-bottom
  grid-row: 2;
  grid-column: 3;
}
