.opening :global {
  animation: nova-global-fadein calc(var(--menu-anim-dur-enter) * var(--nova-animation-duration-factor, 1)) var(--menu-anim-tf-enter);
}

.closing :global {
  animation: nova-global-fadeout calc(var(--menu-anim-dur-exit) * var(--nova-animation-duration-factor, 1)) var(--menu-anim-tf-exit) forwards;
}

.menu-popover {
  display: flex;
  overflow-x: hidden;
}

.menu-popover-inner {
  max-inline-size: min(calc(100% + var(--menu-scrollbar-offset, 0px)), 100vw);
}

