.current-new-swatch-container {
  width: 100%;
  justify-content: center;
  align-items: center;
}
.current-new-swatch-container .current-multi-color {
  width: var(--color-indicator-automatic-swatch-width);
  margin-inline-start: var(--color-indicator-spacing-xs);
  cursor: default;
}
.current-new-swatch-container .current-multi-color:focus {
  outline: var(--focus-light-bg);
  outline-offset: calc(-1px + calc(-1 * var(--focus-width)));
}

.swatch {
  box-sizing: border-box;
  width: 1.5rem;
  height: 1rem;
  margin-inline-end: var(--color-indicator-spacing-xs);
  border: 1px solid var(--color-indicator-swatch-bd);
}
.swatch:focus {
  outline: var(--focus-light-bg);
  outline-offset: calc(-1px + calc(-1 * var(--focus-width)));
}

.swatch-spacing {
  margin-inline-start: var(--color-indicator-spacing-xs);
  margin-inline-end: 0;
}

.no-color {
  background: linear-gradient(328deg, transparent 46%, var(--color-indicator-no-color-mark) 50%, var(--color-indicator-no-color-mark) 50%, transparent 54%);
}

.swatch-border {
  border: 1px solid var(--color-indicator-swatch-bd);
}

.current-color-swatch-border {
  border-inline-end-width: 0px;
}

.new-color-swatch-border {
  border-inline-start-width: 0px;
}

