.offscreen {
  display: block !important;
  inset-inline-start: -10000px !important;
  inset-block-start: 0 !important;
  width: 1px !important;
  height: 1px !important;
  overflow: hidden !important;
  position: absolute;
}

.container {
  position: relative;
  display: inline-flex;
  font-size: var(--textarea-font-size-m);
}

.textarea {
  width: 100%;
  min-height: 1.5rem;
  padding: var(--textarea-pd);
  border-width: 1px;
  border-style: solid;
  border-color: var(--textarea-bd);
  box-sizing: border-box;
  text-overflow: ellipsis;
  flex-grow: 1;
  background: var(--textarea-bg);
  color: var(--textarea-fg);
  resize: none;
  cursor: auto;
  overflow-x: hidden;
}
.fixed-height .textarea {
  height: 100%;
}

.textarea::placeholder {
  color: var(--textarea-placeholder-fg);
  /* Override default opacity value in FireFox (** https://developer.mozilla.org/en-US/docs/Web/CSS/::placeholder#opaque_text **) */
  opacity: 1;
}

.editable:focus {
  background-color: var(--textarea-focus-bg);
  border-color: var(--textarea-focus-bd);
  color: var(--textarea-focus-fg);
  outline: var(--textarea-editable-focus-outline);
  outline-offset: calc(-2px + var(--focus-width) * -1);
}

.read-only {
  padding-inline-start: 0;
}
.read-only:focus {
  outline: var(--focus-light-bg);
  outline-offset: calc(-1 * var(--focus-width));
}

.disabled {
  cursor: default;
}
.disabled:focus {
  outline: var(--focus-light-bg);
  outline-offset: calc(-2px + var(--focus-width) * -1);
}
.disabled::placeholder {
  color: var(--textarea-placeholder-disabled-fg);
}

.status-icon {
  position: absolute;
  top: 1px;
  bottom: 1px;
  inset-inline-end: var(--textarea-status-icon-offset);
  align-items: center;
  margin-inline-end: var(--textarea-mg-xs);
  padding: 0.35rem 0;
  padding-inline-end: 0.15rem;
}

.status-focusout {
  padding-inline-end: 1.25rem;
}

.with-scrollbar .status-icon {
  padding-inline-end: 0;
}
.with-scrollbar .status-focusout {
  padding-inline-end: 1.1rem;
}

.status-message-container {
  position: absolute;
}

