.container {
  min-width: 100%;
  display: inline-flex;
  border-style: solid;
  font-size: var(--message-strip-font-size-m);
  align-items: center;
  min-height: 2rem;
  flex-shrink: 0;
  box-sizing: border-box;
  border-width: 1px;
  background-color: var(--message-strip-bg);
  border-color: var(--message-strip-bd);
  color: var(--message-strip-fg);
}

.section-message {
  display: flex;
  position: relative;
  align-items: flex-start;
  flex-grow: 1;
  min-height: 1.375rem;
  padding: var(--message-strip-pd-xs) 0;
}

.section-message:focus {
  outline: var(--focus-light-bg);
  outline-offset: calc(-1 * var(--focus-width) - 2px);
}

.content-container {
  align-self: center;
  min-height: 1rem;
}

.text {
  font-size: var(--message-strip-font-size-m);
  display: inline;
}

.link {
  margin-inline-start: var(--message-strip-pd-s);
}

.help-button {
  height: 1rem;
  min-height: 1rem;
  width: 1rem;
  margin: 0 var(--message-strip-pd-xs);
  padding: var(--message-strip-pd-none);
  vertical-align: middle;
}

.spacer {
  flex-grow: 1;
}

.close {
  display: flex;
  justify-content: center;
}

.close-button {
  padding: 0 var(--message-strip-pd-xs);
  margin-inline-end: var(--message-strip-pd-s);
}

.icon {
  flex-shrink: 0; // Keeps the status icon from shrinking

  // NOTE: Icon should not be centered
  align-self: flex-start;
  margin-inline: var(--message-strip-pd-s);
}

.action {
  // NOTE: Action button should not be centered
  align-self: flex-start;
  margin-inline: var(--message-strip-pd-s);
}

.menu-button-container {
  display: inline-block;
  margin-inline: var(--message-strip-pd-s);
}
