@use 'utils/offscreen';
@use 'reactDnd/dndstyles';
$min-height: 2.125rem;

.outer-container {
  max-width: 100%;
  background-color: var(--token-input-bg);
  box-sizing: border-box;
  border: 1px solid var(--token-input-bd);
  border-bottom-color: var(--token-input-border-bottom-color);
  border-bottom-width: var(--token-input-border-bottom-width);
  border-bottom-style: solid;
  &:focus-within {
    border-color: var(--token-input-active-bd);
    border-bottom-color: var(--token-input-border-bottom-color-active);
  }
  &.read-only {
    border-color: transparent;
  }
}

.container {
  width: 100%;
  min-width: 0;
  position: relative;
  display: flex;
  align-items: center;
  min-height: $min-height; // so that token input does not grow when the first token is added

  &.has-status-icon {
    padding-inline-end: 1.25rem;
  }
}

.input-field-container {
  display: flex;
  flex-grow: 1;
  flex-basis: 100%;
  min-width: 10rem;
}

.input-field {
  width: 100%;
  height: 100%;

  & > input {
    background: transparent;
    &:hover {
      background: transparent;
    }
    &:focus {
      outline: none;
    }
  }
}

.focus-visible {
  outline: var(--token-input-focus-outline);
  outline-offset: var(--token-input-focus-outline-offset);
}

.input-icon {
  position: absolute;
  height: 1.25rem;
  inset-block-start: var(--token-input-icon-position-top);
  inset-inline-end: 1px;
  display: flex;
  align-items: center;
  margin: 0 var(--token-input-spacing-xs);
  cursor: default;
}

.offscreen {
  @extend %offscreen;
}

.button {
  width: $min-height;
  height: $min-height;
}

.tokenizer {
  width: 100%;
}

.drag-over {
  @extend %dragZoneBackground;
  @extend %dragZoneOutline;
}
