.item .expand-icon, .item .node-status-icon, .item .node-icon {
  flex-shrink: 0;
}

.item {
  max-width: 100%;
  position: relative;
}
.item .item-inner {
  display: flex;
  align-items: center;
  padding: var(--tree-spacing-xxs) 0;
  padding-inline-end: var(--tree-spacing-s);
  gap: var(--tree-spacing-xs);
}
.item .item-inner > .node-label,
.item .item-inner > .node-icon,
.item .item-inner > .expand-icon {
  min-width: 0;
  z-index: 0;
}
.item .item-inner > .node-label {
  align-items: baseline;
  font-size: var(--tree-font-size-m);
  user-select: none;
}
.item .node-status-icon {
  margin-inline-start: auto;
}
.item .expand-icon {
  margin-block-start: var(--tree-spacing-xxs);
}
.item .expand-icon-pointer-cursor {
  cursor: pointer;
}

.item:not(.selected):hover .item-inner {
  background-color: var(--tree-node-hover-bg);
  cursor: var(--tree-node-hover-cursor);
}

.item:focus::after {
  content: "";
  position: absolute;
  inset: 0;
  outline: var(--focus-light-bg);
  outline-offset: calc(-1 * var(--focus-width));
  pointer-events: none;
}
.item:focus.selected::after {
  outline: var(--tree-node-selected-focus-bg);
}

.selected .item-inner {
  background-color: var(--tree-selected-inactive-bg);
  filter: var(--tree-selected-inactive-filter);
  cursor: var(--tree-node-hover-cursor);
  position: relative;
}
.selected .item-inner:after {
  position: absolute;
  content: "";
  height: 100%;
  width: var(--tree-node-selected-indicator-width);
  inset-block-start: 0;
  inset-inline-start: 0;
  background: var(--tree-node-selected-indicator-bd);
}

:focus-within .selected .item-inner {
  background-color: var(--tree-selected-bg);
  filter: var(--tree-selected-filter);
}

.show-scroll label {
  white-space: pre;
}

