.bar-container {
  display: flex;
  align-items: center;
  padding: calc(var(--focus-width) * 2);
}
.bar-container:focus {
  outline: var(--focus-light-bg);
  outline-offset: calc(-1 * var(--focus-width));
}

.progress-bar {
  width: var(--busy-state-bar-width);
  height: var(--busy-state-bar-height);
  background-color: var(--busy-state-bar-bg);
  border-radius: var(--busy-state-bar-border-radius);
  overflow: hidden;
  position: relative;
}

.progress-bar:after {
  content: "";
  background: linear-gradient(90deg, var(--busy-state-bar-animation-start), var(--busy-state-bar-animation-end), var(--busy-state-bar-animation-start));
  transform-origin: 0% 50%;
  position: absolute;
  inset: 0;
  width: var(--busy-state-bar-width);
  height: var(--busy-state-bar-height);
}
:root[dir=ltr] .progress-bar:after :global {
  animation: nova-busystate-bar-animation-ltr calc(2s * var(--nova-animation-duration-factor, 1)) infinite linear;
}
:root[dir=rtl] .progress-bar:after :global {
  animation: nova-busystate-bar-animation-rtl calc(2s * var(--nova-animation-duration-factor, 1)) infinite linear;
}

