@use 'utils/icon';

%textTruncation {
  white-space: var(--list-item-white-space, pre);
  text-overflow: ellipsis;
  overflow: hidden;
}

.item-container {
  position: relative;
  display: flex;
  align-items: center;
}

.item-container > li {
  cursor: default;
  width: 100%;
}

.item {
  position: relative;
}

.item:not(.disabled):hover {
  cursor: pointer;

  .item-inner {
    color: var(--list-item-hover-fg);
    background-color: var(--list-item-hover-bg);

    > .item-textSeparator,
    > .item-secondaryText {
      color: var(--list-item-hover-fg);
    }
  }

  // .iconColor is set inside SVGs for themeing purposes and is not specified
  // using `cn.iconColor`. Using :global() avoids this class name from becoming
  // a module (list scoped class name) and will, therefore, use the non-module
  // class name specified.
  .item-icon svg :global(.iconColor),
  .navigation-icon svg :global(.iconColor) {
    --icon-default-fg: var(--list-item-hover-fg);
  }
}

.focused:focus {
  &::after {
    content: '';
    position: absolute;
    inset: 0;
    outline: var(--list-item-focus-outline);
    outline-offset: calc(-1 * var(--focus-width));
    z-index: 1;
    pointer-events: none;
  }
}

.selected:not(:hover) {
  .item-inner {
    color: var(--list-item-selected-fg);
    background-color: var(--list-item-selected-bg);
    filter: var(--list-item-selected-filter);

    > .item-textSeparator,
    > .item-secondaryText {
      color: var(--list-item-selected-fg);
    }
  }

  &:focus::after {
    outline: var(--list-item-selected-focus-outline);
  }

  // .iconColor is set inside SVGs for themeing purposes and is not specified
  // using `cn.iconColor`. Using :global() avoids this class name from becoming
  // a module (list scoped class name) and will, therefore, use the non-module
  // class name specified.
  .item-icon svg :global(.iconColor),
  .navigation-icon svg :global(.iconColor) {
    --icon-default-fg: var(--list-item-selected-fg);
  }
}

.item-hoverable:active {
  .item-inner {
    color: var(--list-item-active-fg);
    background-color: var(--list-item-active-bg);
    filter: var(--list-item-active-filter);

    > .item-textSeparator,
    > .item-secondaryText {
      color: var(--list-item-active-fg);
    }
  }
}

.item-inner {
  box-sizing: border-box;
  padding: var(--list-spacing-squish-s);
  border: 1px solid transparent;
  display: flex;
  align-items: center;
  gap: var(--menu-spacing-xs);
  min-height: 0.875rem;
  padding-inline-start: calc(
    var(--menu-list-item-spacing-m) + (var(--menu-list-item-spacing-xs) + #{icon.$width}) *
      var(--menu-list-item-indentation-level)
  );
  padding-inline-end: var(--menu-spacing-m);
  font-family: var(--menu-list-item-font-family);
  color: var(--menu-list-item-fg);
}

.text-container {
  display: inline-flex;
  align-items: center;
  flex-grow: 1;
  gap: var(--list-spacing-s);
  overflow: hidden;
}

.item-text {
  @extend %textTruncation;

  .has-secondaryText & {
    flex-grow: 0;
    flex-basis: auto;
    min-width: 0;
  }

  .info-has-priority & {
    flex-grow: 1;
    flex-basis: 0;
    min-width: 3rem;
  }
}

.item-secondaryText {
  text-align: end;
  font-size: var(--menu-list-item-font-size-s);
  color: var(--menu-list-item-description-fg);
  @extend %textTruncation;
  flex-grow: 1;
  flex-basis: 0;
  min-width: 3rem;

  .info-has-priority & {
    flex-grow: 0;
    flex-basis: auto;
  }
}

.checkmark {
  padding-inline-start: calc(
    var(--menu-list-item-spacing-xxs) + (var(--menu-list-item-spacing-xs) + #{icon.$width}) *
      var(--menu-list-item-indentation-level)
  );
  inset-inline-start: 0;
  inset-block-start: 50%;
  position: absolute;
  transform: translateY(-50%);
  --icon-default-override-fg: currentColor;
}

.item-disabled {
  &,
  &.item-secondaryText {
    color: var(--menu-disabled-color);
  }
}

.operation-item .item-inner {
  padding-inline-end: var(--menu-list-item-spacing-xs);
}

.navigation-icon {
  flex-shrink: 0;
  padding: 0 var(--menu-spacing-xs);
  --icon-default-override-fg: currentColor;
}

.item-icon {
  flex-shrink: 0;
  --icon-default-override-fg: currentColor;
}

.close-button {
  flex-shrink: 0;
  padding: var(--menu-spacing-xs);
  height: 1.2rem;
  border-radius: 2px;
  cursor: pointer;
  box-sizing: border-box;
  display: inline-flex;
  align-items: center;

  .close-button-icon {
    --icon-default-override-fg: currentColor;
  }

  &:hover {
    background-color: var(--close-button-icon-hover-bg);
  }

  &:active {
    background-color: var(--close-button-icon-active-bg);
    filter: var(--close-button-icon-active-filter);

    .close-button-icon :global(.iconColor) {
      --icon-default-override-fg: var(--close-button-icon-active-fg);
    }
  }
}

.close-button-disabled {
  cursor: default;
  pointer-events: none;
}

.has-icon-spacing .item-inner {
  padding-inline-start: calc(
    var(--menu-list-item-spacing-m) + (var(--menu-list-item-spacing-xs) + #{icon.$width}) *
      var(--menu-list-item-indentation-level) + #{icon.$width} + var(--menu-spacing-xs)
  );
}

.has-right-icon .item-inner {
  padding-inline-end: calc(
    var(--menu-list-item-spacing-xs) + #{icon.$width} + var(--menu-spacing-xs) * 3
  );
}
