@use 'utils/animation';

$toast-anim-duration: animation.duration(var(--toast-anim-dur));
$toast-anim-prop-enter-exit: $toast-anim-duration var(--toast-anim-tf-enter-exit) forwards;
$toast-anim-prop-exit: $toast-anim-duration var(--toast-anim-tf-exit) forwards;

.container {
  position: fixed;
  z-index: 100;
  width: 17rem;
  max-width: 20rem;
  min-width: 10rem;
  // This padding matches the box-shadow blur-radius which is
  // defined in px and doesn't scale with the base font-size.
  padding: var(--toast-spacing-xl);
  overflow: hidden;
  // This is defined explicitly because some consumers may use bootstrap, which globally sets
  // box-sizing to 'border-box'.
  box-sizing: content-box;
}

.container-inner {
  display: flex;
  font-size: var(--toast-font-size-m);
  box-shadow: var(--toast-sh);
  padding: var(--toast-spacing-m);
  background-color: var(--toast-bg);
  border: var(--toast-border-width) solid var(--toast-bd);
  border-radius: 0 2px 2px 0;
  will-change: transform;
  position: relative;
}

.container-inner-border {
  position: absolute;
  height: 100%;
  inset-block-start: 0;
  inset-inline-start: -3px;
  border-inline-start: var(--toast-accent-bd-width) solid var(--toast-accent-bd-color);
}

.entering-border-anim {
  :global {
    animation: nova-global-slidein-messagetoast $toast-anim-prop-enter-exit;
  }
}

.closing-border-anim {
  :global {
    animation: nova-global-slideout-messagetoast $toast-anim-prop-exit;
  }
}

// Animations are applied based on the MessageToast's reference element dock position.
// referenceElementDock = ['start' | 'center' | 'end', 'start' | 'center' | 'end']
// The first value is the x position, the second value is the y position, this maps directly to
// the nested values below.
.entering {
  &_start {
    &_start,
    &_center,
    &_end {
      :global {
        animation: nova-global-slideinstart $toast-anim-prop-enter-exit;
      }
    }
  }

  &_end {
    &_start,
    &_center,
    &_end {
      :global {
        animation: nova-global-slideinend $toast-anim-prop-enter-exit;
      }
    }
  }

  &_center {
    &_start {
      :global {
        animation: nova-global-slideintop $toast-anim-prop-enter-exit;
      }
    }
    &_end {
      :global {
        animation: nova-global-slideinbottom $toast-anim-prop-enter-exit;
      }
    }
    &_center {
      :global {
        animation: nova-global-popin $toast-anim-prop-enter-exit;
      }
    }
  }
}

.closing {
  &_start {
    &_start,
    &_center,
    &_end {
      :global {
        animation: nova-global-slideoutstart $toast-anim-prop-exit;
      }
    }
  }

  &_end {
    &_start,
    &_center,
    &_end {
      :global {
        animation: nova-global-slideoutend $toast-anim-prop-exit;
      }
    }
  }

  &_center {
    &_start {
      :global {
        animation: nova-global-slideouttop $toast-anim-prop-exit;
      }
    }
    &_end {
      :global {
        animation: nova-global-slideoutbottom $toast-anim-prop-exit;
      }
    }
    &_center {
      :global {
        animation: nova-global-popout $toast-anim-prop-exit;
      }
    }
  }
}

.toast-content {
  width: 100%;
  gap: var(--toast-block-margin);
}

.action-button {
  align-self: flex-end;

  &:focus {
    outline: var(--focus-style) var(--focus-width) var(--toast-focus);
  }
}

.section-message {
  flex-grow: 1;
  // These are used in conjuntion with -webkit-line-clamp to truncate toast after dynamic number of max lines calculated in MessageToast.tsx
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.close-button {
  padding: var(--toast-spacing-none);
  min-height: auto;
  position: absolute;
  right: 0;
  top: 0;
}

.toast-icon {
  margin-inline-end: var(--toast-spacing-s);
}
