.overlay {
  position: absolute;
  inset: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--busy-state-overlay-bg);
  overflow: hidden;
  padding-inline-start: var(--busy-state-pd);
  padding-inline-end: var(--busy-state-pd);
}

.state {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}

.state-text {
  font-size: var(--busy-state-font-size-m);
  color: var(--busy-state-fg);
  white-space: pre-wrap;
  overflow-wrap: anywhere;
  word-break: break-word;
}

.container {
  position: relative;
  display: inline-block;
  vertical-align: top;
}

.children-container {
  display: contents;
}

.fill-parent-container {
  height: 100%;
  width: 100%;
}

.button-container {
  display: inline-block;
  background-color: var(--button-container-bg);
}

