.dialog-content {
  height: 100%;
  width: 100%;
  overflow: hidden;
  display: grid;
  grid-template-areas:
    'cropper preview'
    'toolbar preview'
    'description preview';
  grid-template-columns: minmax(0, 2fr) minmax(auto, 1fr);
  grid-template-rows: minmax(0, 1fr) auto auto;
  grid-column-gap: var(--cropper-dialog-column-gap);
  grid-row-gap: var(--cropper-dialog-row-gap);
}

.cropper {
  grid-area: cropper;
  height: 100%;
  width: 100%;
  padding: 1px;
}

.preview {
  grid-area: preview;
  overflow: hidden;
  max-width: 100%;
  position: relative;
  & > img {
    position: absolute;
    left: 0; // This should not be replaced with inset-inline-start
  }
}

.ellipse {
  border-radius: 50%;
}

.bar {
  display: flex;
  grid-area: toolbar;
}

.description {
  width: 100%;
  direction: inherit;
  text-align: center;
  grid-area: description;
}
