.avatar-container {
  cursor: pointer;
  overflow: hidden;
  padding: 0;
  border: 0;
  max-width: 100%;
  position: relative;
}

.avatar {
  position: relative;
}

.avatar:focus {
  outline: none;
}

.edit-icon {
  position: absolute;
  inset-inline-end: 0;
  inset-block-end: 0;
  opacity: 0;
  transition: opacity calc(250ms * var(--nova-animation-duration-factor, 1)) cubic-bezier(0, 0, 0.2, 1) 0ms;
  background: var(--avatar-selector-edit-icon-bg);
  width: 1.5rem;
  height: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  border: solid var(--avatar-selector-edit-icon-bw) var(--avatar-selector-edit-icon-bd);
}

.avatar-container:hover .edit-icon,
.avatar:focus + .edit-icon {
  opacity: 1;
}

