@use 'utils/offscreen';

%container {
  position: relative;
  pointer-events: none;
}

.container_vertical {
  @extend %container;
  grid-column: 2;
  grid-row: 3 / 3;
}

.container_horizontal {
  @extend %container;
  grid-row: 2;
  grid-column: 3 / 3;
}

.containerGrip {
  width: 0;
  height: 0;
}

%grip {
  width: 0.875rem;
  height: 0.875rem;
  box-sizing: content-box;
  position: absolute;
  pointer-events: auto;
  display: inline-flex;
  justify-content: center;
  align-content: center;
  align-items: center;
  border-radius: 100%;
  border: 1px solid var(--slider-grip-bd);
  background-color: var(--slider-grip-bg);
}

.grip-enable-focus-outline {
  position: absolute;
}

.grip-enable-focus-outline:focus::before {
  outline: var(--focus-light-bg);
  content: ' ';
  height: 1.1rem;
  position: absolute;
  width: 1rem;
  pointer-events: none;
}

.grip-enable-focus-outline {
  &.container_horizontal:focus::before {
    inset-inline-start: -0.53rem;
    inset-block-start: -0.25rem;
  }

  &.container_vertical:focus::before {
    inset-inline-start: -1px;
    inset-block-start: -9px;
  }
}

.vertical {
  @extend %grip;
  margin-block-start: calc(-1 * var(--slider-grip-spacing-s));
}

.horizontal {
  @extend %grip;
  margin-inline-start: calc(-1 * var(--slider-grip-spacing-s));
  inset-block-start: -3px;
}

.indicator {
  width: 0.375rem;
  height: 0.375rem;
  border-radius: 100%;
  background-color: var(--slider-grip-indicator-bg);
  .disabled & {
    background-color: var(--slider-grip-indicator-disabled-bg);
  }
}

%input {
  position: absolute;
  visibility: visible;
  // NOTE: sometimes this input needs to be on top of a label from a different
  //     | SliderTrackLabel. So we put the inputs above them.
  //     | This also means the stacking context needs to be kept correct.
  z-index: 1;
}

.input {
  @extend %input;
  inset-block-start: -0.25rem;
  inset-inline-start: 0;
  width: 100%;
  min-width: 2rem;
  // FIXME: refactor Input to get rid of this selector?
  //      | text-align is inherited, but putting it on `.input` still doesn't work in FF?
  & > input {
    text-align: center;
    font-size: var(--slider-value-grip-font-size-m);
    padding: var(--slider-value-grip-spacing-squish-xs);
    min-height: 1.65rem;
    color: var(--slider-grip-label-fg);
  }

  &.input-overflow-right {
    left: auto;
    right: 0;
  }

  &.input-overflow-left {
    right: auto;
    left: 0;
  }
}

.datepicker {
  @extend %input;
  inset-block-start: -0.25rem;
  inset-inline-start: 0;
  max-width: 100%;
  background-color: var(--slider-datepicker-bg);
}

.offscreen {
  @extend %offscreen;
}

.disabled {
  border-color: var(--slider-grip-disabled-bd);
}

.arrowgrip {
  @extend %grip;
  border-block-end: none;
  border-radius: 3px;
  height: 0.375rem;
  width: 0.78rem;

  &.vertical {
    // NOTE: Move the rotational axis to the visual middle of the arrow handle.
    //     | The pointy part of the arrow is not added into the height of this
    //     | element so we use 100% and half the width.
    transform-origin: calc(0.78rem / 2) 100%;
    transform: rotate(90deg);
    inset-inline-start: 0.1rem;

    [dir='rtl'] & {
      transform: rotate(-90deg);
    }
  }
}

.arrowgrip {
  &::before,
  &::after {
    border-inline-start: 0.45rem solid transparent;
    border-inline-end: 0.45rem solid transparent;
    border-block-start: 0.5rem solid var(--slider-grip-bd);
    content: ' ';
    position: absolute;
  }
  &::before {
    inset-block-start: 0.3125rem;
  }
  // NOTE 'border-block-start-color' seen not working, hence used property 'border-top-color'
  &::after {
    inset-block-start: 0.25rem;
    border-top-color: var(--slider-grip-bg);
  }
}

.arrow-indicator {
  inset-block-start: 0.125rem;
  border-inline-start: 4px solid transparent;
  border-inline-end: 4px solid transparent;
  border-block-start: 4px solid var(--slider-grip-indicator-bg);
  position: relative;
  z-index: 1;

  &.disabled {
    //NOTE border-block-start-color not working so I keep it border-top-color as it is
    border-top-color: var(--slider-grip-indicator-disabled-bg);
  }
}
