.container {
  padding: 0 0.25rem;
}

.wrap {
  display: flex;
  flex-wrap: wrap;
  overflow: auto;
  max-height: calc((1.5rem + 0.25rem * 2) * var(--tokenizer-max-lines, 3.5));
  white-space: pre-wrap;
}

.children-container {
  display: inline-flex;
  flex: 1;
}

.separator {
  margin: 0 0.5rem;
}

.token {
  margin: 0.25rem;
  display: inline-flex;
  max-width: var(--token-max-width, min(calc(100% - 2 * 0.25rem), 25rem));
  min-width: 3.5rem;
  vertical-align: middle;
}

.busy-state-container {
  max-width: var(--token-max-width, min(100%, 25rem));
}

