%container {
  display: flex;
}

.container_vertical {
  @extend %container;
  flex-direction: column;
  min-height: var(--splitter-min-size);
}

.container_horizontal {
  @extend %container;
  flex-direction: row;
  min-width: var(--splitter-min-size);
  overflow: hidden;
}

.container_horizontal:has(.section:last-child.collapsed) {
  justify-content: flex-end;
}

.section {
  overflow: auto;
  box-sizing: border-box;

  .container_horizontal &.hasPadding {
    padding: 0 var(--splitter-section-padding);
  }

  .container_vertical &.hasPadding {
    padding: var(--splitter-section-padding) 0;
  }

  &.collapsed {
    visibility: hidden;
  }
}

%bar {
  background-color: var(--splitter-bg);
  // this is relative position for the resize-icon + collapse-icon case
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
}

%bar:focus {
  outline: var(--focus-light-bg);
}

// splitterbar grab area
%bar::before {
  content: '';
  background: transparent;
  position: absolute;
}

.bar_horizontal {
  @extend %bar;
  width: var(--splitter-bar-size);
  cursor: col-resize;
  flex-direction: column;
  align-items: center;

  // grab area
  &::before {
    width: 0.5rem;
    inset-block-start: 0;
    inset-block-end: 0;
  }
}

.bar_vertical {
  @extend %bar;
  height: var(--splitter-bar-size);
  cursor: row-resize;

  // grab area
  &::before {
    height: 0.5rem;
    inset-inline-start: 0;
    inset-inline-end: 0;
  }
}

.hide-splitter-bar {
  background-color: transparent;
}

.resize-icon {
  // the icon is larger than the bar, so we don't allow pointer events on the out of bounds area.
  pointer-events: none;
  position: absolute;
}

.collapse-icon_horizontal {
  cursor: pointer;
  z-index: 1;
  position: absolute;
  // FIXME: these aren't positioned according to livingston spec
  .resize-icon + & {
    inset-block-start: calc(50% + 0.5rem);
  }
}

.collapse-icon_vertical {
  height: var(--splitter-icon-size);
  cursor: pointer;
  z-index: 1;
  position: absolute;
  // FIXME: these aren't positioned according to livingston spec
  .resize-icon + & {
    inset-inline-start: calc(50% + 0.5rem);
  }
}

.resizable {
  cursor: default;
}
