@use 'utils/animation';

// # REACT DND STYLES # //
// http://designsystem-snapshot.sas.com/latest/components/drag-and-drop/styling/
// this cover predefine placesholder and mixins for dnd styles referring design system
// Make sure you import and use 'getDndStyleTokens' in your jsx
// import { getDndStyleTokens } from '<../../../../style/reactDnd/DndStyleTokens>';

// **** Placeholders ****//

// ** Drag Affordance * //
%draggable {
  cursor: default;

  &:hover {
    transition: box-shadow animation.duration(var(--dnd-item-anim-dur)) var(--dnd-item-trans-dur);
    box-shadow: var(--dnd-item-medium-sh);
  }
}

%draggedItem {
  opacity: var(--dnd-moved-item-opacity);
  box-sizing: border-box;
}

// ** Drag Image * //
%dragImage {
  box-shadow: var(--dnd-item-medium-sh);
  background: var(--dnd-drag-image-bg);
}

// ** Drop Zone * //
%dragZoneBackground {
  background: var(--dnd-drop-zone-bg);
}
%dragZoneOutline {
  outline: 1px solid var(--dnd-drop-zone-bd);
  outline-offset: -1px;
}

%dragZoneBorder {
  border: 1px solid var(--dnd-drop-zone-bd);
}
%dragZoneBorderColor {
  border-color: var(--dnd-drop-zone-bd);
}

// * Drop Line * //
%dropLineVertical {
  background: var(--dnd-drop-line-bg);
  width: 2px;
  height: 100%;
}

%dropLineHorizontal {
  background: var(--dnd-drop-line-bg);
  width: 100%;
  height: 2px;
}

%dropLineLoop {
  background: var(--dnd-drop-line-bg);
  border: 1px solid var(--dnd-drop-line-loop);
  box-shadow: 0 1px 4px 0 var(--dnd-drop-line-loop-shadow);
  width: 6px;
  height: 6px;
  border-radius: 100%;
}

// **** Mixins ****//

// * Drop line *

// Uses -
// @include dndstyles.DropLine(); //will take 'Default' values for dnd token, style class, start/end position gap and drop-line horizontal  // OR
// @include dndstyles.DropLine($verticalBar: true); // OR
// @include dndstyles.DropLine($class:'<my-class>', $tokenPrefix:'<my-component-dnd>', $verticalBar:<true>, $startPositionGap: <5px>, $endPositionGap: <5px>);

@mixin DropLine(
  $class: 'drop-line',
  $tokenPrefix: 'dnd',
  $verticalBar: false,
  $startPositionGap: 0px,
  $endPositionGap: 0px
) {
  $loopSize: 6px;
  $startPosition: calc((#{$loopSize} / 2) + #{$startPositionGap});
  $endPosition: calc((#{$loopSize} / 2) + #{$endPositionGap});

  .#{$class} {
    position: relative;
  }

  // loop
  .#{$class}:before {
    content: '';
    background: var(--#{$tokenPrefix}-drop-line-bg);
    border: 1px solid var(--#{$tokenPrefix}-drop-line-loop);
    box-shadow: 0 1px 4px 0 var(--#{$tokenPrefix}-drop-line-loop-shadow);
    position: absolute;
    width: $loopSize;
    height: $loopSize;
    border-radius: 100%;
    z-index: 1;
    pointer-events: none;

    @if ($verticalBar) {
      inset-inline-start: $startPositionGap;
      inset-block-start: 0;
    } @else {
      inset-inline-start: 0;
      inset-block-start: $startPositionGap;
    }
  }
  .#{$class}.place-at-end:before {
    @if ($verticalBar) {
      inset-inline-start: auto;
      inset-inline-end: $endPositionGap;
    } @else {
      inset-block-start: auto;
      inset-block-end: $endPositionGap;
    }
  }

  .#{$class}:after {
    content: '';
    background: var(--#{$tokenPrefix}-drop-line-bg);
    position: absolute;
    pointer-events: none;

    @if ($verticalBar) {
      width: 2px;
      height: 100%;
      inset-inline-start: $startPosition;
    } @else {
      width: 100%;
      height: 2px;
      inset-block-start: $startPosition;
      inset-inline-start: 0;
    }
  }
  .#{$class}.place-at-end:after {
    @if ($verticalBar) {
      inset-inline-start: auto;
      inset-inline-end: $endPosition;
    } @else {
      inset-block-end: $endPosition;
      inset-block-start: auto;
    }
  }
}

// * Drop Indicator (embedded div/span customized as dropline)
// uses -
// @include dndstyles.DropIndicator();

@mixin DropIndicator($class: 'drop-indicator', $tokenPrefix: 'dnd', $verticalBar: false) {
  .#{$class} {
    background-color: var(--#{$tokenPrefix}-drop-line-bg);
    position: absolute;
    display: flex;
    justify-content: center;
    z-index: 1;
    @if ($verticalBar) {
      width: 2px;
      height: 100%;
    } @else {
      width: 100%;
      height: 2px;
    }

    @content;
  }
  .#{$class}:before {
    content: '';
    position: absolute;
    width: 6px;
    height: 6px;
    border-radius: 100%;
    border: 1px solid var(--#{$tokenPrefix}-drop-line-loop);
    background: var(--#{$tokenPrefix}-drop-line-bg);
    box-shadow: 0 1px 4px 0 var(--#{$tokenPrefix}-drop-line-loop-shadow);
    pointer-events: none;
    @if ($verticalBar) {
      transform: translate(-50%, 0);
    } @else {
      transform: translate(0, -50%);
    }
  }
}
