.tiled-layout {
  display: flex;
  flex-direction: column;
}

.tiles-container {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.content {
  flex-grow: 1;
  min-height: 0;
  overflow: auto;
}

.toolbar {
  flex-shrink: 0;
}

.variable-width {
  flex: 1;
}

.row-container {
  display: flex;
  flex-wrap: wrap;
  overflow: auto;
}
