.container {
  display: inline-flex;
  border: 1px solid var(--composite-input-bd);
  box-sizing: border-box;
  min-height: 1.75rem;
  background: var(--composite-input-bg);

  &.disabled {
    border-color: var(--composite-input-disabled-bd);
    &:focus-within {
      border-color: var(--composite-input-disabled-bd);
    }
  }

  &:focus-within {
    border-color: var(--composite-input-active-bd);
  }

  &.focus-visible {
    position: relative;

    &:after {
      outline: var(--composite-input-focus-outline);
      outline-offset: var(--composite-input-focus-outline-offset);
      content: '';
      position: absolute;
      inset: 0;
      pointer-events: none;
    }
  }

  &.alert {
    border-color: var(--composite-input-error-bd);
  }

  &.warning {
    border-color: var(--composite-input-warning-bd);
  }
}

.input {
  min-height: 0;
  flex-grow: 1;
}

.readonly-button {
  visibility: hidden;
}

.button {
  min-height: 0;
  flex-shrink: 0;
  border: 0;
}

.editable-button {
  border-start-start-radius: 0;
  border-end-start-radius: 0;
  border-inline-start: 0;
}
