.formContainer {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  flex-grow: 1;
  padding-top: var(--geoArea-spacing-m);
  box-sizing: border-box;
}

.formScrollContainer {
  flex: 1 1 auto;
  min-height: 0px;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
}

.formContent {
  align-items: stretch;
  height: 100%;
  padding: var(--geoArea-spacing-m);
  box-sizing: border-box;
}

.formIconLarge {
  height: 2rem;
  width: 2rem;
  flex-shrink: 0;
}

.formHeader {
  align-self: center;
  font-weight: bold;
  text-align: center;
  flex-grow: 1;
}

.formField {
  width: 100%;
}

.formFieldCentered {
  align-items: center;
}

.formWindowShadeSection {
  height: 11rem;
  width: 100%;
}

.formDemographicsReloadLabel {
  place-self: center;
  text-align: center;
  align-items: center;
  flex-grow: 1;
}

.footer {
  display: flex;
  box-sizing: border-box;
  justify-content: end;
  padding: var(--geoArea-spacing-m);
}

.footerButton {
  min-width: 0;
}
