.container {
  padding: logical var(--object-switcher-panel-spacing-l) var(--object-switcher-panel-spacing-m)
    var(--object-switcher-panel-spacing-m) var(--object-switcher-panel-spacing-m);
  height: 100%;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
}

.heading {
  font-weight: var(--object-switcher-panel-font-weight-thick);
  justify-content: center;
  width: 100%;
}

.list {
  width: 100%;
  flex-grow: 1;
  min-height: 0;
  overflow: auto;
  margin-block-start: var(--object-switcher-panel-spacing-xl);
}

.divider {
  margin: logical var(--object-switcher-panel-spacing-m) 0;
}

.button-container {
  display: flex;
  justify-content: flex-end;
}
