$dialog-header-base-padding: var(--dialog-spacing-xl);
$dialog-border-radius: var(--dialog-content-border-radius);

.dialog {
  display: flex;
  flex-direction: column;
  margin: auto;
  position: absolute;
  justify-content: center;
  align-items: center;
  min-width: 24rem;
  min-height: 10rem;
  &.with-sub-content-footer {
    min-height: 12rem;
  }
}

.dialog-inner {
  height: 100%;
  inline-size: 100%;
  display: flex;
  flex-grow: 1;
  flex-basis: 100%;
  flex-direction: column;
  color: var(--dialog-content-fg);
  background-color: var(--dialog-content-bg);
  border: var(--dialog-content-bw) solid var(--dialog-content-bd);
  border-radius: $dialog-border-radius;
  box-shadow: var(--dialog-content-sh);
  box-sizing: border-box;
  position: relative;
  max-height: 100%;
  min-height: 100%;
}
.dialog-header {
  flex-shrink: 0;
  color: var(--dialog-header-fg);
  background-color: var(--dialog-header-bg);
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-inline: var(--dialog-spacing-xl);
  padding-block-start: var(--dialog-header-space-top);
  padding-block-end: var(--dialog-header-space-bottom);
  // RTL specific styles not needed as left-right both uses same radius value
  border-top-left-radius: $dialog-border-radius;
  border-top-right-radius: $dialog-border-radius;
}

.dialog-title {
  font-size: var(--dialog-header-font-size);
  font-weight: var(--dialog-header-font-weight);
  color: var(--dialog-header-fg);
  line-height: var(--dialog-line-height);
  overflow: hidden;
  text-align: center;
  .accent-icon {
    margin-inline-end: var(--dialog-spacing-s);
    width: var(--dialog-header-icon-size);
    height: var(--dialog-header-icon-size);
  }
}

.dialog-content {
  flex-grow: 1;
  flex-basis: 100%;
  margin: var(--dialog-content-pd);
  max-width: 100%;
  overflow: auto;
}

.dialog-footer {
  flex-shrink: 0;
  padding: logical var(--dialog-spacing-none)
    var(--dialog-content-start-pd, var(--dialog-content-pd)) var(--dialog-spacing-m)
    var(--dialog-spacing-m);
  display: flex;
  justify-content: flex-end;
  gap: var(--dialog-spacing-s);
  align-items: center;
  width: var(--dialog-footer-width);
  box-sizing: border-box;
}

.dialog-sub-footer {
  padding: logical var(--dialog-spacing-none) var(--dialog-content-pd) var(--dialog-spacing-s)
    var(--dialog-spacing-m);
}

.dialog-footer-controls {
  flex-grow: 1;
}
.dialog-footer-buttons {
  justify-content: flex-end;
  flex-grow: var(--dialog-footer-stretchToFit);
}

.close-icon-wrapper {
  position: absolute;
  inset-inline-end: 0;
  inset-block-start: 0;

  .close-icon {
    cursor: pointer;
    // avoid overlap on dialog rounded edges
    :root[dir='ltr'] & {
      border-top-right-radius: $dialog-border-radius;
    }
    :root[dir='rtl'] & {
      border-top-left-radius: $dialog-border-radius;
    }
  }
}

.drag-cursor {
  cursor: move;
}

.no-drag-cursor {
  cursor: default;
}

.drag-icon {
  cursor: move;
  visibility: hidden;
}
.drag-icon-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 1rem;
  height: 1rem;
  position: absolute;
  inset-block-start: 0;
  inset-inline-start: 0;
  pointer-events: none;

  &:focus {
    outline: var(--dialog-header-drag-icon-focus);
    outline-offset: calc(-1 * var(--focus-width) - 2px);
    .drag-icon {
      visibility: visible;
    }
  }
}

.accent:before {
  content: '';
  width: 100%;
  height: 0.125rem;
  position: absolute;
  inset-block-start: -1px;
  inset-inline-start: 0;
  z-index: 1;
  background-color: var(--dialog-header-accent);
  // RTL specific styles not needed as left-right both uses same radius value
  border-top-left-radius: $dialog-border-radius;
  border-top-right-radius: $dialog-border-radius;
}

// BaseDialog 'headerImage' and 'headerLogo' internal API styles
.header-image {
  // Move the image the negative amount of the top padding. This ensures the header's size
  // contains the image size, but aligns the image at the top of the header.
  margin-block-start: calc(-1 * #{$dialog-header-base-padding});
}

.header-logo {
  position: absolute;
  inset-inline-start: 1rem;
  inset-block-start: 1rem;
}

.busy-state-indicator-container {
  display: contents;
}
