@use 'utils/animation';
.opening {
  :global {
    animation: nova-global-fadein animation.duration(var(--menu-anim-dur-enter))
      var(--menu-anim-tf-enter);
  }
}

.closing {
  :global {
    animation: nova-global-fadeout animation.duration(var(--menu-anim-dur-exit))
      var(--menu-anim-tf-exit) forwards;
  }
}

.menu-popover {
  display: flex;
  overflow-x: hidden;
}

// Allows truncation while also providing the extra space for the scrollbar
// if truncation isn't needed.
.menu-popover-inner {
  max-inline-size: min(calc(100% + var(--menu-scrollbar-offset, 0px)), 100vw);
}
