$token-border-radius: 0.668rem;

%selected-token {
  border-color: var(--token-selected-bd);
  border-width: var(--token-selected-bd-width, 1px);
}

%focus-indicator:focus {
  outline: var(--focus-light-bg);
  outline-offset: calc(var(--focus-width) * -1);
}

%token {
  border-radius: $token-border-radius;
  background-color: var(--token-bg);
  color: var(--token-fg);
  max-inline-size: 100%;
  block-size: 1.5rem;
  box-sizing: border-box;
  position: relative;
  padding: var(--token-spacing-xxs) var(--token-spacing-s);

  &::after {
    display: block;
    position: absolute;
    content: '';
    inset: 0;
    border-radius: $token-border-radius;
    border: 1px solid var(--token-bd);
    pointer-events: none;
  }
}

.token {
  @extend %token;
  font-size: var(--token-font-size-s);
  display: inline-flex;
  align-items: center;
  user-select: none;

  &.dropdown-open:after,
  &.include-button:hover:after,
  &.include-button:focus-within:after,
  &:not(.read-only):focus-within:after {
    @extend %selected-token;
  }
}

.token-button {
  @extend %token;
  border: none;

  &.dropdown-open:after,
  &:hover:after,
  &:focus-within:after {
    @extend %selected-token;
  }
}

.text {
  @extend %focus-indicator;
  font-size: var(--token-font-size-s);
}

.icon {
  block-size: var(--token-font-size-s);
  inline-size: var(--token-font-size-s);
}

.text-wrapper {
  @extend %focus-indicator;
  display: flex;
  align-items: center;
  flex-grow: 1;
  min-inline-size: 1rem;
}

.close-icon-container {
  flex-shrink: 0;
}

%grid-based-token-button {
  padding: 0;
  border: none;
  border-radius: 0%;
  block-size: unset;
}

.grid-based-token-main-button-container {
  max-inline-size: 100%;
}

.grid-based-token-main-button {
  @extend %grid-based-token-button;
  @extend %focus-indicator;
  max-inline-size: 100%;
}

.grid-based-token-icon-button {
  @extend %grid-based-token-button;

  &:focus {
    outline: var(--focus-light-bg);
    outline-offset: calc(2px + var(--focus-width) * -1);
  }
}

.down-arrow-icon {
  flex-shrink: 0;
}
