.content a:focus {
  outline: var(--focus-light-bg);
  outline-offset: calc(-1 * var(--focus-width));
}

// *** STYLES - HELPCONTENT EUA ***

:global {
  $text-size: var(--help-popup-content-xis-font-size-m);
  $margin-medium: var(--help-popup-content-xis-mg); // 1rem;
  $margin-default: 0.6875rem;
  $margin-small: var(--help-popup-content-xis-small-mg); // 0.5rem;
  $margin-tiny: var(--help-popup-content-xis-tiny-mg); // 0.25rem;
  $margin-none: var(--help-popup-content-xis-none-mg); // 0;
  $inset-tiny: var(--help-popup-content-xis-tiny-pd); // 0.25rem;
  $inset-small: var(--help-popup-content-xis-small-pd); // 0.5rem;
  $indentation-m: var(--help-popup-content-xis-pd); // 1rem;
  $indentation: var(--help-popup-content-xis-indent-pd); // 2rem;
  $font-weight-default: var(--help-popup-content-xis-font-weight-default);
  $font-weight-thick: var(--help-popup-content-xis-font-weight-thick);

  .xis-topicContent {
    color: var(--help-popup-content-xis-fg);
    font-size: $text-size;
    line-height: var(--help-popup-content-xis-line-height);

    ol,
    ul {
      padding-inline-start: $indentation;
    }
    a {
      color: var(--help-popup-content-xis-link-fg);
    }
  }
  .xis-title {
    font-size: $text-size;
    font-weight: $font-weight-thick;
    color: var(--help-popup-content-xis-title-fg);
    text-align: center;
    margin-block-end: $margin-medium;
  }

  .xis-paragraph,
  .xis-paraSimple {
    margin-block-start: $margin-none;
    margin-block-end: $margin-default;
    &:last-child {
      margin-block-end: $margin-none;
    }
  }
  .paraLeadin {
    margin: $margin-default $margin-none;
  }

  .xis-caution,
  .xis-note {
    margin-block-start: $margin-medium;
    margin-block-end: $margin-medium;
    padding-block-start: $inset-tiny;
    padding-block-end: $inset-small;
  }

  // caution
  .xis-caution {
    border-block-start: 1px solid var(--help-popup-content-xis-caution-bd);
    border-block-end: 1px solid var(--help-popup-content-xis-caution-bd);
  }
  .xis-cautionGenText {
    color: var(--help-popup-content-xis-caution-fg);
    font-weight: $font-weight-default;
  }
  .xis-cautionLeadin {
    font-weight: $font-weight-thick;
    margin: $margin-none;
  }

  // codeFragment
  .xis-codeFragment {
    font-family: 'Consolas, Menlo,' Lucida Console ', Courier, Monaco, monospace';
    font-weight: $font-weight-default;
    color: var(--help-popup-content-xis-title-fg);
    margin-block-start: $margin-default;
    margin-block-end: $margin-default;
    white-space: pre-wrap;
  }
  .xis-listTermDef .xis-codeFragment,
  .xis-step .xis-codeFragment,
  .xis-definition .xis-codeFragment {
    margin-block-start: $margin-none;
  }
  .xis-userSuppliedValue {
    font-style: italic;
  }

  // listColumnar
  .xis-listColumnar table {
    margin: $margin-default $margin-none;
  }
  .xis-listColumnar td {
    padding-block-end: calc(#{$margin-tiny} - 1px);
    padding-inline-end: calc(#{$indentation} - 1px);
  }
  .xis-paraLeadIn {
    margin: $margin-none;
  }
  .xis-itemColumn {
    margin-block-end: $margin-tiny;
  }

  // list - unordered
  .xis-listUnordered ul {
    margin-block-start: $margin-default;
    padding-inline-start: $indentation-m;
    list-style-type: disc;
    ol {
      padding-inline-start: $indentation-m;
    }
    .xis-item {
      margin-block-end: $margin-default;
    }
    .xis-paraSimple {
      margin-block-end: $margin-small;
    }
    .xis-caution .xis-paraSimple {
      margin-block-end: $margin-none;
    }
  }

  // listSteps
  .xis-step {
    margin-block-end: $margin-default;
    padding-inline-start: $margin-tiny;
    .xis-paragraph {
      margin-block-end: $margin-small;
    }
  }
  .xis-listSteps ol,
  .xis-listSteps ul {
    padding-inline-start: $indentation;
  }
  .xis-listSteps ol .xis-step ul,
  .xis-listSteps ol .xis-step ol {
    padding-inline-start: $indentation-m;
  }
  .xis-listSteps ul {
    list-style-type: decimal;
  }
  .xis-listSteps ol ol,
  .xis-listSteps ul ul {
    list-style-type: lower-alpha;
  }
  .xis-listSteps ol ol ol {
    list-style-type: lower-roman;
  }

  // listTermDef/termDefPair
  .xis-termDefPair {
    margin-block-start: $margin-default;
    margin-block-end: $margin-default;
  }

  .xis-windowItem,
  .xis-windowName {
    font-weight: $font-weight-thick;
  }

  .xis-definition {
    margin-inline-start: $indentation-m;
  }

  // listValueDescription / valueDescriptionPair
  .xis-listValueDescription {
    margin: $margin-default $margin-none;
  }
  .xis-listValueDescription table {
    border: none;
    border-collapse: collapse;
  }
  .xis-valueDescription,
  .xis-listValueDescription td {
    padding-block-start: $margin-small;
  }
  td.xis-value {
    padding-block-start: calc(#{$margin-small} - 1px);
    padding-inline-end: calc(#{$margin-medium} - 1px);
  }

  // note
  .xis-note {
    border-block-start: 2px dotted var(--help-popup-content-xis-note-bd);
    border-block-end: 2px dotted var(--help-popup-content-xis-note-bd);
  }
  .xis-noteGenText {
    font-weight: $font-weight-thick;
    color: var(--help-popup-content-xis-note-fg);
    padding-inline-end: $inset-small;
  }
  .text {
    font-weight: $font-weight-default;
  }

  // seeAlso
  .xis-seeAlso {
    margin-block-end: $margin-medium;
  }
  .xis-seeAlso ul {
    list-style-type: none;
    padding-inline-start: $margin-none;
  }
  .xis-seeAlso li {
    padding-inline-start: $margin-small;
    text-indent: calc(#{$margin-small} * -1);
  }
  .xis-seeAlsoGenTitle {
    font-size: $text-size;
    font-weight: $font-weight-thick;
    border-block-end: 1px solid var(--help-popup-content-xis-gen-title-bd);
    padding: $inset-small $margin-none;
    margin: $margin-default $margin-none;
  }
  .xis-xrefBookTitle,
  .xis-bookTitleRef,
  .xis-xrefSee,
  .xis-xref {
    margin-block-end: $margin-tiny;
  }
  .xis-bookTitleRef,
  .xis-bookTitle,
  .xis-xrefBookTitle,
  .xis-xrefBookTitleRef {
    font-style: italic;
  }

  // seeAlsoGroup
  .xis-seeAlsoGroup {
    margin: $margin-small $margin-none;
  }
  .xis-groupName {
    color: var(--help-popup-content-xis-title-fg);
    font-size: $text-size;
    font-weight: $font-weight-default;
    margin-block-end: $margin-small;
    padding-block-start: $margin-small;
  }

  // tip
  .xis-tip {
    border: 1px solid var(--help-popup-content-xis-tip-bd);
    padding: $inset-small;
    margin: $margin-medium $margin-none;
    p {
      margin: $margin-none;
    }
    h4,
    p {
      display: inline;
    }
    .xis-codeFragment,
    .xis-item:last-child {
      margin-block-end: $margin-none;
    }
  }
  .xis-tipGenText {
    color: var(--help-popup-content-xis-tip-title-fg);
    font-weight: $font-weight-thick;
    margin-inline-end: $margin-medium;
  }
}
