:root {
  // If :root is transformed to an consumer-specific classname and
  // applied to ReportContentWrapper, these styles should still apply
  &.wrapper,
  .wrapper {
    position: relative;
    display: flex;
    flex-grow: 1;
    flex-basis: 0;
    flex-direction: column;
    height: 100%;
    width: 100%;
    isolation: isolate;
  }
}

// NOTE: this selector also applies to the popover root.
:root {
  // NOTE: bootstrap (v4) sets this to 'left' on body.
  // The initial value on our browser target is 'start'.
  text-align: initial;
}
