.suggestion-search-bar {
  display: flex;
  max-inline-size: 38.4375rem;
  width: 100%;
  align-items: center;
  justify-content: flex-end;
  position: relative;
}

.suggestion-search-input {
  width: 100%;
}
.suggestion-search-input input {
  padding-inline-end: var(--catalog-suggestive-search-spacing-xl);
}

.suggestion-search-input.has-clear-button input {
  padding-inline-end: calc(var(--catalog-suggestive-search-spacing-xl) + 1.25rem);
}

.suggestion-search-button-container {
  display: inline-flex;
  position: absolute;
  inset-inline-end: 0;
}

.clear-search-button {
  padding-inline: var(--button-spacing-xxs);
}

