%search-field {
  background-color: var(--searchfield-bg);
  border-color: var(--searchfield-bd);
  &:hover {
    background-color: var(--searchfield-hover-bg);
    border-color: var(--searchfield-hover-bd);
  }
  &.disabled {
    background-color: var(--searchfield-disabled-bg);
    border-color: var(--searchfield-disabled-bd);
  }
}
%search-field-input {
  background: transparent;
  border-width: 0;
  color: var(--searchfield-fg);
  &:hover {
    background: transparent;
    color: var(--searchfield-hover-fg);
  }
  &:focus {
    color: var(--searchfield-active-fg);
    outline: none;
  }
  .disabled & {
    background-color: transparent;
    color: var(--searchfield-disabled-fg);
  }
}

.form {
  display: inline-flex;
  border-style: solid;
  border-width: 1px;
  box-sizing: border-box;
  width: 15rem;
  min-width: 6rem;
  min-height: 1.75rem;
  max-width: 100%;
  @extend %search-field;

  &.active {
    outline: var(--searchfield-focus-outline);
    outline-offset: var(--searchfield-focus-outline-offset);

    &:not(.disabled) {
      border-color: var(--searchfield-active-bd);
    }
  }
}

.search-input {
  flex-grow: 1;
  min-width: 0;

  > input {
    min-height: 0;
    width: 100%;
    @extend %search-field-input;
  }
}

.reset-button {
  flex-shrink: 0;
}

.reset-button-search {
  padding: 0;
}

.search-button {
  min-height: 0;
  flex-shrink: 0;
  padding: 0 var(--searchfield-spacing-xs);
}

.disabled {
  cursor: default;

  &.active:focus-within {
    outline: var(--searchfield-focus-outline, var(--focus-light-bg));
    outline-offset: calc(-2px + var(--focus-width) * -1);
  }
}

.search-button-container {
  display: inline-flex;
  align-items: center;
  padding-inline-start: 0.25rem;
}
