$small-size: 1.375rem;
$small-font-size: var(--avatar-font-size-s);
$small-icon-size: var(--avatar-font-size-s);
$medium-size: 2rem;
$medium-font-size: var(--avatar-font-size-m);
$medium-icon-size: var(--avatar-font-size-m);
$extra-large-size: 5.5rem;
$extra-large-font-size: 3rem;
$extra-large-icon-size: 3rem;

.noselect {
  user-select: none;
}

.avatar {
  color: var(--avatar-fg);
  border-color: var(--avatar-bd);
  position: relative;

  // Icon Styling
  svg > :global(.iconColor) {
    --icon-default-fg: var(--avatar-fg);
  }
}

.inner-container {
  @extend .noselect;
  background-color: var(--avatar-bg);
  align-items: center;
  border-color: var(--avatar-bd);
  border-radius: var(--avatar-border-radius);
  border-style: solid;
  border-width: var(--avatar-bw);
  box-sizing: border-box;
  display: flex;
  height: inherit;
  justify-content: center;
  width: inherit;
}

.has-image {
  border-style: none;
  border: none;
}

.avatar > * {
  font-weight: var(--avatar-font-weight);
  text-transform: var(--avatar-text-transform-uppercase);
}

.avatar-image {
  border-radius: 50%;
  height: 100%;
  object-fit: cover;
  width: 100%;

  &.image-invisible {
    visibility: hidden;
  }
}

.avatar-button {
  cursor: pointer;
}

.avatar-button:hover {
  background-color: var(--avatar-hover-bg);
  color: var(--avatar-hover-fg);

  // Icon Styling
  svg > :global(.iconColor) {
    --icon-default-fg: var(--avatar-hover-fg);
  }
}

.avatar-button:active,
.avatar-button.active {
  background-color: var(--avatar-active-bg);
  color: var(--avatar-active-fg);

  // Icon Styling
  svg > :global(.iconColor) {
    --icon-default-fg: var(--avatar-active-fg);
  }
}

.small {
  font-size: $small-font-size;
  height: $small-size;
  width: $small-size;
}

.medium {
  font-size: $medium-font-size;
  height: $medium-size;
  width: $medium-size;
}

.extra-large {
  font-size: $extra-large-font-size;
  height: $extra-large-size;
  width: $extra-large-size;
}

.small .avatar-icon {
  width: $small-icon-size;
  height: $small-icon-size;
}
.medium .avatar-icon {
  width: $medium-icon-size;
  height: $medium-icon-size;
}
.extra-large .avatar-icon {
  width: $extra-large-icon-size;
  height: $extra-large-icon-size;
}

.avatar:focus-visible .inner-container::after {
  content: '';
  outline: var(--avatar-focus);
  outline-offset: calc(-1 * var(--focus-width) - 1px);
  position: absolute;
  height: 100%;
  width: 100%;
  z-index: 1;
}
