// Scrollbars should not scale with base font size, so use 'px' values instead of 'rem' values.
:root {
  ::-webkit-scrollbar {
    width: 16px;
    height: 16px;
    background-color: transparent;
  }

  ::-webkit-scrollbar-corner {
    background-color: transparent;
  }

  ::-webkit-scrollbar-button {
    display: none;
  }

  ::-webkit-scrollbar-thumb {
    background-color: var(--scrollbar-bg, hsl(216, 13%, 53%));
    background-clip: padding-box;
    border-radius: 8px;
    border-color: transparent;
    border-style: solid;
    border-width: 6px;
    min-height: 24px;
  }
}

:root *:hover::-webkit-scrollbar {
  background-color: var(--scrollbar-hover-track-bg);
}

:root *:hover::-webkit-scrollbar-thumb {
  border-width: 4px;
}
