.tick-container_horizontal, .tick-container_vertical {
  pointer-events: none;
  position: relative;
  box-sizing: border-box;
  width: calc(100% + 1px);
  height: calc(100% + 1px);
}

.tick-container_vertical {
  inset-inline-start: -1px;
  inset-block-start: -1px;
}

.tick-container_horizontal {
  inset-inline-start: -1px;
}

.tick_horizontal, .tick_vertical {
  position: absolute;
  display: inline-block;
}

.tick_vertical {
  height: 1px;
  width: 0.5rem;
  inset-inline-start: -0.375rem;
  border-block-start: 1px solid var(--slider-track-ticks-fg);
}

.tick_horizontal {
  width: 1px;
  height: 0.5rem;
  inset-block-start: 100%;
  border-inline-start: 1px solid var(--slider-track-ticks-fg);
}

