.dropdown-color-swatch {
  border: var(--colorpicker-swatch-bd-width) solid var(--colorpicker-swatch-bd);
  background-color: var(--colorpicker-swatch-bg);
  border-radius: var(--colorpicker-swatch-border-radius);
  padding: var(--colorpicker-swatch-pd);
  box-sizing: border-box;
  cursor: pointer;
  min-inline-size: 1.75rem;
  min-block-size: 1.75rem;
}
.dropdown-color-swatch:hover {
  border-color: var(--colorpicker-swatch-hover-bd);
  background-color: var(--colorpicker-swatch-hover-bg);
}
.dropdown-color-swatch:active {
  border-color: var(--colorpicker-swatch-active-bd);
  background-color: var(--colorpicker-swatch-active-bg);
}
.dropdown-color-swatch.disabled {
  cursor: default;
  border-color: var(--colorpicker-swatch-disabled-bd);
}
.dropdown-color-swatch.read-only {
  cursor: default;
  border-color: transparent;
}

.dropdown-color-swatch-inner {
  border: 1px solid var(--colorpicker-swatch-inner-bd);
  width: 1rem;
  height: 1rem;
  border-radius: 1px;
  box-sizing: border-box;
}
.disabled .dropdown-color-swatch-inner {
  opacity: 0.3;
}

.no-color-swatch .dropdown-color-swatch-inner {
  background: linear-gradient(-45deg, transparent 47%, var(--colorpicker-no-color-swatch-mark-bg) 47%, var(--colorpicker-no-color-swatch-mark-bg) 54%, transparent 54%);
}

.popover {
  padding: 0;
}

