.toolbar {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
  position: relative;
  overflow-x: clip;

  & > * + * {
    margin-inline-start: var(--toolbar-spacing-xs);
    margin-inline-end: 0;
  }
}

.separator-container {
  display: flex;
  align-self: stretch;
}

.spacer-container {
  display: contents;
  flex-grow: 1; // Legacy Edge does not support display: contents
}
