$token-border-radius: 0.668rem;

.drag-image-preview {
  border-radius: $token-border-radius;

  & > div {
    max-width: unset;
    margin: 0;
  }
}

.token {
  min-width: 3.5rem; // Match the min-width in Tokenizer.css .token class
}

.token:after {
  border-color: var(--token-selected-bd) !important;
  border-width: var(--token-selected-bd-width, 1px) !important;
}
