.carousel-container {
  position: relative;
  display: flex;
  align-items: center;
  max-width: 100%;
  width: 100%;
  height: 100%;
}

.page-container {
  justify-content: center;
  align-items: center;
}

.scroll-window {
  margin-inline: var(--tile-carousel-spacing-s);
}

.zero-state {
  width: 100%;
  min-height: 5rem;
  padding-inline: var(--tile-carousel-spacing-m);
}

.add-button,
.add-button:hover {
  width: var(--tile-carousel-add-button-size, 7.5rem);
  height: var(--tile-carousel-add-button-size, 7.5rem);
  flex-shrink: 0;
  border: 1px dashed var(--tile-carousel-add-button-bd);
}

.scrollable-container {
  overflow: hidden;
}

.inner-container {
  max-width: 100%;
  align-items: center;
}

.zero-state-container {
  width: 100%;
  height: 100%;
}

.hidden {
  display: none;
}
