%label-container {
  display: flex;
}

.label-container_vertical {
  @extend %label-container;
  height: 100%;
  grid-column: 1;
  grid-row: 3 / 3;
  flex-direction: column;
  padding-inline-end: var(--slider-tick-label-spacing-xs);
  justify-content: flex-end;
}

.label-container_horizontal {
  @extend %label-container;
  grid-row: 3;
  grid-column: 3 / 3;
  flex-direction: row;
}

.slider-tick-label {
  font-size: var(--slider-tick-label-fontSize, var(--slider-tick-label-font-size-s));
  font-weight: var(--slider-tick-label-fontWeight, inherit);
  font-style: var(--slider-tick-label-fontStyle, inherit);
  font-family: var(--slider-tick-label-fontFamily, inherit);
  text-decoration: var(--slider-tick-label-textDecoration, inherit);
  color: var(--slider-tick-label-fg);

  .disabled & {
    color: var(--slider-tick-label-disabled-fg);
  }
}

%label {
  font-size: var(--slider-tick-label-font-size-s);
  position: relative;
  user-select: none;
  white-space: nowrap;
  color: var(--slider-tick-label-fg);
}

.label_vertical {
  @extend %label;
  display: block;
  transform: translateY(-50%);
  text-align: end;
}

.label_horizontal {
  @extend %label;
  display: inline-block;
  transform: translateX(-50%);
  inset-inline-start: 100%;

  &:not(.label-horizontal-first, .label-horizontal-last) {
    max-width: 100%;
  }
}
[dir='rtl'] .label_horizontal {
  transform: translateX(50%);
}

.label-horizontal-first {
  transform: initial;
}
[dir='rtl'] .label-horizontal-first {
  transform: initial;
}

.label-horizontal-last {
  transform: translateX(-100%);
  text-align: right;
  max-width: 50%;
}
[dir='rtl'] .label-horizontal-last {
  transform: translateX(100%);
}
