// Field Min Width
$fieldMinWidthProp: --with-labels-field-min-width;
$fieldMinWidthFallback: 3rem;
$fieldMinWidth: var($fieldMinWidthProp, $fieldMinWidthFallback);

// Field Width
$fieldWidthProp: --with-labels-field-width;

// Field Label Min Width
$fieldLabelMinWidthProp: --with-labels-field-label-min-width;
$fieldLabelMinWidthFallback: 3rem;
$fieldLabelMinWidth: var($fieldLabelMinWidthProp, $fieldLabelMinWidthFallback);

// Field Label Width
$fieldLabelWidthProp: --with-labels-field-label-width;

// Field Suffix Min Width
$fieldSuffixMinWidthProp: --with-labels-field-suffix-min-width;
$fieldSuffixMinWidthFallback: 0;
$fieldSuffixMinWidth: var($fieldSuffixMinWidthProp, $fieldSuffixMinWidthFallback);

//Setup the grid for potential areas
.withLabels {
  display: grid;
  align-items: flex-start;

  &.labelTop {
    grid-template-areas:
      'label label'
      'input suffix'
      'description description';
    grid-template-columns: minmax($fieldMinWidth, auto) minmax($fieldSuffixMinWidth, 1fr);

    grid-template-rows: auto minmax(auto, 1fr) auto;

    &.field-width-as-percentage {
      grid-template-columns: minmax($fieldMinWidth, var($fieldWidthProp)) minmax(
          $fieldSuffixMinWidth,
          min-content
        );
    }
  }

  &.labelLeft {
    grid-template-areas:
      'label input suffix'
      'label description description';
    grid-template-rows: minmax(auto, 1fr) auto;
    grid-template-columns:
      minmax($fieldLabelMinWidth, var($fieldLabelWidthProp, max-content)) minmax(
        $fieldMinWidth,
        var($fieldWidthProp, auto)
      )
      minmax($fieldSuffixMinWidth, 1fr);

    &.field-width-as-percentage {
      grid-template-columns:
        minmax($fieldLabelMinWidth, var($fieldLabelWidthProp, max-content)) minmax(
          $fieldMinWidth,
          var($fieldWidthProp)
        )
        minmax($fieldSuffixMinWidth, auto);
    }
  }
}

.input {
  grid-area: input;
  display: flex; // need for safari to avoid extra space at bottom
}

.label {
  grid-area: label;

  //Label specific styling based on labelPosition of "top"
  .labelTop & {
    padding-block-end: var(--with-labels-vertical-spacing-override, var(--with-labels-spacing-xs));
  }

  //Label specific styling based on labelPosition of "left"
  .labelLeft & {
    display: flex;
    padding-block-start: calc(var(--with-labels-spacing-xs) + 1px);
    padding-inline-end: var(
      --with-labels-horizontal-spacing-override,
      var(--with-labels-spacing-end)
    );
  }
}

.description {
  grid-area: description;
  padding-block-start: var(--with-labels-spacing-xs);

  .text {
    font-size: var(--with-labels-font-size-s);
    vertical-align: top;
  }
}

.suffix {
  grid-area: suffix;
  display: inline-flex;
  padding-block-start: var(--with-labels-spacing-xs);
  padding-inline-start: var(--with-labels-spacing-s);
}
