@use 'utils/checkbox';

.container {
  display: grid;
  grid-template-columns: checkbox.$indentation minmax(0, auto) 1fr; // help popup should be close to the label when if description is very long
  grid-template-rows: auto auto;
  row-gap: var(--checkbox-row-gap);
  place-items: start;

  &.no-label {
    grid-template-columns: auto auto;
  }
}

.main {
  grid-row: 1 / 2;
  grid-column: 1 / 3;
  cursor: pointer;
  position: relative;
  height: auto;
  display: flex;
  flex-direction: row-reverse;
  align-items: flex-start;
  width: max-content;
  max-width: 100%;
  box-sizing: border-box;
  //Padding is added to allow room for the focus indicator which is not defined in rems, so this shouldn't be either.
  // 'logical' shorthand refers to 'block-start inline-start block-end inline-end'. In LTR this is 'top left bottom right'.
  padding: logical checkbox.$focusMinimumPadding checkbox.$focusMinimumPadding
    checkbox.$focusMinimumPadding var(--checkbox-spacing-xs);

  .no-label & {
    grid-column: 1 / 2;
    padding: checkbox.$focusMinimumPadding;
    margin-block-start: var(--checkbox-spacing-xxs);
    margin-block-end: var(--checkbox-spacing-xxs);
  }

  &.disabled,
  &.read-only {
    cursor: default;
  }

  &:focus:after {
    content: '';
    pointer-events: none;
    position: absolute;
    inset-inline-start: 0;
    inset-block-start: 0;
    height: 100%;
    width: 100%;
    outline: var(--focus-light-bg);
    outline-offset: calc(-1 * var(--focus-width));
  }
}

.box {
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  border-radius: 1px;
  border-style: solid;
  border-width: checkbox.$borderWidth;
  height: checkbox.$size;
  width: checkbox.$size;
  padding: 0;
  margin-inline-end: checkbox.$marginInlineEnd;
  margin-block-start: var(--checkbox-spacing-xxs);
  margin-block-end: var(--checkbox-spacing-xxs);
  transition: all 0.15s ease;
  flex-shrink: 0;
  background-color: var(--checkbox-bg);
  border-color: var(--checkbox-bd);

  .no-label & {
    margin: 0;
  }

  &.mixed:after {
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    // This border matches the background of the checkbox so we use bg
    border: solid 0.2rem var(--checkbox-bg);
    // This is the mixed mark so we use fg
    background: var(--checkbox-fg);
  }

  // Selected state
  &.selected {
    background-color: var(--checkbox-selected-bg);
    border-color: var(--checkbox-selected-bd);
  }

  // Disabled & Disabled Read-Only shared styles
  .disabled &,
  .disabled.read-only & {
    background-color: var(--checkbox-disabled-bg);
    border-color: var(--checkbox-disabled-bd);

    &.mixed:after {
      background-color: var(--checkbox-disabled-fg);
    }
  }

  // Disabled Selected State
  .disabled &.selected {
    background-color: var(--checkbox-disabled-selected-bg);
    border-color: var(--checkbox-disabled-selected-bd);
  }

  // Read-Only & Read-Only Selected State
  .read-only & {
    background-color: var(--checkbox-read-only-bg);
    border-color: var(--checkbox-read-only-bd);
  }

  // Disabled Read-Only Selected State
  .disabled.read-only &.selected {
    background-color: var(--checkbox-disabled-bg);
    border-color: var(--checkbox-disabled-bd);
  }
}

.label {
  min-width: 0;
  font-size: var(--checkbox-font-size-m);
}

.help-button {
  grid-row: 1 / 2;
  grid-column: -2 / -1;
  min-height: unset;
  margin-block-start: var(--checkbox-spacing-xxs);
  padding: var(--checkbox-squish-xxs);
}

.description {
  grid-row: 2 / 3;
  grid-column: -3 / -1;
  font-size: var(--checkbox-font-size-xs);
}
