.container {
  display: flex;
}

.separator {
  // NOTE: this is the layout from OpenUI5. It could probably be simplified.
  box-sizing: border-box;
  min-width: 0.5rem;
  // 'fr-CA' is the only locale not using a colon or a dot as time separator, it uses a separator of
  // 'h', 'min', 's' so we don't want the max-width set for this locale
  &:not(.frCaLocale) {
    max-width: 0.7rem;
  }
  padding: 0.125rem;
  white-space: pre;
  display: inline-flex;
  justify-content: center;
  user-select: none;
  align-items: flex-end;
  height: 1.5rem;
}

.milliseconds,
.hours,
.minutes {
  min-width: 3.5rem;
  width: 3.5rem;
}
