.offscreen {
  display: block !important;
  inset-inline-start: -10000px !important;
  inset-block-start: 0 !important;
  width: 1px !important;
  height: 1px !important;
  overflow: hidden !important;
  position: absolute;
}

.container {
  display: inline-block;
  max-width: var(--dropdown-max-width);
  visibility: var(--dropdown-visibility);
  min-width: 6rem;
  line-height: normal;
}

.dropdown-popover {
  white-space: nowrap;
}

.dropdown-popover-item {
  cursor: default;
}

.message {
  padding: var(--dropdown-spacing-squish-s);
  font-size: var(--dropdown-font-size-m);
  white-space: normal;
  overflow-wrap: anywhere;
  word-break: break-word;
  background-color: var(--dropdown-message-bg);
  border: var(--dropdown-message-bd);
  color: var(--dropdown-message-fg);
}

