.status-overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0;
  left: 0;
}

.content {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  outline: none;
  flex-direction: column;
}

