@use 'utils/animation';

.container {
  position: absolute;
  overflow: hidden;
  inset-block-start: 0;
  inset-inline-end: 0;
  height: 100vh;
  width: var(--floating-panel-width);
  background-color: var(--floating-panel-bg);
  border-inline-start: 1px solid var(--floating-panel-bd);
  box-shadow: var(--floating-panel-sh);
  z-index: 1;
  will-change: transform;

  :root[dir='ltr'] & {
    :global {
      animation: nova-floatingpanel-slidein-ltr
        animation.duration(var(--floating-panel-anim-dur-enter)) var(--floating-panel-anim-tf-enter);
    }
  }
  :root[dir='rtl'] & {
    :global {
      animation: nova-floatingpanel-slidein-rtl
        animation.duration(var(--floating-panel-anim-dur-enter)) var(--floating-panel-anim-tf-enter);
    }
  }
  :root[dir='ltr'] &-closing {
    :global {
      animation: nova-floatingpanel-slideout-ltr
        animation.duration(var(--floating-panel-anim-dur-exit)) var(--floating-panel-anim-tf-exit);
    }
  }
  :root[dir='rtl'] &-closing {
    :global {
      animation: nova-floatingpanel-slideout-rtl
        animation.duration(var(--floating-panel-anim-dur-exit)) var(--floating-panel-anim-tf-exit);
    }
  }
}
