.component-container_horizontal, .component-container_vertical {
  display: flex;
  position: relative;
  align-items: center;
}

.scroll-container {
  width: 100%;
  height: 100%;
  min-width: 0;
  flex-grow: 1;
}
.scroll-container div {
  width: 100%;
  height: 100%;
}

.content-container {
  width: 100%;
  height: 100%;
}
.no-scroll .content-container {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.button-container {
  max-width: max(100%, var(--segmentedbutton-extreme-min-width, 2rem), 2rem);
  flex-grow: 1;
}
.button-container .button {
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
  border-radius: 0;
}
.no-scroll .shrinking-text .button-container {
  min-width: 3rem;
}
.no-scroll .shrinking-icon .button-container {
  min-width: 2rem;
}

.component-container_vertical {
  flex-direction: column;
}
.component-container_vertical .button {
  border-bottom-width: 0px;
}
.component-container_vertical .button-container:first-child .button {
  border-start-start-radius: var(--segmentedbutton-border-radius);
  border-start-end-radius: var(--segmentedbutton-border-radius);
}
.component-container_vertical .button-container:last-child .button {
  border-bottom-width: 1px;
  border-end-start-radius: var(--segmentedbutton-border-radius);
  border-end-end-radius: var(--segmentedbutton-border-radius);
}
.component-container_vertical .disabled .button {
  border-top-width: 1px;
  border-top-color: var(--button-normal-bd);
}
.component-container_vertical .selected:not(.disabled) + .button-container .button {
  border-top-color: var(--button-selected-bd);
}
.component-container_vertical .disabled:first-child .button,
.component-container_vertical .disabled + .disabled .button {
  border-top-color: var(--button-bd);
}
.component-container_vertical.no-scroll .content-container {
  flex-direction: column;
}

.component-container_horizontal {
  flex-direction: row;
}
.component-container_horizontal .content-container {
  align-items: stretch;
}
.component-container_horizontal .button {
  border-inline-end-width: 0px;
}
.component-container_horizontal .button-container:first-child .button {
  border-start-start-radius: var(--segmentedbutton-border-radius);
  border-end-start-radius: var(--segmentedbutton-border-radius);
}
.component-container_horizontal .button-container:last-child .button {
  border-inline-end-width: 1px;
  border-start-end-radius: var(--segmentedbutton-border-radius);
  border-end-end-radius: var(--segmentedbutton-border-radius);
}
.component-container_horizontal .disabled .button {
  border-inline-start-width: 1px;
  border-inline-start-color: var(--button-normal-bd);
}
.component-container_horizontal .selected:not(.disabled) + .button-container .button {
  border-inline-start-color: var(--button-selected-bd);
}
.component-container_horizontal .disabled:first-child .button,
.component-container_horizontal .disabled + .disabled .button {
  border-inline-start-color: var(--button-bd);
}
.component-container_horizontal.equal-width .button-container {
  flex-basis: 0;
}

