.container {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding-top: var(--pin-spacing-m);
  box-sizing: border-box;
  overflow: hidden;
}

.form-scroll-container {
  flex: 1 1 auto;
  min-height: 0px;
  height: 100%;
  overflow: hidden;
}

.form-content {
  align-items: stretch;
  height: 100%;
  box-sizing: border-box;
  overflow-x: hidden;
  overflow-y: auto;
  padding: var(--pin-spacing-m);
}

.input {
  width: 100%;
  min-width: 0;
}

.label {
  align-items: center;
}

.pin-icon {
  height: 2rem;
  width: 2rem;
  flex-shrink: 0;
}

.location-label {
  color: var(--myLocation-fg);
}

.linked-list-heading {
  width: 100%;
  justify-content: space-between;
  align-items: flex-end;
  box-sizing: border-box;
  padding: 0 var(--pin-spacing-m);
}

.heading-text {
  font-weight: 600;
  align-items: flex-end;
}

.linked-list {
  width: 100%;
}

.add-area-popover-title {
  justify-content: center;
  width: 100%;
  padding-bottom: var(--addAreaPopoverTitle-spacing-m);
  font-weight: 600;
  color: var(--addAreaPopoverTitle-fg);
}

.content-container {
  height: 0;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.footer {
  padding: var(--pin-spacing-m);
  flex-shrink: 0;
  max-height: 100%;
  box-sizing: border-box;
}

.footer-toolbar {
  flex-grow: 1;
  flex-flow: wrap;
  overflow: hidden auto;
}

.footer-toolbar-button {
  flex-direction: column;
  font-size: var(--button-font-size-s);
  gap: var(--button-spacing-s);
}

