.list {
  flex-grow: 1;
  min-height: 0;
}
.list .list-inner {
  height: 100%;
  width: 100%;
}

.icon-column-static-icon {
  margin-block-start: var(--list-spacing-xs);
}

.row-selection-border > .cell {
  position: relative;
}
.row-selection-border > .cell.direction_ltr::before {
  left: 0;
}
.row-selection-border > .cell.direction_rtl::before {
  right: 0;
}
.row-selection-border > .cell::before {
  content: "";
  border-block-start: var(--focus-width) solid transparent;
  border-block-end: var(--focus-width) solid transparent;
  width: calc(100% - var(--table-last-column-pd-adjustment, 0px) - 1px);
  height: 100%;
  inset-block-start: 0;
  position: absolute;
  box-sizing: border-box;
  pointer-events: none;
}
.row-selection-border > .cell.direction_ltr:first-child::before {
  border-left: var(--focus-width) solid transparent;
}
.row-selection-border > .cell.direction_rtl:first-child::before {
  border-right: var(--focus-width) solid transparent;
}
.row-selection-border > .cell.direction_ltr:last-child::after {
  border-right: var(--focus-width) solid transparent;
  right: var(--table-last-column-pd-adjustment, 0px);
}
.row-selection-border > .cell.direction_rtl:last-child::after {
  border-left: var(--focus-width) solid transparent;
  left: var(--table-last-column-pd-adjustment, 0px);
}
.row-selection-border > .cell:last-child::after {
  content: "";
  position: absolute;
  height: 100%;
  box-sizing: border-box;
}
.row-selection-border.row-selection-border-selected > .cell::before,
.row-selection-border.row-selection-border-selected > .cell:first-child::before,
.row-selection-border.row-selection-border-selected > .cell:last-child::after {
  border-color: var(--list-row-border-highlighting-color);
  z-index: 1;
}

.display-icons-on-demand .icon-content {
  display: none;
}

.item-row {
  background-color: var(--list-item-bg);
}
.item-row:hover {
  --list-item-fg: var(--list-item-hover-fg, var(--list-item-default-fg));
  --list-item-description-fg: var(--list-item-hover-fg, var(--list-item-default-description-fg));
  background-color: var(--list-item-hover-bg, var(--list-item-bg));
}

.item-row:not(.ignore-hover):hover .display-icons-on-demand .icon-content {
  display: inherit;
}

.item-row.focused-show-icons .display-icons-on-demand .icon-content {
  display: inherit;
}

.display-icons-on-demand [data-context-menu-visible=true] .icon-content {
  display: inherit;
}

.item-row.help-popup-expanded-by-row .display-icons-on-demand .icon-content {
  display: inherit;
}

