  height: 100%;
}

:root .sas_components-Image-Image_fit {
  width: 100%;
  height: 100%;
}

:root .sas_components-Image-Image_fit-height {
  height: 100%;
}

:root .sas_components-Image-Image_fit-width {
  width: 100%;
}

:root .sas_components-Image-Image_tile {
  background-size: auto;
  background-repeat: repeat;
  width: 100%;
  height: 100%;
}

:root .sas_components-Image-Image_span {
  display: inline-block;
}

