.draggable {
  cursor: default;
}
.draggable:hover {
  transition: box-shadow calc(var(--dnd-item-anim-dur) * var(--nova-animation-duration-factor, 1)) var(--dnd-item-trans-dur);
  box-shadow: var(--dnd-item-medium-sh);
}

.dragged-item {
  opacity: var(--dnd-moved-item-opacity);
  box-sizing: border-box;
}

.drag-over {
  background: var(--dnd-drop-zone-bg);
}

.drag-over {
  outline: 1px solid var(--dnd-drop-zone-bd);
  outline-offset: -1px;
}

.drag-image-preview {
  padding: 0 0.25rem;
  box-sizing: border-box;
}

.node-count {
  position: absolute;
  inset-block-start: -0.75rem;
  inset-inline-end: -0.93rem;
}
.node-count div {
  border-radius: 50%;
}

