%select {
  width: 100%;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: var(--select-min-block-size);
  font-size: var(--select-font-size);
  font-family: var(--select-font-family);
  font-style: var(--select-font-style);
  font-weight: var(--select-font-weight);
  text-decoration: var(--select-text-decoration);
  border: 1px solid var(--select-bd);
  background-color: var(--select-bg);
  color: var(--select-fg);
}

%select-icon {
  .select & :global(.iconColor) {
    --icon-default-fg: var(--select-fg);
  }

  .select:hover & :global(.iconColor) {
    --icon-default-fg: var(--select-hover-fg);
  }

  .select.expanded & :global(.iconColor) {
    --icon-default-fg: var(--select-active-fg);
  }
}

.select {
  cursor: pointer;
  padding: var(--select-pd);
  @extend %select;

  &:hover {
    border-color: var(--select-hover-bd);
    background-color: var(--select-hover-bg);
    color: var(--select-hover-fg);
  }
}

.read-only {
  padding: var(--select-spacing-squish-s);
  padding-inline-start: 0;
  @extend %select;
  cursor: default;

  &.disabled {
    color: var(--select-disabled-fg);
    background-color: transparent;
  }
}

.expanded {
  border-color: var(--select-active-bd);

  &,
  &:hover {
    background-color: var(--select-active-bg);
    border-color: var(--select-active-bd);
    color: var(--select-active-fg);
  }
}

.focus-visible:focus {
  outline: var(--focus-light-bg);
  outline-offset: calc(-1 * var(--focus-width) - 2px);

  &.read-only {
    outline-offset: calc(-1 * var(--focus-width));
  }
}

.text-wrapper {
  display: inline-flex;
  line-height: normal;
  min-width: 0;
  flex-grow: 1;
}

.label {
  white-space: pre;
  text-overflow: ellipsis;
  overflow: hidden;
  width: 100%;

  [dir='rtl'] & {
    text-indent: 1px; //avoid text edge cutoff specifically in italics
  }
}

.label-disabled {
  cursor: default;
  user-select: none;
}

.label-icon-only {
  display: none;
}

.label-placeholder {
  composes: label;
  color: var(--select-placeholder-fg);

  .expanded & {
    color: var(--select-placeholder-active-fg);
  }
}

.select-icon {
  @extend %select-icon;
  flex-shrink: 0;
  margin-inline-start: var(--select-spacing-xs);
}

.dropdown-popover {
  white-space: nowrap;
}

.dropdown-popover-item {
  cursor: default;
}

// FIXME: due to pending token refactor, just hard-code this value for now
.disabled,
.disabled:hover,
.disabled:focus {
  opacity: var(--select-disabled-opacity);
  background-color: var(--select-disabled-bg);
  border-color: var(--select-disabled-bd);
  color: var(--select-disabled-fg);
  cursor: default;
}

.status {
  border-color: var(--select-status-bd);

  &:hover {
    border-color: var(--select-status-bd);
  }

  &:focus {
    border-color: var(--select-status-bd);
  }

  &.expanded {
    color: var(--select-active-fg);
  }
}

.status-icon {
  flex-shrink: 0;
  display: flex;
  align-items: center;
  margin-inline-start: var(--select-spacing-xs);
  // pointer-events: none allows click events to reference
  // the parent as the target since this element unmounts onFocus - NOVACOMMONS-12333
  pointer-events: none;
}

.item-icon {
  @extend %select-icon;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  width: var(--dropdown-icon-width);
  &:not(.icon-only) {
    margin-inline-end: var(--select-spacing-xs);
  }
}
