@use 'utils/animation';
$switch-height: 0.875rem;

.container {
  display: flex;
  height: $switch-height;
  width: 1.75rem;

  // Centers the Switch with other input controls
  padding: calc((1.75rem - #{$switch-height}) / 2) 0;
}

.switch-button {
  background-color: var(--switch-bg);
  border: 1px solid var(--switch-bd);
  border-radius: 1rem;
  cursor: pointer;
  height: 100%;
  overflow: hidden;
  padding: 0;
  transition: animation.duration(var(--switch-anim-dur)) var(--switch-anim-tf);
  transition-property: background-color, border-color;
  width: 100%;

  &:focus {
    outline: var(--focus-light-bg);
  }

  &.readonly {
    cursor: default;
  }

  &.disabled {
    background-color: var(--switch-disabled-bg);
    border-color: var(--switch-disabled-bd);
    cursor: default;
  }
}

.switch-inner {
  height: 100%;
  position: relative;
  transition: animation.duration(var(--switch-anim-dur)) var(--switch-anim-tf);
  transition-property: transform;
  width: 100%;
  display: flex;
  align-items: center;
  padding: 1px;
  box-sizing: border-box;
}

.handle {
  align-items: center;
  background-color: var(--switch-fg);
  border: 1px solid var(--switch-fg);
  border-radius: 50%;
  display: flex;
  height: var(--switch-handle-size);
  transition: animation.duration(var(--switch-anim-dur)) var(--switch-anim-tf);
  transition-property: background-color, border-color;
  width: var(--switch-handle-size);

  .disabled & {
    background-color: var(--switch-disabled-fg);
    border-color: var(--switch-disabled-bd);
  }
}

.icon {
  &-off {
    inset-inline-start: calc(100% - 1.5 * var(--switch-handle-size));
    position: absolute;

    > svg > :global(.iconColor) {
      transition: animation.duration(var(--switch-anim-dur)) var(--switch-anim-tf);
      transition-property: fill;
    }
  }

  &-on {
    inset-inline-start: calc(-100% + 1.25 * var(--switch-handle-size));
    position: absolute;

    > svg > :global(.iconColor) {
      transition: animation.duration(var(--switch-anim-dur)) var(--switch-anim-tf);
      transition-property: fill;
    }
  }
}
