//commonly use Flex Layout and Flex Item in the DualSelector
%flex-layout {
  display: flex;
  box-sizing: border-box;
}

// DualSelector Style starts here
.container {
  @extend %flex-layout;
  height: 100%;
  min-height: 11rem;
  width: 100%;
}

//DualSelector Source Container
.source-panel {
  @extend %flex-layout;
  height: 100%;
  padding-inline-end: var(--dualselector-spacing-s);
  gap: var(--dualselector-spacing-xs);
}

.contents {
  @extend %flex-layout;
  flex-grow: 1;
  overflow: hidden;
  flex-direction: column;
}

.content-title {
  padding-block-end: var(--dualselector-spacing-s);
  display: flex;
  align-items: center;
  width: 100%;
}

.content-title-text {
  display: contents;
}

//DualSelector Source/Target list Container
.content-list {
  border: 1px solid var(--dualselector-bd);
  min-width: auto;
  height: 100%;
  overflow: auto;
  flex-grow: 1;
  background: var(--dualselector-bg);
}

//DualSelector Target list Container
.target-panel {
  @extend %flex-layout;
  height: 100%;
  padding-inline-start: var(--dualselector-spacing-s);
}

.target-panel-multiple {
  flex: 1;
  flex-direction: column;
}

.action-button-panel {
  display: flex;
  padding-inline-start: var(--dualselector-action-button-spacing);
  flex-direction: column;
  justify-content: center;
  position: relative;
  height: 100%;
}

.custom-field {
  margin-block-start: var(--dualselector-spacing-s);
}

.target-list-container {
  flex: 1;
  overflow: auto;
}

.add-buttons-container {
  justify-content: space-evenly;
  height: 100%;
}

.target-list-top-spacing {
  margin-block-start: var(--dualselector-spacing-m);
}
