:root .sas_components-ObjectToolbar-ObjectToolbar_wrapper {
  position: absolute;
  right: 2px;
  top: 2px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-end;
  pointer-events: none;
  gap: 0.75rem;
  transition-property: opacity;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}

:root .sas_components-ObjectToolbar-ObjectToolbar_enable-pointer {
  pointer-events: auto;
}

:root .sas_components-ObjectToolbar-ObjectToolbar_toolbar-container {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

:root .sas_components-ObjectToolbar-ObjectToolbar_toolbar {
  border: 1px solid var(--object-toolbar-bd);
  background-color: var(--object-toolbar-bg);
}

:root .sas_components-ObjectToolbar-ObjectToolbar_button {
  border-width: 0;
  padding: 0 0.25rem;
}

:root .sas_components-ObjectToolbar-ObjectToolbar_button-pane {
  background-color: var(--button-pane-bg);
}
