%combobox-input {
  background: transparent;
  border-width: 0;
  color: inherit;
  font-family: inherit;
  font-size: inherit;
  font-style: inherit;
  font-weight: inherit;
  text-decoration: inherit;

  &::placeholder {
    color: var(--combobox-placeholder-fg);
  }

  &:hover,
  &:focus,
  .disabled & {
    background: transparent;
    color: inherit;
  }

  &:focus {
    outline: none;
  }
}

.active {
  outline: var(--combobox-focus-outline);
  outline-offset: var(--combobox-focus-outline-offset);

  &:not(.disabled) {
    border-color: var(--combobox-active-bd);
  }
}

.combo-input {
  flex-grow: 1;
  min-width: 0;
  font-size: inherit;

  > input {
    line-height: normal;
    // min-height to support custom fonts and font sizes
    min-height: 0;
    width: 100%;
    @extend %combobox-input;
  }
}

.input-box {
  width: 100%;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-inline-end: var(--combobox-spacing-s);
  // min-height to support custom fonts and font sizes
  min-height: var(--combobox-min-block-size);
  text-align: center;
  cursor: pointer;
  border: 1px solid var(--combobox-bd);
  color: var(--combobox-fg);
  background-color: var(--combobox-bg);
  font-family: var(--combobox-fontFamily, inherit);
  font-size: var(--combobox-fontSize, var(--combobox-font-size));
  font-style: var(--combobox-fontStyle, inherit);
  font-weight: var(--combobox-fontWeight, inherit);
  text-decoration: var(--combobox-textDecoration, inherit);
  --icon-default-override-fg: currentColor;

  &:hover:not(:focus-within) {
    border-color: var(--combobox-hover-bd);
    color: var(--combobox-hover-fg);
    background-color: var(--combobox-hover-bg);
  }

  &:focus-within {
    border-color: var(--combobox-active-bd);
    color: var(--combobox-active-fg);
    background-color: var(--combobox-active-bg);
  }

  &.error,
  &.error:hover:not(:focus-within) {
    border-color: var(--combobox-error-bd);
  }

  &.warning,
  &.warning:hover:not(:focus-within) {
    border-color: var(--combobox-warning-bd);
  }

  &.read-only,
  &.read-only:hover {
    background-color: var(--combobox-readonly-bg);
    border-color: var(--combobox-readonly-bd);
    color: var(--combobox-readonly-fg);
  }
}

.combobox-icon {
  padding-inline-start: 0.469rem;
  min-width: 0.875rem;
}

.focus-visible:focus {
  outline: var(--combobox-focus-outline, var(--focus-light-background));
  outline-offset: var(
    --combobox-focus-outline-offset,
    calc(-2px + calc(-1 * var(--focus-border-width)))
  );
}

.disabled {
  opacity: 0.3;
  cursor: default;
}

.drop-down {
  cursor: default;
}
