.heading {
  display: flex;
  align-items: flex-start;
  color: var(--heading-fg);
  font-weight: var(--heading-font-weight-default);
  max-width: 100%;
  font-size: var(--heading-font-size);
  line-height: var(--heading-line-height);
  font-family: var(--heading-font-family);
}
.heading:focus {
  outline: var(--heading-focus);
  outline-offset: calc(-1 * var(--focus-width));
}

.text {
  min-width: 0;
  white-space: pre-wrap;
  overflow-wrap: anywhere;
  word-break: break-word;
}

.nowrap .text {
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: pre;
  overflow-wrap: unset;
  word-break: unset;
}

.icon {
  flex-shrink: 0;
  height: var(--heading-icon-font-size, var(--heading-font-size));
  width: var(--heading-icon-font-size, var(--heading-font-size));
}
.icon:not(.icon-only) {
  margin-block-start: auto;
  margin-block-end: auto;
  margin-inline-start: 0;
  margin-inline-end: var(--heading-icon-inline-size, 0.25rem);
}

