.flow_horizontal {
  display: inline-flex;
  flex-direction: row;
  gap: var(--flow-gap-size);
}

.flow_vertical {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  max-inline-size: 100%;
  gap: var(--flow-gap-size);

  > * {
    max-inline-size: 100%;
  }
}

.wrap {
  flex-wrap: wrap;
  &.flow_vertical {
    display: inline-flex;
    block-size: 100%;
  }
}
