.container_horizontal, .container_vertical {
  position: relative;
  pointer-events: none;
}

.container_vertical {
  grid-column: 2;
  grid-row: 3/3;
  width: 3px;
  justify-self: center;
  height: 100%;
}

.container_horizontal {
  grid-row: 2;
  grid-column: 3/3;
  height: 3px;
  align-self: center;
}

.horizontal, .vertical {
  position: absolute;
  pointer-events: auto;
  display: inline-block;
  background-color: var(--slider-range-bg);
}

.range-enable-focus-outline:focus {
  outline: var(--focus-light-bg);
  outline-offset: calc(-1 * var(--focus-width) -2px);
}

.vertical {
  width: 3px;
  grid-row: 3/5;
  grid-column: 2;
  justify-self: center;
}

.horizontal {
  height: 3px;
  grid-row: 2;
  grid-column: 3/5;
  align-self: center;
}

.show-exclusive {
  border: 1px solid var(--slider-track-bd);
  box-sizing: border-box;
}
.show-exclusive.vertical {
  inset-inline-start: 0.1px;
}
.show-exclusive.horizontal {
  inset-block-start: 0.1px;
}

.disabled {
  background-color: var(--slider-range-disabled-bg);
}

