.offscreen {
  display: block !important;
  inset-inline-start: -10000px !important;
  inset-block-start: 0 !important;
  width: 1px !important;
  height: 1px !important;
  overflow: hidden !important;
  position: absolute;
}

.container_horizontal, .container_vertical {
  position: relative;
  pointer-events: none;
}

.container_vertical {
  grid-column: 2;
  grid-row: 3/3;
}

.container_horizontal {
  grid-row: 2;
  grid-column: 3/3;
}

.containerGrip {
  width: 0;
  height: 0;
}

.arrowgrip, .horizontal, .vertical {
  width: 0.875rem;
  height: 0.875rem;
  box-sizing: content-box;
  position: absolute;
  pointer-events: auto;
  display: inline-flex;
  justify-content: center;
  align-content: center;
  align-items: center;
  border-radius: 100%;
  border: 1px solid var(--slider-grip-bd);
  background-color: var(--slider-grip-bg);
}

.grip-enable-focus-outline {
  position: absolute;
}

.grip-enable-focus-outline:focus::before {
  outline: var(--focus-light-bg);
  content: " ";
  height: 1.1rem;
  position: absolute;
  width: 1rem;
  pointer-events: none;
}

.grip-enable-focus-outline.container_horizontal:focus::before {
  inset-inline-start: -0.53rem;
  inset-block-start: -0.25rem;
}
.grip-enable-focus-outline.container_vertical:focus::before {
  inset-inline-start: -1px;
  inset-block-start: -9px;
}

.vertical {
  margin-block-start: calc(-1 * var(--slider-grip-spacing-s));
}

.horizontal {
  margin-inline-start: calc(-1 * var(--slider-grip-spacing-s));
  inset-block-start: -3px;
}

.indicator {
  width: 0.375rem;
  height: 0.375rem;
  border-radius: 100%;
  background-color: var(--slider-grip-indicator-bg);
}
.disabled .indicator {
  background-color: var(--slider-grip-indicator-disabled-bg);
}

.datepicker, .input {
  position: absolute;
  visibility: visible;
  z-index: 1;
}

.input {
  inset-block-start: -0.25rem;
  inset-inline-start: 0;
  width: 100%;
  min-width: 2rem;
}
.input > input {
  text-align: center;
  font-size: var(--slider-value-grip-font-size-m);
  padding: var(--slider-value-grip-spacing-squish-xs);
  min-height: 1.65rem;
  color: var(--slider-grip-label-fg);
}
.input.input-overflow-right {
  left: auto;
  right: 0;
}
.input.input-overflow-left {
  right: auto;
  left: 0;
}

.datepicker {
  inset-block-start: -0.25rem;
  inset-inline-start: 0;
  max-width: 100%;
  background-color: var(--slider-datepicker-bg);
}

.disabled {
  border-color: var(--slider-grip-disabled-bd);
}

.arrowgrip {
  border-block-end: none;
  border-radius: 3px;
  height: 0.375rem;
  width: 0.78rem;
}
.arrowgrip.vertical {
  transform-origin: calc(0.78rem / 2) 100%;
  transform: rotate(90deg);
  inset-inline-start: 0.1rem;
}
[dir=rtl] .arrowgrip.vertical {
  transform: rotate(-90deg);
}

.arrowgrip::before, .arrowgrip::after {
  border-inline-start: 0.45rem solid transparent;
  border-inline-end: 0.45rem solid transparent;
  border-block-start: 0.5rem solid var(--slider-grip-bd);
  content: " ";
  position: absolute;
}
.arrowgrip::before {
  inset-block-start: 0.3125rem;
}
.arrowgrip::after {
  inset-block-start: 0.25rem;
  border-top-color: var(--slider-grip-bg);
}

.arrow-indicator {
  inset-block-start: 0.125rem;
  border-inline-start: 4px solid transparent;
  border-inline-end: 4px solid transparent;
  border-block-start: 4px solid var(--slider-grip-indicator-bg);
  position: relative;
  z-index: 1;
}
.arrow-indicator.disabled {
  border-top-color: var(--slider-grip-indicator-disabled-bg);
}

