@use 'reactDnd/dndstyles';

.tile {
  &.selected,
  &:hover {
    .checkbox {
      display: inline-block;
    }
  }
}

// Tile bar with Checkbox and State Icons
.bar {
  display: flex;
  z-index: 10;
  align-items: flex-start;
  position: absolute;
  width: 100%;
}

.checkbox {
  display: none;
  margin-block-start: 2px;
  margin-inline-start: 2px;
}

// Thumbnail + Content
.content-container {
  flex-basis: 100%;
  flex-grow: 1;
  overflow: auto;
  position: relative;
  z-index: 5;
}

// Thumbnail Styles
.thumbnail-container {
  overflow: hidden;
  padding-block-end: 56.25%; //creates a 16:9 aspect ratio area for the thumbnail
  position: relative;

  > img {
    object-fit: cover;
    position: absolute;
    height: 100%;
    width: 100%;
  }

  // SVG thumbnails
  > span {
    position: absolute;
    height: 100%;
    width: 100%;

    > svg {
      height: 100%;
      width: 100%;
    }
  }

  &:focus:after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    outline-offset: calc(var(--focus-width) * -1);
    outline: var(--focus-light-bg);
    pointer-events: none;
  }
}

// Tile Content
.content {
  padding: var(--tile-content-pd);
}

.footer {
  display: flex;
  align-items: flex-start;
}

.object-marker {
  background-color: var(--tile-object-marker-bg);
  min-block-size: 0;
}

.object-status {
  margin-block-start: 1px;
  margin-inline-start: 1px;
}

.draggable {
  @extend %draggable;
}

.dragged-item {
  @extend %draggedItem;
}

.drag-preview {
  margin: 0;
  & .checkbox {
    // the checkbox should always be visible in the drag preview
    display: inline-block;
  }
}

// Drop line
@include dndstyles.DropLine($verticalBar: true);

.drop-container::after {
  top: 0;
}

.drag-over {
  @extend %dragZoneBackground;
  @extend %dragZoneOutline;
}

// Override form input spaces
.metadata-table-input {
  input {
    padding: 0;
  }
}
// Override form labels spaces
.metadata-table-form .metadata-table-row {
  > div + div {
    align-self: center;
    padding: 0;
  }
}
