.tile {
  display: flex;
  position: relative;
  flex-direction: column;
  background-color: var(--tile-bg);
  border: 1px solid var(--tile-bd);
  box-shadow: var(--tile-sh);
  box-sizing: border-box;
  margin: var(--tile-spacing-s);
  max-width: 100%;
  z-index: 1;
  overflow: auto;
  border-radius: var(--tile-border-radius);

  &:focus {
    outline: var(--tile-focus);
  }
}

.selected::after {
  content: '';
  outline: solid var(--tile-bd-width) var(--tile-bd);
  outline-offset: calc(-1 * var(--tile-bd-width) + 1px);
  border-radius: var(--tile-outline-radius);
  position: absolute;
  inset: 0;
  z-index: 10;
  pointer-events: none;
}
