.container {
  box-sizing: border-box;
  position: relative;
  margin: 0;
}

.hbar {
  overflow-x: auto;
  overflow-y: hidden;
}

.hbar .body {
  min-height: 1px;
}

.vbar {
  overflow-x: hidden;
  overflow-y: auto;
}

.vbar .body {
  min-width: 1px;
}

