.container {
  width: 100%;
  position: relative;
  box-sizing: border-box;
}

.section {
  display: flex;
  flex-direction: column;
  position: relative;
}

.section-header {
  display: flex;
  align-items: center;
}

.heading-layout {
  flex: 1 1 0;
  min-width: 3rem;
}

.heading {
  font-size: var(--windowshade-font-size-h5);
  font-weight: var(--windowshade-font-weight-default);
}

.header-action {
  min-width: 2rem;
  display: flex;
  justify-content: flex-end;
  flex-basis: auto;
  margin-inline-start: var(--windowshade-spacing-s);
}

.header {
  height: 2.25rem;
  display: flex;
  align-items: center;
  user-select: none;
  cursor: pointer;
  position: relative;
  padding: var(--windowshade-spacing-s) 0;
  box-sizing: border-box;
  overflow: hidden;
}
.header:focus {
  outline: var(--windowshade-header-focus);
  outline-offset: calc(-1 * var(--focus-width) - 2px);
}
.header-wrap {
  height: auto;
  min-height: 2rem;
}

.disabled {
  cursor: default;
}

.header-icon {
  display: flex;
  justify-content: center;
  flex-shrink: 0;
  flex-grow: 0;
  color: var(--windowshade-header-fg);
  position: absolute;
}
.header-icon-open {
  transform: translate3d(0, 0, 0) rotate(90deg);
}
[dir=rtl] .header-icon-open {
  transform: translate3d(0, 0, 0) rotate(-90deg);
}
.header-icon-closed {
  transform: translate3d(0, 0, 0) rotate(0deg);
}
.header-icon-transitioning {
  transition: transform calc(250ms * var(--nova-animation-duration-factor, 1)) cubic-bezier(0, 0, 0.2, 1) 0ms;
}

.header-optional-icon {
  padding-inline-start: var(--windowshade-spacing-m);
  flex-shrink: 0;
}
.header-optional-icon :global(.iconColor) {
  --icon-default-fg: var(--windowshade-header-fg);
}

.indicator {
  display: inline-flex;
  padding-inline-start: var(--windowshade-spacing-xs);
}

.indicator-no-icon {
  padding-inline-start: var(--windowshade-spacing-m);
}

.indicator-multi-state {
  padding-inline-start: var(--windowshade-spacing-xs);
}

.count-indicator {
  display: inline-flex;
  margin-inline-start: var(--windowshade-spacing-xs);
}

.count-indicator-no-icon {
  margin-inline-start: var(--windowshade-spacing-m);
}

.count-indicator-multi-state {
  margin-inline-start: var(--windowshade-spacing-xs);
}

.header-text {
  font-size: var(--windowshade-header-font-size-m);
  min-width: 2rem;
  color: var(--windowshade-header-fg);
  padding-inline-start: var(--windowshade-padding-start);
}

.header-Count {
  flex-shrink: 0;
  font-size: var(--windowshade-header-font-size-m);
  min-width: 2rem;
  color: var(--windowshade-header-fg);
  white-space: nowrap;
  padding-inline-start: var(--windowshade-spacing-xs);
}

.content {
  overflow: auto;
  flex-basis: 100%;
  padding-inline-start: var(--windowshade-spacing-m);
}
.content-transitioning {
  padding-block-end: 0;
  transition: height calc(250ms * var(--nova-animation-duration-factor, 1)) cubic-bezier(0, 0, 0.2, 1) 0ms;
}

.section-container {
  padding-block-start: var(--windowshade-spacing-xs);
  margin-block-end: var(--windowshade-spacing-s);
}

.required-mark {
  color: var(--windowshade-header-asterisk-fg);
  margin-inline-start: var(--windowshade-spacing-xxs);
}

.arrow-cursor .header-icon {
  cursor: default;
}

