.noselect, .inner-container {
  user-select: none;
}

.avatar {
  color: var(--avatar-fg);
  border-color: var(--avatar-bd);
  position: relative;
}
.avatar svg > :global(.iconColor) {
  --icon-default-fg: var(--avatar-fg);
}

.inner-container {
  background-color: var(--avatar-bg);
  align-items: center;
  border-color: var(--avatar-bd);
  border-radius: var(--avatar-border-radius);
  border-style: solid;
  border-width: var(--avatar-bw);
  box-sizing: border-box;
  display: flex;
  height: inherit;
  justify-content: center;
  width: inherit;
}

.has-image {
  border-style: none;
  border: none;
}

.avatar > * {
  font-weight: var(--avatar-font-weight);
  text-transform: var(--avatar-text-transform-uppercase);
}

.avatar-image {
  border-radius: 50%;
  height: 100%;
  object-fit: cover;
  width: 100%;
}
.avatar-image.image-invisible {
  visibility: hidden;
}

.avatar-button {
  cursor: pointer;
}

.avatar-button:hover {
  background-color: var(--avatar-hover-bg);
  color: var(--avatar-hover-fg);
}
.avatar-button:hover svg > :global(.iconColor) {
  --icon-default-fg: var(--avatar-hover-fg);
}

.avatar-button:active,
.avatar-button.active {
  background-color: var(--avatar-active-bg);
  color: var(--avatar-active-fg);
}
.avatar-button:active svg > :global(.iconColor),
.avatar-button.active svg > :global(.iconColor) {
  --icon-default-fg: var(--avatar-active-fg);
}

.small {
  font-size: var(--avatar-font-size-s);
  height: 1.375rem;
  width: 1.375rem;
}

.medium {
  font-size: var(--avatar-font-size-m);
  height: 2rem;
  width: 2rem;
}

.extra-large {
  font-size: 3rem;
  height: 5.5rem;
  width: 5.5rem;
}

.small .avatar-icon {
  width: var(--avatar-font-size-s);
  height: var(--avatar-font-size-s);
}

.medium .avatar-icon {
  width: var(--avatar-font-size-m);
  height: var(--avatar-font-size-m);
}

.extra-large .avatar-icon {
  width: 3rem;
  height: 3rem;
}

.avatar:focus-visible .inner-container::after {
  content: "";
  outline: var(--avatar-focus);
  outline-offset: calc(-1 * var(--focus-width) - 1px);
  position: absolute;
  height: 100%;
  width: 100%;
  z-index: 1;
}

