.formContainer {
  width: 100%;
  height: calc(100% - 0.5rem);
  align-items: end;
}

.formScrollContainer {
  width: 100%;
  height: 100%;
  overflow: auto;
  align-items: center;
  display: block;
}

.formField {
  width: 100%;
  box-sizing: border-box;
  flex-grow: 1;
  flex-direction: column;
  justify-content: center;
}

.formTile {
  > div {
    // check with NC on why thumbnails have a hard-coded 50% padding
    // and cause scrollbars if tile content uses 100% of it's height
    overflow-y: hidden;
  }
}

.formGraph {
  height: 100%;
  width: 100%;
}

.formGraphRelative {
  position: relative;
}
