.stack-button_left,
.stack-button_right, .stack-button_top,
.stack-button_bottom {
  display: flex;
  overflow: hidden;
  max-width: 25rem;
}

.stack-button_top,
.stack-button_bottom {
  white-space: nowrap;
  text-overflow: clip;
  flex-shrink: 0;
}

.stack-button_left,
.stack-button_right {
  height: auto;
  white-space: normal;
  text-overflow: ellipsis;
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
  display: block;
  width: 100%;
}

