.formContainer {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  flex-grow: 1;
  padding-top: var(--geoRoute-spacing-m);
  box-sizing: border-box;
}

.formScrollContainer {
  flex: 1 1 auto;
  min-height: 0px;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
}

.formContent {
  align-items: stretch;
  height: 100%;
  padding: var(--geoRoute-spacing-m);
  box-sizing: border-box;
}

.formIconLarge {
  height: 2rem;
  width: 2rem;
  flex-shrink: 0;
}

.formHeader {
  align-self: center;
  font-weight: bold;
  text-align: center;
  flex-grow: 1;
}

.formField {
  width: 100%;
}

.formFieldIndent {
  padding-left: var(--geoRoute-spacing-m);
  width: calc(100% - var(--geoRoute-spacing-m));
}

.formFieldCentered {
  align-items: center;
}

.formTable div[role='rowgroup'] {
  overflow-y: hidden;
}

.formRowContainer {
  overflow: hidden;
}

.formGrabberIcon {
  height: auto;
  cursor: move;
}

.formDashIcon {
  position: absolute;
  inset-inline-start: 0.55rem;
  z-index: 1;

  // These numbers add up to 14px, the height of an icon
  &:last-child {
    bottom: -6px;
  }
  &:first-child {
    top: -8px;
  }
}

.formRouteIcon {
  display: flex;
  place-content: center;
  align-items: center;
}

.formRouteIconLabel {
  position: absolute;
  font-weight: bold;
  text-transform: uppercase;
}

.formRouteDistance {
  font-weight: bold;
  color: var(--geoRoute-distance-color);
}

.locationHeader {
  width: 100%;
  padding: var(--geoRoute-spacing-xs) 0;
}

.footer {
  display: flex;
  box-sizing: border-box;
  justify-content: end;
  padding: var(--geoRoute-spacing-m);
}

.footerButton {
  min-width: 0;
}
