@use 'utils/offscreen';

.container {
  display: inline-block;
  max-width: var(--dropdown-max-width);
  visibility: var(--dropdown-visibility);
  min-width: 6rem;
  line-height: normal;
}

.dropdown-popover {
  white-space: nowrap;
}

.dropdown-popover-item {
  cursor: default;
}

.message {
  padding: var(--dropdown-spacing-squish-s);
  font-size: var(--dropdown-font-size-m);
  white-space: normal;
  overflow-wrap: anywhere;
  word-break: break-word;
  background-color: var(--dropdown-message-bg);
  border: var(--dropdown-message-bd);
  color: var(--dropdown-message-fg);
}

.offscreen {
  @extend %offscreen;
}
