.vertical {
  display: flex;
  height: min-content;
  width: min-content;

  &:focus > .vertical-button {
    outline: var(--button-focus-outline);
    outline-offset: var(--button-focus-outline-offset);
  }
}

.vertical-button {
  display: flex;
  justify-content: center;
  padding: var(--breadcrumb-squish-s);
}

.vertical-field {
  user-select: none;
  gap: var(--breadcrumb-mg-xs);
  font-size: var(--breadcrumb-font-size-m);
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  padding: var(--breadcrumb-squish-s);
  border: 1px solid transparent;
  display: flex;
  align-items: center;
  color: var(--vertical-field-fg);
  background-color: var(--vertical-field-bg);
  font-family: var(--breadcrumb-font-family);
}
.vertical-field-icon {
  :global(.iconColor) {
    --icon-default-override-fg: var(--vertical-field-fg);
  }
}
.default-cursor {
  cursor: default !important;
}
