// Hover and active states should not apply to pressed or disabled states
.pressed,
.toggle.disabled {
  &,
  &:hover,
  &:active {
    color: var(--button-fg);
    background-color: var(--button-bg);
    border-color: var(--button-bd);

    // icon color
    & :global(.iconColor) {
      --icon-default-fg: var(--button-fg);
    }
    & :global(.alert) {
      --icon-alert-fg: var(--button-fg);
    }
    & :global(.warning) {
      --icon-warning-fg: var(--button-fg);
    }
    & :global(.information) {
      --icon-information-fg: var(--button-fg);
    }
    & :global(.success) {
      --icon-success-fg: var(--button-fg);
    }
    & :global(.neutral) {
      --icon-neutral-fg: var(--button-fg);
    }
  }
}

.toggle:active:not(.disabled) {
  & :global(.iconColor) {
    --icon-default-override-fg: var(--button-active-fg);
  }
  & :global(.alert) {
    --icon-alert-override-fg: var(--button-active-fg);
  }
  & :global(.warning) {
    --icon-warning-override-fg: var(--button-active-fg);
  }
  & :global(.information) {
    --icon-information-override-fg: var(--button-active-fg);
  }
  & :global(.success) {
    --icon-success-override-fg: var(--button-active-fg);
  }
  & :global(.neutral) {
    --icon-neutral-override-fg: var(--button-active-fg);
  }
}

.pressed {
  & :global(.iconColor) {
    --icon-default-override-fg: var(--button-fg);
  }
  & :global(.alert) {
    --icon-alert-override-fg: var(--button-fg);
  }
  & :global(.warning) {
    --icon-warning-override-fg: var(--button-fg);
  }
  & :global(.information) {
    --icon-information-override-fg: var(--button-fg);
  }
  & :global(.success) {
    --icon-success-override-fg: var(--button-fg);
  }
  & :global(.neutral) {
    --icon-neutral-override-fg: var(--button-fg);
  }
}
