.scale {
  background-size: contain;
  background-repeat: no-repeat;
  width: 100%;
  height: 100%;
}

.fit {
  width: 100%;
  height: 100%;
}

.fit-height {
  height: 100%;
}

.fit-width {
  width: 100%;
}

.tile {
  background-size: auto;
  background-repeat: repeat;
  width: 100%;
  height: 100%;
}

.span {
  display: inline-block;
}

.img {
  border: none;
}

.img:focus,
.span:focus {
  outline: var(--focus-light-bg);
  outline-offset: calc(-2px + (-1 * var(--focus-width)));
}

.hidden-img {
  display: none;
}

.clickable {
  cursor: pointer;
}

