.disabled {
  cursor: default;
}

.outer-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 14rem;
  max-width: 100%;
}
.mask-box {
  width: 100%;
  display: flex;
  justify-content: center;
  margin-block-end: var(--focus-preview-spacing-xs);
}
.mask-label {
  padding: var(--focus-preview-spacing-m) 0;
}
