// Why embedded.css?
// Applications should use global.css. embedded.scss is needed for libraries that are published externally because it
// is against certain fonts licensing agreements to include them there. Extra hardening against libraries like
// bootstrap may also  be included in this file.  "embedded" could be interpreted as "can be embedded in third party
// websites". Embedded solutions should use something like postcss to convert :root into the root selector of the
// embedded component or rely on shadow Dom. For example, va-sdk converts ":root" to ".va-sdk".
@use './style/_reset.scss';
@use './style/_scrollbar.scss';
@use './style/_selection.scss';
@use './style/utils/_keyframes.scss';

:root {
  font-family: 'AvenirNext', Arial, Helvetica, Verdana, 'Open Sans', sans-serif;
}
