.container {
  display: flex;
}

.separator {
  box-sizing: border-box;
  min-width: 0.5rem;
  padding: 0.125rem;
  white-space: pre;
  display: inline-flex;
  justify-content: center;
  user-select: none;
  align-items: flex-end;
  height: 1.5rem;
}
.separator:not(.frCaLocale) {
  max-width: 0.7rem;
}

.milliseconds,
.hours,
.minutes {
  min-width: 3.5rem;
  width: 3.5rem;
}

