.dialog {
  display: flex;
  flex-direction: column;
  margin: auto;
  position: absolute;
  justify-content: center;
  align-items: center;
  min-width: 24rem;
  min-height: 10rem;
}
.dialog.with-sub-content-footer {
  min-height: 12rem;
}

.dialog-inner {
  height: 100%;
  inline-size: 100%;
  display: flex;
  flex-grow: 1;
  flex-basis: 100%;
  flex-direction: column;
  color: var(--dialog-content-fg);
  background-color: var(--dialog-content-bg);
  border: var(--dialog-content-bw) solid var(--dialog-content-bd);
  border-radius: var(--dialog-content-border-radius);
  box-shadow: var(--dialog-content-sh);
  box-sizing: border-box;
  position: relative;
  max-height: 100%;
  min-height: 100%;
}

.dialog-header {
  flex-shrink: 0;
  color: var(--dialog-header-fg);
  background-color: var(--dialog-header-bg);
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-inline: var(--dialog-spacing-xl);
  padding-block-start: var(--dialog-header-space-top);
  padding-block-end: var(--dialog-header-space-bottom);
  border-top-left-radius: var(--dialog-content-border-radius);
  border-top-right-radius: var(--dialog-content-border-radius);
}

.dialog-title {
  font-size: var(--dialog-header-font-size);
  font-weight: var(--dialog-header-font-weight);
  color: var(--dialog-header-fg);
  line-height: var(--dialog-line-height);
  overflow: hidden;
  text-align: center;
}
.dialog-title .accent-icon {
  margin-inline-end: var(--dialog-spacing-s);
  width: var(--dialog-header-icon-size);
  height: var(--dialog-header-icon-size);
}

.dialog-content {
  flex-grow: 1;
  flex-basis: 100%;
  margin: var(--dialog-content-pd);
  max-width: 100%;
  overflow: auto;
}

.dialog-footer {
  flex-shrink: 0;
  padding: logical var(--dialog-spacing-none) var(--dialog-content-start-pd, var(--dialog-content-pd)) var(--dialog-spacing-m) var(--dialog-spacing-m);
  display: flex;
  justify-content: flex-end;
  gap: var(--dialog-spacing-s);
  align-items: center;
  width: var(--dialog-footer-width);
  box-sizing: border-box;
}

.dialog-sub-footer {
  padding: logical var(--dialog-spacing-none) var(--dialog-content-pd) var(--dialog-spacing-s) var(--dialog-spacing-m);
}

.dialog-footer-controls {
  flex-grow: 1;
}

.dialog-footer-buttons {
  justify-content: flex-end;
  flex-grow: var(--dialog-footer-stretchToFit);
}

.close-icon-wrapper {
  position: absolute;
  inset-inline-end: 0;
  inset-block-start: 0;
}
.close-icon-wrapper .close-icon {
  cursor: pointer;
}
:root[dir=ltr] .close-icon-wrapper .close-icon {
  border-top-right-radius: var(--dialog-content-border-radius);
}
:root[dir=rtl] .close-icon-wrapper .close-icon {
  border-top-left-radius: var(--dialog-content-border-radius);
}

.drag-cursor {
  cursor: move;
}

.no-drag-cursor {
  cursor: default;
}

.drag-icon {
  cursor: move;
  visibility: hidden;
}

.drag-icon-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 1rem;
  height: 1rem;
  position: absolute;
  inset-block-start: 0;
  inset-inline-start: 0;
  pointer-events: none;
}
.drag-icon-wrapper:focus {
  outline: var(--dialog-header-drag-icon-focus);
  outline-offset: calc(-1 * var(--focus-width) - 2px);
}
.drag-icon-wrapper:focus .drag-icon {
  visibility: visible;
}

.accent:before {
  content: "";
  width: 100%;
  height: 0.125rem;
  position: absolute;
  inset-block-start: -1px;
  inset-inline-start: 0;
  z-index: 1;
  background-color: var(--dialog-header-accent);
  border-top-left-radius: var(--dialog-content-border-radius);
  border-top-right-radius: var(--dialog-content-border-radius);
}

.header-image {
  margin-block-start: calc(-1 * var(--dialog-spacing-xl));
}

.header-logo {
  position: absolute;
  inset-inline-start: 1rem;
  inset-block-start: 1rem;
}

.busy-state-indicator-container {
  display: contents;
}

