@use 'utils/animation';

$resize-icon-width: 24px;
$cell-selection-border: var(--focus-width) solid var(--table-context-area-outline-color);

%container {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}

.table {
  background: var(--table-bg);
  position: relative;

  &.disabled,
  &.read-only {
    .grid-cell-content {
      cursor: default;
    }
  }
}

.outer-container {
  background-color: transparent;
  border: 1px solid var(--table-bd);
  box-sizing: border-box;
}

.hbar {
  flex: 0 0 auto;
  height: var(--table-scroll-bar-space);

  &.has-border {
    border-inline-start: var(--table-cell-border);
  }

  &.has-frozen-column {
    border-inline-start: var(--table-frozen-column-divider-width) solid
      var(--table-frozen-column-divider-color);
  }
}

.row-group {
  position: relative;
  overflow-x: hidden;
  backface-visibility: hidden;
}

.row {
  display: flex;
  flex-direction: row;
}

.header-section {
  display: flex;
}

.header-container {
  position: relative;
  overflow: hidden;
  flex: 1 0 2.5rem;
}

.header-action-button {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--table-header-bg, var(--table-bg));
  isolation: isolate;

  .disabled & {
    background-color: var(--table-disabled-bg);
  }

  > span {
    cursor: pointer;
  }
}

.header-action-button.fauxcus.showFocus:after {
  content: '';
  position: absolute;
  inset-block-start: 0;
  inset-inline-start: 0;
  inset-block-end: 0;
  inset-inline-end: 0;
  pointer-events: none;
  outline: var(--table-focus-light-bg-color) var(--table-focus-style) var(--table-focus-width);
  outline-offset: calc(var(--table-focus-width) * -1);
  z-index: 1;
}

.header-row.grid-cell {
  @extend %container;
  font-weight: var(--table-font-weight-thick);
  height: 100%;
  background: var(--table-bg);
  border-block-end: 1px solid var(--table-header-bottom-bd);
  border-inline-end-color: var(--table-header-right-bd);
  border-block-start: none;

  .selection-cell,
  .grid-cell-content-container {
    background-color: var(--table-header-bg, transparent);
  }

  &.last-frozen-column {
    border-inline-end-color: var(--table-frozen-column-divider-color);
  }

  .grid-cell-content {
    display: flex;
    align-items: center;
    max-height: 100%;

    .heading {
      flex-grow: 1;
      font-weight: var(--table-font-weight-thick);
      min-width: 1rem;
    }
  }

  .disabled & .grid-cell-content-container {
    background-color: var(--table-disabled-bg);
  }
}

.header-accessory-container {
  display: flex;
  align-items: center;
  flex-shrink: 0;
}

.header-accessory {
  height: 100%;

  &-icon {
    margin-inline-end: var(--table-spacing-xs);
  }

  &-helpPopup {
    margin-inline-end: var(--table-spacing-xs);
    margin-inline-start: var(--table-spacing-xs);
  }

  &-sort {
    margin-inline-start: var(--table-spacing-xxs);
    flex-shrink: 0;
  }
}

.body-container {
  overflow-x: hidden;
  overflow-y: auto;
  min-height: 1.75rem;
  flex: 1 1 1.75rem;
}

.empty-row {
  .grid-cell-content {
    display: none;
  }

  .selection-cell {
    display: none;
  }
}

.ghost {
  cursor: default;
  box-sizing: border-box;
  padding: 0;
  display: flex;
}

.ghost-bottom {
  background-color: var(--table-dnd-dragged-bg);
  height: 100%;
  width: 100%;
  border: 1px solid var(--table-dnd-bd); // Needs to override the border: none
  position: absolute;
  left: 1px;
  top: 1px;
}

.ghost-top {
  display: flex;
  flex-direction: row;
  background: var(--table-dnd-dragged-bg);
  border: 1px solid var(--table-dnd-bd); // Needs to override the border: none
  height: 100%;
  width: 100%;
  padding: var(--table-dnd-spacing-xxs) 0;
  overflow: hidden;
  box-sizing: border-box;
  position: relative;
}

.ghost-top .grid-cell {
  border-top: none;
}

.drop-indicator {
  background: var(--table-dnd-indicator-line-bg);
  inset-block-start: auto;
  margin-block-start: 0.35rem;
  height: 2px;
  position: absolute;
  display: flex;
  z-index: 100;
  margin-block-end: 0.35rem;
}

.drop-indicator:before {
  content: '';
  position: absolute;
  width: 0.4rem;
  height: 0.4rem;
  border-radius: 0.25rem;
  border: 2px solid var(--table-dnd-indicator-loop);
  background: var(--table-dnd-indicator-line-bg);
  box-shadow: 0 0 4px 0 var(--table-dnd-indicator-line-bg);
  inset-inline-start: -1px;
  inset-block-start: -5px;
  pointer-events: none;
}

.row-count {
  position: absolute;
  inset-block-start: -0.35rem;
  inset-inline-end: -0.45rem;

  //NOTE: Use div here to make Badge round because we don't have direct contorl on div.square
  div {
    border-radius: 50%;
  }
}

.grid-cell-no-selection {
  user-select: none;
}

.grid-cell-content-container {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: logical 0 var(--table-cell-horiz-padding, 1rem) 0 1rem;
  min-width: 0;
  font-size: var(--table-font-size-m);

  > .grid-cell-content {
    min-width: 0;
    width: 100%;
    overflow: hidden;
    // Temporarily set line-height back to normal for grid cells.
    line-height: normal;
  }

  // Logical properties should *NOT* be used here as the
  // direction is handled by a hook in the component
  .grid-cell.direction_ltr:last-child & {
    padding-right: calc(var(--table-last-column-pd-adjustment, 0px) + 1rem);
  }

  // Logical properties should *NOT* be used here as the
  // direction is handled by a hook in the component
  .grid-cell.direction_rtl:last-child & {
    padding-left: calc(var(--table-last-column-pd-adjustment, 0px) + 1rem);
  }
}

// Grid cells need an opaque background for sticky functionality on headers and frozon columns.
.grid-cell {
  @extend %container;
  flex-shrink: 0;
  background: var(--table-bg);
  border-top: var(--table-cell-border);
  border-inline-end: var(--table-cell-border);
  position: relative;
  z-index: 0;

  .selection-cell,
  .grid-cell-content-container {
    background-color: var(--table-row-bg, transparent);
    transition: animation.duration(var(--table-anim-dur)) var(--table-anim-tm-enter);
    transition-property: background-color;
  }

  &:last-child {
    border-inline-end: none;
  }

  .disabled & .grid-cell-content-container {
    background-color: var(--table-disabled-bg);
  }
}

.drop-region {
  background: var(--table-drop-zone-bg);
  border: 1px solid var(--table-drop-zone-bd);
}

.drop-region .grid-cell {
  background: inherit;
}

.disabled-row .grid-cell .grid-cell-content-container {
  background-color: var(--table-disabled-bg);
}

.row-separator.grid-cell {
  border-top: 1px solid var(--table-row-bd);
}

// Prevents double border between first header and first row.
.row:first-child .grid-cell {
  border-top: none;
}

.selection-cell {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

// Alternate row shading
.alternate-row-shading .body-row:nth-child(odd) {
  .grid-cell-content-container,
  .selection-cell,
  &.empty-row .grid-cell {
    background-color: var(--table-alternate-row-bg);
  }
}

// State styles
.table:not(.disabled):not(.hide-state-styles) {
  // Hover State Styling
  // NOTE: Hover state styling should be visible when `.active` is applied even if selection mode is none
  &.activate,
  &:not(.selection-mode-none):not(.readOnly):not(.hide-hover-state-styles) {
    .body-row:not(.disabled-row):not(.read-only-row):hover {
      .grid-cell-content-container,
      .selection-cell {
        background-color: var(--table-row-hover-bg, var(--table-hover-bg));
        transition: animation.duration(var(--table-anim-dur)) var(--table-anim-tm-exit);
        transition-property: background-color;
      }
    }
  }

  // NOTE: Hover state cursor for list item/row with default action or is selectable should be a hand
  &.activate,
  &:not(.selection-mode-none):not(.readOnly):not(.selectable-only-by-selection-control) {
    .body-row:not(.disabled-row):not(.read-only-row):hover {
      .grid-cell-content-container,
      .selection-cell {
        cursor: pointer;
      }
    }
  }

  // Pressed State Styling
  // NOTE: Pressed state styling should be visible when `.active` is applied even if selection mode is none
  &.activate,
  &.double-click,
  &:not(.selection-mode-none):not(.readOnly):not(.hide-pressed-state-styles) {
    .body-row:not(.disabled-row):not(.read-only-row) .pressed {
      &.grid-cell-content-container,
      &.selection-cell {
        background-color: var(--table-row-pressed-bg, var(--table-pressed-bg));
        filter: var(--table-pressed-filter);
      }
    }
  }

  .body-row:not(.disabled-row):not(.read-only-row) {
    .grid-cell.activate {
      .grid-cell-content-container.pressed {
        background-color: var(--table-row-pressed-bg, var(--table-pressed-bg));
        filter: var(--table-pressed-filter);
      }
    }
  }

  // Selected/Selected Inactive
  &:not(.selection-mode-none):not(.hide-selected-state-styles) {
    --cell-selection-border-color: var(--table-selection-border-color);

    &.selection-border-solid-no-bg {
      --cell-selection-border-color: var(--table-selected-border-highlight-bd);
    }

    // Optional selection border styling
    &.selection-border-dashed,
    &.selection-border-solid {
      .body-container .grid-cell.selected {
        &.cell-selection-border-above.cell-selection-border-below {
          --table-focus-width: var(--table-selection-border-focus);
        }

        &.cell-selection-border-above,
        &.cell-selection-border-below,
        &.cell-selection-border-prev,
        &.cell-selection-border-next {
          &::before {
            box-sizing: border-box;
            content: '';
            position: absolute;
            pointer-events: none;
            z-index: 1;
            inset-block-start: 0;
            inset-inline-start: 0;
            inset-block-end: 0;
            // this offset allows the dashed border to connect seamlessly across cells
            inset-inline-end: -2px;
          }
        }

        &.cell-selection-border-above::before {
          border-top: var(--table-selection-border-width) var(--table-selection-border-style)
            var(--cell-selection-border-color);
        }

        &.cell-selection-border-below::before {
          border-bottom: var(--table-selection-border-width) var(--table-selection-border-style)
            var(--cell-selection-border-color);
        }

        &.cell-selection-border-prev::before {
          border-inline-start: var(--table-selection-border-width)
            var(--table-selection-border-style) var(--cell-selection-border-color);
        }

        &.cell-selection-border-next::before {
          border-inline-end: var(--table-selection-border-width) var(--table-selection-border-style)
            var(--cell-selection-border-color);
        }

        &.cell-selection-border-next:last-child::before {
          inset-inline-end: var(--table-last-column-pd-adjustment);
        }
      }
    }

    // Selected Inactive
    &:not(.selection-border-solid-no-bg)
      .body-row:not(.disabled-row, .read-only-row)
      .grid-cell
      .selected {
      &.grid-cell-content-container,
      &.selection-cell {
        background-color: var(--table-row-selected-inactive-bg, var(--table-selected-inactive-bg));
        filter: var(--table-selected-inactive-filter);
      }

      // Needed for support of legacy tokens
      > .grid-cell-content {
        filter: var(--table-selected-internal-filter);
      }
    }

    // Selected
    &:focus-within:not(.selection-border-solid-no-bg)
      .body-row:not(.disabled-row, .read-only-row)
      .grid-cell
      .selected {
      &.grid-cell-content-container,
      &.selection-cell {
        background-color: var(--table-row-selected-bg, var(--table-selected-bg));
        filter: var(--table-selected-filter);
      }

      // Needed for support of legacy tokens
      > .grid-cell-content {
        filter: var(--table-selected-internal-filter);
      }
    }
  }
}

//This if for 'single' and 'desktop' selection mode
.selection-accent .body-row:not(.disabled-row) .grid-cell:first-child .selected:after {
  content: '';
  display: block;
  height: 100%;
  width: var(--table-selected-inline-start-bw);
  position: absolute;
  inset-block-start: 0;
  inset-inline-start: 0;
  background: var(--table-selected-accent-bg);
}

.frozen {
  position: sticky;
  z-index: 10;
}

.frozen-resizing {
  z-index: 15;
}

.non-frozen-resizing {
  z-index: 5;
}

.last-frozen-column {
  border-inline-end: var(--table-frozen-column-divider-width) solid
    var(--table-frozen-column-divider-color);
}

// Allow Table to inherit focus styling from the global focus context
// since table does not use the conventional focus behavior
.grid-wrapper {
  --table-focus-width: var(--focus-width);
  --table-focus-light-bg-color: var(--focus-light-bg-color);
  --table-focus-style: var(--focus-style);
}

.table.showFocus:focus {
  .fauxcus:not(.frozen) {
    position: relative;
  }

  .fauxcus:after {
    content: '';
    position: absolute;
    inset-block-start: 0;
    inset-inline-start: 0;
    inset-block-end: 0;
    inset-inline-end: 0;
    pointer-events: none;
    outline: var(--table-focus-light-bg-color) var(--table-focus-style) var(--table-focus-width);
    outline-offset: calc(var(--table-focus-width) * -1);
    z-index: 1;
  }

  // Logical properties should *NOT* be used here as the
  // direction is handled by a hook in the component
  .grid-cell.direction_ltr.fauxcus:last-child:after {
    right: var(--table-last-column-pd-adjustment, 0px);
  }

  // Logical properties should *NOT* be used here as the
  // direction is handled by a hook in the component
  .grid-cell.direction_rtl.fauxcus:last-child:after {
    left: var(--table-last-column-pd-adjustment, 0px);
  }
}

// Adds the correct color for the focus border of selected cells when using
// the hight-contrast theme. Lists using certain selection modes do not
// show selection state styles. The header-row background color does not change.
// The focus border will need to stay the same for Select All.
.table:not(.hide-selected-state-styles, .hide-state-styles, .selection-border-solid-no-bg) {
  .selected:not(.header-row).fauxcus:after {
    outline-color: var(--table-focus-selected-bg-color);
  }
}

.resizeIcon {
  position: absolute;
  height: 100%;
  width: $resize-icon-width;
  inset-inline-end: -($resize-icon-width / 2);
  z-index: 10;
}

.resizeIcon:hover {
  cursor: col-resize;
}

.context-area {
  position: relative;

  &:after {
    content: '';
    position: absolute;
    inset-block-start: 0;
    inset-inline-start: 0;
    inset-block-end: 0;
    inset-inline-end: 0;
    outline: var(--table-context-area-outline);
    outline-offset: var(--table-context-area-outline-offset);

    // Logical properties should *NOT* be used here as the
    // direction is handled by a hook in the component
    .grid-cell.direction_ltr:last-child & {
      right: var(--table-last-column-pd-adjustment, 0px);
    }

    // Logical properties should *NOT* be used here as the
    // direction is handled by a hook in the component
    .grid-cell.direction_rtl:last-child & {
      left: var(--table-last-column-pd-adjustment, 0px);
    }
  }
}

.resize-column-in-progress-icon {
  position: absolute;
  inset-inline-end: -1px;
  height: 100%;
  width: 1px;
  background: var(--table-fg);

  &:before,
  &:after {
    content: '';
    position: absolute;
    border-top: solid transparent 4px;
    border-bottom: solid transparent 4px;
    inset-block-start: 50%;
    transform: translateY(-50%);
    z-index: 10;
  }

  &:before {
    border-inline-end: solid var(--table-fg) 5px;
    inset-inline-end: 2px;
  }

  &:after {
    border-inline-start: solid var(--table-fg) 5px;
    inset-inline-start: calc(100% + 1px);

    .last-frozen-column & {
      inset-inline-start: calc(100% + var(--table-frozen-column-divider-width, 1px));
    }
  }
}

.cell-selection-border {
  &::before {
    border-block-start: $cell-selection-border;
    border-block-end: $cell-selection-border;
    box-sizing: border-box;
    content: '';
    position: absolute;
    inset-block-start: 0;
    inset-block-end: 0;
    inset-inline-end: 0;
    inset-inline-start: 0;
    pointer-events: none;
    z-index: 1;
  }

  // Logical properties should *NOT* be used here as the
  // direction is handled by a hook in the component
  .grid-cell.direction_ltr:first-child &::before {
    border-left: $cell-selection-border;
  }

  // Logical properties should *NOT* be used here as the
  // direction is handled by a hook in the component
  .grid-cell.direction_rtl:first-child &::before {
    border-right: $cell-selection-border;
  }

  // Logical properties should *NOT* be used here as the
  // direction is handled by a hook in the component
  .grid-cell.direction_ltr:last-child &::before {
    border-right: $cell-selection-border;
    right: var(--table-last-column-pd-adjustment, 0px);
  }

  // Logical properties should *NOT* be used here as the
  // direction is handled by a hook in the component
  .grid-cell.direction_rtl:last-child &::before {
    border-left: $cell-selection-border;
    left: var(--table-last-column-pd-adjustment, 0px);
  }
}

.group-header {
  @extend %container;
  text-transform: uppercase;
  font-weight: bold;
  white-space: nowrap;
}

.grid-cell-content-focuslock {
  display: contents;
}
