@use 'utils/animation';

.object-marker {
  display: inline-flex;
  max-width: 100%;
  align-items: center;
  min-height: 1.875rem;
  &:focus {
    outline: var(--focus-light-bg);
    outline-offset: calc(-1 * var(--focus-width));
  }
}
.object-marker-link {
  cursor: pointer;
}

.disabled {
  cursor: default;
}

.icon {
  flex-shrink: 0;
  padding: var(--object-marker-spacing-xxs);
}

.animatedInProgress {
  z-index: 1;

  :global {
    animation-name: nova-objectmarker-spin;
    animation-duration: animation.duration(700ms);
    animation-timing-function: linear;
    animation-delay: 0s;
    animation-iteration-count: infinite;
    animation-direction: normal;
    animation-fill-mode: none;
    animation-play-state: running;
  }
}
