%scroll-container {
  display: flex;
  min-height: 1.75rem;
}
%scroll-content {
  overflow: hidden;
  scroll-behavior: smooth;
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}
.horizontal {
  width: 100%;
  .scroll-container {
    @extend %scroll-container;
  }
  .scroll-content {
    @extend %scroll-content;
    align-items: center;
  }

  .fade-container {
    flex-grow: 1;
  }

  .scroll-button {
    min-width: 2rem;
    [dir='ltr'] &:first-child {
      border-bottom-right-radius: 0;
      border-top-right-radius: 0;
    }
    [dir='rtl'] &:first-child {
      border-bottom-left-radius: 0;
      border-top-left-radius: 0;
    }
    [dir='ltr'] &:last-child {
      border-bottom-left-radius: 0;
      border-top-left-radius: 0;
    }
    [dir='rtl'] &:last-child {
      border-bottom-right-radius: 0;
      border-top-right-radius: 0;
    }
  }
}
.vertical {
  height: 100%;
  box-sizing: border-box;
  overflow: hidden;
  .scroll-container {
    @extend %scroll-container;
    flex-direction: column;
    height: 100%;
  }
  .scroll-content {
    @extend %scroll-content;
    flex-direction: column;
    block-size: 100%;

    &.show-scroll-bar-x {
      overflow-x: auto;
    }

    &.show-scroll-bar-y {
      overflow-y: auto;
    }
  }
  .fade-container {
    inline-size: 100%;
  }
  .scroll-button {
    &:first-child {
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
    }
    &:first-child {
      border-top-left-radius: 0;
      border-top-right-radius: 0;
    }
  }
}
.fade-container {
  overflow: hidden;
  position: relative;
}
.scroll-button {
  flex-shrink: 0;
}
.show-scroll-bar {
  overflow: auto;
}
