.surface {
  display: flex;
  flex-grow: 1;
  flex-basis: 0;
  flex-direction: column;
  height: 100%;
  line-height: normal;
  overflow: hidden;
  user-select: none;
}
.report-object {
  @extend .surface;
  overflow: auto;
  flex-basis: auto;
}

.report-container-wrapper {
  @extend .surface;
  flex-direction: row;
}
.report-container-inner {
  @extend .surface;
  width: 0;
}
