.container {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
}

.header {
  display: flex;
  align-items: stretch;
  margin: -1px -1px 0 -1px;
}

.search-field-container {
  max-height: calc(1.75rem + 2px);
  width: 0;
  flex-grow: 1;
  margin-left: -1px;
}
.search-field-container:focus-within {
  z-index: 1;
}

.search-field {
  box-sizing: border-box;
  min-width: 0;
  width: 100%;
  height: 100%;
}

.button {
  border-radius: 0;
  padding: 0 0.25rem;
}

.content-container {
  height: 0;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.footer {
  padding: var(--mainSearch-spacing-m);
  flex-shrink: 0;
  max-height: 100%;
  box-sizing: border-box;
}

.footer-toolbar {
  flex-grow: 1;
  flex-flow: wrap;
  overflow: hidden auto;
}

.footer-toolbar-button {
  flex-direction: column;
  font-size: var(--button-font-size-s);
  gap: var(--button-spacing-s);
}

.list {
  height: 0;
  flex-grow: 1;
  overflow: hidden;
}

.group-item-header {
  font-weight: 600;
}

.new-feature-popover-title {
  justify-content: center;
  width: 100%;
  padding-bottom: var(--newFeaturePopoverTitle-spacing-m);
  font-weight: 600;
  color: var(--newFeaturePopoverTitle-fg);
}

.overflow-button {
  margin-top: 0.5rem;
}

