.container {
  height: 100%;
  align-items: normal;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 1.5rem;
}
.header-title {
  font-weight: var(--form-group-navigation-font-weight-thick);
  min-width: 0;
}

.reset-button {
  flex-shrink: 0;
  text-decoration: underline;
}

.content {
  overflow-y: auto;
  overflow-x: hidden;
  flex-grow: 1;
  flex-basis: 100%;
}

