.list {
  block-size: 1.75rem;
}
.list > li {
  block-size: 100%;
  display: flex;
  align-items: center;
}

.item-icon {
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  margin-inline-start: var(--breadcrumb-mg-xs);
}
.item-icon.opening :global {
  animation: nova-global-fadein calc(var(--breadcrumb-anim-dur-enter) * var(--nova-animation-duration-factor, 1)) var(--breadcrumb-anim-tf-enter) forwards;
}

.list-item {
  max-inline-size: 100%;
}

