@use 'reactDnd/dndstyles';

.draggable {
  @extend %draggable;
}

.dragged-item {
  @extend %draggedItem;
}

.drag-image-preview {
  padding: 0 0.25rem; // added for better visual appearance, so we should be good not using spacing tokens
  box-sizing: border-box;
}

// Drop zone
.drag-over {
  @extend %dragZoneBackground;
  @extend %dragZoneOutline;
}

.node-count {
  position: absolute;
  inset-block-start: -0.75rem;
  inset-inline-end: -0.93rem;

  //NOTE: Use div here to make Badge round because we don't have direct contorl on div.square
  div {
    border-radius: 50%;
  }
}
