.hack {
  padding: 0.25rem 0;

  // center the breadcrumb item list
  display: flex;
  justify-content: center;
  & > nav {
    // nova has a width of 100% by default which prevents us from centering it.
    width: auto;
    // prevent the nav from overflowing it's flex container.
    min-width: 0;
  }

  li > a {
    font-size: 1.0625rem !important;
  }
}
