.emptyText {
  width: 100%;
  text-align: center;
  font-size: 0.7912rem;
  line-height: 2.75rem;
  min-height: 2.75rem;
}
.emptyText.vertical {
  text-align: center;
  line-height: 1.3;
  max-width: 75%;
  align-self: center;
}

.header {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  background-color: var(--prompt-header-bg);
}
.header.vertical {
  max-width: 50%;
}
.header.hover {
  background-color: var(--prompt-header-hover-bg);
}

.headerContent {
  overflow: auto;
  display: flex;
  flex-direction: row;
}
.headerContent.vertical {
  flex-direction: column;
  height: 100%;
}
.headerContent.transitioning.vertical {
  transition: opacity 0.5s;
}
.headerContent.transitioning:not(.vertical) {
  overflow: hidden;
  transition: height 0.5s;
}

.buttonContainer {
  width: 100%;
  display: flex;
  justify-content: center;
}

.button {
  position: absolute;
  height: 0.75rem;
  z-index: 1;
}

.paddingVisible {
  padding-top: 0.5rem;
  transition: padding-top 0.2s;
}

.paddingHidden {
  padding-top: 0rem;
  transition: padding-top 0.2s;
}

