%colorchip-size {
  block-size: 1rem;
  inline-size: 1rem;
}
%colorchip-bd {
  border: var(--colorchip-swatch-bd-width) solid var(--colorchip-bd);
}
%colorchip {
  @extend %colorchip-size;
  @extend %colorchip-bd;
  padding: var(--colorchip-padding);
  display: inline-flex;
  flex-shrink: 0;
  cursor: pointer;
  border: 1px solid var(--colorchip-bd);
  &:hover {
    border-color: var(--colorchip-hover-bd);
    %colorchip-inner {
      background-clip: content-box;
    }
  }
}
%colorchip:focus {
  outline: var(--focus-light-bg);
  outline-offset: calc(-1px + calc(-1 * var(--focus-width)));
}
%colorchipborder {
  border: var(--colorchip-swatch-bd-width) solid var(--colorchip-swatch-bd);
}
%colorchip-inner {
  @extend %colorchip-bd;
  block-size: 100%;
  inline-size: 100%;
  border-radius: 1px;
  box-sizing: border-box;
}

.colorchip {
  @extend %colorchip;
}
.colorchip-inner {
  @extend %colorchip-inner;
}

.no-color {
  @extend %colorchip;
  border: var(--colorchip-selected-bd-width) solid var(--colorchip-bd);
  &:hover,
  &.selected {
    padding: var(--colorchip-padding);
  }
}
.no-color-inner {
  @extend %colorchip-inner;
  @extend %colorchipborder;
  background: linear-gradient(
    -45deg,
    transparent 47%,
    var(--colorchip-no-color-mark) 47%,
    var(--colorchip-no-color-mark) 54%,
    transparent 54%
  );
}
.selected {
  @extend %colorchipborder;
  .colorchip-inner {
    background-clip: content-box;
  }
}
.empty-chip {
  @extend %colorchip-size;
  @extend %colorchip-bd;
  padding: var(--colorchip-padding);
  display: inline-flex;
  flex-shrink: 0;
}

.empty-chip-inner {
  @extend %colorchip-inner;
  @extend %colorchipborder;
}

// changed to make hover outline effect

.automatic,
.custom-colorchip {
  @extend %colorchip;
  &:hover {
    .automatic-inner,
    .custom-colorchip-inner {
      background-clip: content-box;
      border-color: var(--colorchip-bd);
    }
  }
}
.automatic-inner,
.custom-colorchip-inner {
  @extend %colorchip-inner;
  border: var(--colorchip-swatch-bd-width) solid var(--colorchip-inner-bd);
}

.visible-inner-border {
  border-color: var(--colorchip-inner-bd);
}
