.stack-link {
  font-size: 0.875rem;
  min-height: 1rem;
  display: inline-flex;
  align-items: center;
  padding: 0.25rem 0.5rem;
  position: relative;
  cursor: var(--stack-link-cursor);
  text-decoration: var(--stack-link-text-decoration);
  color: var(--stack-link-fg);
}
.stack-link:focus {
  outline: var(--focus-light-bg);
  outline-offset: calc(var(--focus-width) * -1);
}

.stack-link-text {
  display: block;
  overflow: hidden;
  width: 100%;
  max-width: 25rem;
  text-align: center;
}

.stack-link-no-wrap {
  white-space: nowrap;
}

