:root .sas_components-Dialog-FullScreenDialog_container {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: var(--full-screen-dialog-bg);
  padding: var(--full-screen-dialog-spacing-xl);
  box-sizing: border-box;
}

:root .sas_components-Dialog-FullScreenDialog_container-inner {
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: auto 1fr;
  grid-row-gap: var(--full-screen-dialog-spacing-m);
  max-height: 100%;
}

:root .sas_components-Dialog-FullScreenDialog_heading {
  align-items: normal;
}
:root .sas_components-Dialog-FullScreenDialog_heading .sas_components-Dialog-FullScreenDialog_header h1 {
  color: var(--full-screen-dialog-title-fg);
  font-size: var(--full-screen-dialog-title-font-size-xl);
  font-weight: var(--full-screen-dialog-title-font-weight);
}

:root .sas_components-Dialog-FullScreenDialog_content-area {
  border: 1px solid var(--full-screen-dialog-content-bd);
  background-color: var(--full-screen-dialog-content-bg);
  padding: var(--full-screen-dialog-spacing-m);
  box-sizing: border-box;
  overflow: auto;
}
