%component-container {
  display: flex;
  position: relative;
  align-items: center;
}

.scroll-container {
  width: 100%;
  height: 100%;
  min-width: 0;
  flex-grow: 1;

  div {
    width: 100%;
    height: 100%;
  }
}

.content-container {
  width: 100%;
  height: 100%;

  .no-scroll & {
    display: flex;
    list-style: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
  }
}

.button-container {
  max-width: max(100%, var(--segmentedbutton-extreme-min-width, 2rem), 2rem);
  flex-grow: 1;

  .button {
    width: 100%;
    height: 100%;
    // center button text on both axis
    align-items: center;
    justify-content: center;
    border-radius: 0; // remove the radius from Button
  }

  .no-scroll .shrinking-text & {
    min-width: 3rem;
  }

  .no-scroll .shrinking-icon & {
    min-width: 2rem;
  }
}

.component-container_vertical {
  @extend %component-container;
  flex-direction: column;

  .button {
    border-bottom-width: 0px;
  }
  .button-container:first-child .button {
    border-start-start-radius: var(--segmentedbutton-border-radius);
    border-start-end-radius: var(--segmentedbutton-border-radius);
  }
  .button-container:last-child .button {
    border-bottom-width: 1px;
    border-end-start-radius: var(--segmentedbutton-border-radius);
    border-end-end-radius: var(--segmentedbutton-border-radius);
  }
  .disabled .button {
    border-top-width: 1px;
    border-top-color: var(--button-normal-bd);
  }
  .selected:not(.disabled) + .button-container .button {
    border-top-color: var(--button-selected-bd);
  }
  .disabled:first-child .button,
  .disabled + .disabled .button {
    border-top-color: var(--button-bd);
  }

  &.no-scroll .content-container {
    flex-direction: column;
  }
}

.component-container_horizontal {
  @extend %component-container;
  flex-direction: row;

  .content-container {
    align-items: stretch;
  }

  .button {
    border-inline-end-width: 0px;
  }

  .button-container:first-child .button {
    border-start-start-radius: var(--segmentedbutton-border-radius);
    border-end-start-radius: var(--segmentedbutton-border-radius);
  }
  .button-container:last-child .button {
    border-inline-end-width: 1px;
    border-start-end-radius: var(--segmentedbutton-border-radius);
    border-end-end-radius: var(--segmentedbutton-border-radius);
  }
  .disabled .button {
    border-inline-start-width: 1px;
    border-inline-start-color: var(--button-normal-bd);
  }
  .selected:not(.disabled) + .button-container .button {
    border-inline-start-color: var(--button-selected-bd);
  }
  .disabled:first-child .button,
  .disabled + .disabled .button {
    border-inline-start-color: var(--button-bd);
  }

  &.equal-width .button-container {
    flex-basis: 0;
  }
}
