.offscreen {
  display: block !important;
  inset-inline-start: -10000px !important;
  inset-block-start: 0 !important;
  width: 1px !important;
  height: 1px !important;
  overflow: hidden !important;
  position: absolute;
}

.drag-over {
  background: var(--dnd-drop-zone-bg);
}

.drag-over {
  outline: 1px solid var(--dnd-drop-zone-bd);
  outline-offset: -1px;
}

.outer-container {
  max-width: 100%;
  background-color: var(--token-input-bg);
  box-sizing: border-box;
  border: 1px solid var(--token-input-bd);
  border-bottom-color: var(--token-input-border-bottom-color);
  border-bottom-width: var(--token-input-border-bottom-width);
  border-bottom-style: solid;
}
.outer-container:focus-within {
  border-color: var(--token-input-active-bd);
  border-bottom-color: var(--token-input-border-bottom-color-active);
}
.outer-container.read-only {
  border-color: transparent;
}

.container {
  width: 100%;
  min-width: 0;
  position: relative;
  display: flex;
  align-items: center;
  min-height: 2.125rem;
}
.container.has-status-icon {
  padding-inline-end: 1.25rem;
}

.input-field-container {
  display: flex;
  flex-grow: 1;
  flex-basis: 100%;
  min-width: 10rem;
}

.input-field {
  width: 100%;
  height: 100%;
}
.input-field > input {
  background: transparent;
}
.input-field > input:hover {
  background: transparent;
}
.input-field > input:focus {
  outline: none;
}

.focus-visible {
  outline: var(--token-input-focus-outline);
  outline-offset: var(--token-input-focus-outline-offset);
}

.input-icon {
  position: absolute;
  height: 1.25rem;
  inset-block-start: var(--token-input-icon-position-top);
  inset-inline-end: 1px;
  display: flex;
  align-items: center;
  margin: 0 var(--token-input-spacing-xs);
  cursor: default;
}

.button {
  width: 2.125rem;
  height: 2.125rem;
}

.tokenizer {
  width: 100%;
}

