.label {
  display: inline-flex;
  max-width: 100%;
  font-size: var(--label-font-size);
  font-weight: var(--label-font-weight);
  color: var(--label-fg);
}

.text-container {
  max-width: 100%;
}

.text {
  cursor: var(--label-text-cursor);
  color: inherit;
  white-space: pre-wrap;
  overflow-wrap: anywhere;
  word-break: break-word;
}

.icon {
  align-self: flex-start;
  flex-shrink: 0;
  margin-inline-end: 0.25rem;
  margin-block-start: 0.125rem;
}

.help {
  min-height: unset;
  vertical-align: top;
  padding: var(--help-icon-spacing-squish-xxs);
}

.nowrap {
  .text-container {
    display: inline-flex;
    min-width: 0;
  }

  .text {
    min-width: 0;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: pre;
    overflow-wrap: unset;
    word-break: unset;
  }
}

.required-mark {
  color: var(--label-asterisk-fg);
  margin-inline-start: 0.125rem;
}

.highlight {
  color: var(--label-highlighted-fg);
  background-color: var(--label-highlighted-bg);
}
