$cell-width: 2.25rem;
$cell-height: 1.5rem;
$cell-row-gap: var(--calendar-cell-row-gap);
$cell-col-gap: var(--calendar-cell-col-gap);

.container {
  display: inline-grid;
  row-gap: var(--calendar-cell-header-row-gap);
}

%auto-column-grid {
  display: grid;
  grid-auto-flow: column;
}

.header {
  @extend %auto-column-grid;
  justify-content: space-between;
  // NOTE: places the header above .calendar even though it comes later in the DOM order
  grid-row: 1 / 1;
}

.header-month-year-container {
  @extend %auto-column-grid;
  column-gap: $cell-col-gap;
}

%header-month-year-button {
  justify-content: center;
  align-items: center;
}

.header-month-button {
  @extend %header-month-year-button;
  width: 7.75rem;
}

.header-year-input {
  @extend %header-month-year-button;
  width: 4.5rem;
}

.calendar {
  // NOTE: this is 'position:relative' because we need to absolutely position the week number separator
  position: relative;
  display: grid;
  // NOTE: places the calendar below .header (which is at 1 / 1)
  grid-row: 2 / 2;
  // NOTE: the number of day-cell rows varies between 4 and 6,
  //     | but we should always have the maximum number of rows.
  // maximum number of rows is 6 + 1 (for the weekday header)
  grid-template-rows: var(--calendar-cell-calendar-row-template);
  justify-content: center;
}

.month-row {
  display: grid;
  grid-template-columns: repeat(7, $cell-width);
  grid-template-rows: $cell-height;
  column-gap: var(--weekday-col-grid-gap);
}

.weekday-header {
  display: grid;
  grid-template-columns: repeat(7, [day-start] $cell-width);
  grid-template-rows: $cell-height;
  column-gap: var(--weekday-col-grid-gap);
}

.with-week-numbers {
  grid-template-columns: $cell-width repeat(7, [day-start] $cell-width);
}

%month-grid-cell {
  display: flex;
  align-items: center;
  justify-content: center;
}

.day-cell {
  @extend %month-grid-cell;
  font-size: var(--calendar-cell-font-size-m);
  border-width: var(--calendar-cell-border-width);
  line-height: var(--calendar-cell-line-height);
  font-weight: var(--calendar-cell-font-weight);
  cursor: default;
  box-sizing: border-box;

  color: var(--day-cell-normal-fg);
  background-color: var(--day-cell-normal-bg);
  padding: var(--day-cell-pd);

  &:hover {
    color: var(--day-cell-hover-fg);
    background-color: var(--day-cell-hover-bg);
  }

  &-focused,
  &-focused:focus {
    outline: var(--focus-light-bg);
    outline-offset: calc(-2px + var(--focus-width) * -1);
  }

  &-today {
    color: var(--day-cell-today-fg);
    background-color: var(--day-cell-today-bg);
    border: 1px solid var(--day-cell-today-bd);
  }

  &-external,
  &-external:hover {
    color: var(--day-cell-external-fg);
    background-color: var(--day-cell-external-bg);
  }

  &-selected,
  &-selected:hover {
    &.non-work-day {
      &.day-cell-interval:not(:hover) {
        color: var(--day-cell-interval-fg);
      }
    }

    &:not(.non-work-day):not(.day-cell-interval),
    &:hover {
      color: var(--day-cell-selected-fg);
    }
    background-color: var(--day-cell-selected-bg);
    border: 1px solid var(--day-cell-selected-bd);
  }

  &-selected:focus {
    outline: var(--day-cell-selected-focus-outline);
  }
  &-interval {
    color: var(--day-cell-interval-fg);
    border-color: var(--day-cell-interval-bd);
    background-color: var(--day-cell-interval-bg);
  }

  &-interval:focus {
    outline: var(--day-cell-interval-focus-outline);
  }
}

.day-cell-disabled.day-cell-disabled {
  &:not(.non-work-day) {
    background-color: transparent;
  }
  &.non-work-day {
    border: 1px solid var(--non-work-day-bd);
  }
  border: none;
  user-select: none;
  text-decoration: line-through;
}

.day-cell-content {
  user-select: none;
  pointer-events: none;
}

.weekday-header-cell {
  @extend %month-grid-cell;
  color: var(--weekday-fg);
  font-size: var(--calendar-cell-font-size-s);
  font-weight: var(--calendar-cell-font-weight);
}

.week-number-cell {
  @extend %month-grid-cell;
  color: var(--week-number-cell-fg);
  font-size: var(--week-number-cell-font-size);
  font-weight: var(--week-number-cell-font-weight);
  padding: var(--week-number-cell-pd);
  position: relative;

  &:after {
    content: '';
    display: block;
    height: 100%;
    width: var(--calendar-divider-bw);
    background-color: var(--calendar-divider-bg);
    position: absolute;
    inset-inline-start: calc((#{$cell-width} - 0.25rem) + (0.5 * #{$cell-row-gap}));
  }
}

.non-work-day {
  @extend %month-grid-cell;
  cursor: default;
  box-sizing: border-box;
  &.day-cell-selected {
    color: var(--day-cell-selected-fg);
  }
  &:not(.day-cell-selected) {
    background-color: var(--non-work-day-bg);
    border: 1px solid var(--non-work-day-bd);
    &:not(.day-cell-disabled) {
      color: var(--non-work-day-fg);
    }
    &:hover {
      &:not(.day-cell-disabled) {
        color: var(--day-cell-hover-fg);
        background-color: var(--day-cell-hover-bg);
      }
    }
  }
}

.special-day {
  @extend %month-grid-cell;
  cursor: default;
  box-sizing: border-box;
  box-shadow: inset 0 -0.125rem 0 var(--special-day-fg);
  &.selected {
    box-shadow: inset 0 -0.125rem 0 var(--day-cell-selected-fg);
  }
  width: 1rem;
}

.legend {
  display: block;
  margin-inline-start: auto;
  margin-inline-end: auto;
  margin-block-start: var(--calendar-legend-legend-gap);
}

.legend-item {
  display: flex;
  margin: 0.25rem 0;
}

.legend-spacer {
  flex-grow: 1;
  display: block;
  width: 5rem;
}

.legend-square {
  height: 1.375rem;
  min-width: 1.875rem;
  white-space: nowrap;
}

.legend-text {
  font-size: var(--calendar-legend-font-size-s);
  color: var(--non-work-day-fg);
  padding-inline-start: 0.5rem;
  padding-block-start: 0.145rem;
  width: 5rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.legend-non-work-day {
  @extend %month-grid-cell;
  font-size: var(--calendar-legend-font-size-s);
  cursor: default;
  box-sizing: border-box;
  background-color: var(--non-work-day-bg);
  border: 1px solid var(--non-work-day-bd);
}

.legend-today {
  @extend %month-grid-cell;
  font-size: var(--calendar-legend-font-size-s);
  cursor: default;
  box-sizing: border-box;
  color: var(--day-cell-today-fg);
  background-color: var(--day-cell-today-bg);
  border: 1px solid var(--day-cell-today-bd);
}

.legend-selected {
  @extend %month-grid-cell;
  font-size: var(--calendar-legend-font-size-s);
  cursor: default;
  box-sizing: border-box;
  color: var(--day-cell-selected-fg);
  background-color: var(--day-cell-selected-bg);
  border: 1px solid var(--day-cell-selected-bd);
}

.legend-disabled {
  @extend %month-grid-cell;
  font-size: var(--calendar-legend-font-size-s);
  cursor: default;
  box-sizing: border-box;
}

.legend-special-day {
  @extend %month-grid-cell;
  font-size: var(--calendar-legend-font-size-s);
  cursor: default;
  box-sizing: border-box;
  box-shadow: inset 0 -0.125rem 0 var(--special-day-fg);
  margin: 0 auto;
  width: 0.875rem;
  white-space: nowrap;
}

.today-button {
  margin: auto;
}

.today-button-footer {
  display: grid;
  grid-row: 3 / 3;
  grid-template-rows: var(--calendar-cell-today-button-row-template);
}
