.text {
  position: relative;
}

.with-text-icon .text {
  margin-inline-start: var(--badge-button-text-spacing-xs);
}

.base-button {
  padding: var(--badge-button-padding);
}

.basic {
  display: flex;
  align-items: center;
  justify-content: center;
}
.basic:not(.square) .text::before {
  content: "(";
  position: relative;
  top: -1px;
}
.basic:not(.square) .text::after {
  content: ")";
  position: relative;
  top: -1px;
}

.square {
  background-color: var(--badge-button-bg);
  color: var(--badge-button-fg);
  border-radius: var(--badge-button-border-radius);
}
.square .text {
  color: var(--badge-button-fg);
  font-size: var(--badge-button-text-font-size-m);
  font-weight: var(--badge-button-text-font-weight-thick);
  padding: 0 var(--badge-button-text-spacing-xs);
}

