// Import SVG styling into the global scope as to avoid PostCSS class generation
.svg {
  display: inline-flex;
  height: auto;
  width: auto;

  &:focus {
    outline: var(--focus-light-bg);
  }
}

.icon {
  height: 0.875rem;
  width: 0.875rem;

  > .inner {
    height: 100%;
    width: 100%;
    overflow: visible;
  }
}

.allowMirroring {
  transform: scale(-1, 1);
  transform-origin: center;
}

.disabled {
  opacity: var(--svg-opacity);
}

svg.inner {
  :global {
    @import 'node_modules/@sas/react-svg/svg.scss';

    // below icon styles added to work overrides in FF
    // TODO: check if these could move these to @sas/react-svg
    :global(.alert) {
      fill: var(--icon-alert-override-fg, var(--icon-alert-fg));
    }
    :global(.warning) {
      fill: var(--icon-warning-override-fg, var(--icon-warning-fg));
    }
    :global(.success) {
      fill: var(--icon-success-override-fg, var(--icon-success-fg));
    }
    :global(.information) {
      fill: var(--icon-information-override-fg, var(--icon-information-fg));
    }
    :global(.neutral) {
      fill: var(--icon-neutral-override-fg, var(--icon-neutral-fg));
    }
    :global(.iconColor) {
      fill: var(--icon-default-override-fg, var(--icon-default-fg));
    }

    // TODO: Move these to @sas/react-svg
    [id^='thumbColor1'] > stop {
      stop-color: var(--thumbnail-color1);
    }
    [id^='thumbColor2'] > stop {
      stop-color: var(--thumbnail-color2);
    }
    [id^='thumbColor3'] > stop {
      stop-color: var(--thumbnail-color3);
    }
    [id^='thumbColor4'] > stop {
      stop-color: var(--thumbnail-color4);
    }
    [id^='thumbColor5'] > stop {
      stop-color: var(--thumbnail-color5);
    }
    [id^='thumbColor6'] > stop {
      stop-color: var(--thumbnail-color6);
    }
    [id^='thumbColor7'] > stop {
      stop-color: var(--thumbnail-color7);
    }
    [id^='thumbColor8'] > stop {
      stop-color: var(--thumbnail-color8);
    }
  }
}
