@use 'utils/animation';

.image-container {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  padding: 0;
  border: 0;
  max-width: 100%;
  margin-block-end: 1rem;
  position: relative;
  &.has-background-color {
    background-color: var(--image-selector-bg);
  }
}

.image {
  height: 100%;
  width: 100%;
  max-width: 100%;
  object-fit: cover;
}

.hint-text {
  font-size: var(--image-selector-font-size-s);
}

.edit-icon {
  opacity: 0;
  transition: opacity animation.duration(var(--image-selector-anim-dur))
    var(--image-selector-anim-tf);
  background: var(--image-selector-edit-icon-bg);
  width: 1.5rem;
  height: 1.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  border: solid 1px var(--image-selector-edit-icon-bd);
}

.image-container:hover .edit-icon,
.image-container:focus .edit-icon {
  opacity: 1;
}

.image-container:focus:after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  pointer-events: none;
  outline: var(--focus-light-bg);
  outline-offset: calc(-1 * var(--focus-width));
}

.overlay {
  width: 100%;
  height: 100%;
  display: flex;
  position: absolute;
  justify-content: center;
  align-items: center;
  &.active,
  &:active {
    background-color: var(--image-selector-overlay);
  }
}
