@use 'utils/animation';

.list {
  block-size: 1.75rem;
  > li {
    block-size: 100%;
    display: flex;
    align-items: center;
  }
}

.item-icon {
  flex-shrink: 0;
  display: flex;
  justify-content: center;
  margin-inline-start: var(--breadcrumb-mg-xs);
  &.opening {
    :global {
      animation: nova-global-fadein animation.duration(var(--breadcrumb-anim-dur-enter))
        var(--breadcrumb-anim-tf-enter) forwards;
    }
  }
}

.list-item {
  max-inline-size: 100%;
}
