@use "utils/checkbox";

.item,
.form-fields {
  width: 100%;
  box-sizing: border-box;
}

.title {
  font-weight: var(--form-font-weight-thick);
  text-transform: var(--form-text-transform-uppercase);
}

.toolbar {
  border-block-end: solid 1px var(--form-toolbar-bd);
  padding-block-end: var(--form-toolbar-pd);
}

.dependency {
  box-sizing: border-box;
  padding-inline-start: checkbox.$indentation;
}
