.base, .busy-single {
  height: 2rem;
  border-radius: 1rem;
  padding-inline-start: var(--objectstatus-padding);
  padding-inline-end: var(--objectstatus-padding);
  border-width: 1px;
  box-sizing: border-box;
  border-style: solid;
  display: inline-flex;
  align-items: center;
  overflow: hidden;
  background-color: var(--objectstatus-bg);
  border-color: var(--objectstatus-bd);
  color: var(--objectstatus-fg);
}
.base:focus, .busy-single:focus {
  outline: var(--objectstatus-focus-outline);
  outline-offset: var(--objectstatus-focus-outline-offset);
}

.text, .busy-single {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  line-height: normal;
}

.text {
  font-size: 0.8125rem;
}

.icon {
  flex-shrink: 0;
}

