.link {
  cursor: pointer;
  font-size: var(--link-font-size-m);
  text-decoration: underline;
  color: var(--link-fg);
}

.link:focus {
  outline: var(--focus-light-bg);
  outline-offset: calc(-1 * var(--focus-width));
}

.small {
  font-size: var(--link-font-size-s);
}

.text {
  color: inherit;
  font-size: inherit;
  overflow-wrap: anywhere;
  word-break: break-word;
}

.nowrap,
.nowrap .text {
  display: block;
  white-space: pre;
  overflow: hidden;
  text-overflow: ellipsis;
  overflow-wrap: unset;
  word-break: unset;
}

.has-max-lines .text {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.disabled {
  cursor: default;
  pointer-events: none;
}

.decoration_hover {
  text-decoration: none;

  &:hover,
  &:focus {
    text-decoration: underline;
  }
}

.decoration_none {
  text-decoration: none;
}
