@font-face {
  font-family: "sas-icons";
  font-weight: normal;
  font-style: normal;
  src: url("~@sas/react-svg/font/sas-icons-regular.ttf") format("truetype");
}
.icon {
  display: inline-flex;
  align-items: center;
  speak: none;
  height: var(--icon-height, 0.875rem);
  width: var(--icon-width, 0.875rem);
  box-sizing: content-box;
}
.icon > svg {
  height: 100%;
  width: 100%;
}
.icon:focus {
  outline: var(--focus-light-bg);
}
.icon :global(.alert) {
  fill: var(--icon-alert-override-fg, var(--icon-alert-fg));
}
.icon :global(.warning) {
  fill: var(--icon-warning-override-fg, var(--icon-warning-fg));
}
.icon :global(.success) {
  fill: var(--icon-success-override-fg, var(--icon-success-fg));
}
.icon :global(.information) {
  fill: var(--icon-information-override-fg, var(--icon-information-fg));
}
.icon :global(.neutral) {
  fill: var(--icon-neutral-override-fg, var(--icon-neutral-fg));
}
.icon :global(.iconColor) {
  fill: var(--icon-default-override-fg, var(--icon-default-fg));
}

