@keyframes :global(nova-global-spin) {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}

@keyframes :global(nova-global-fadein) {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes :global(nova-global-fadeout) {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@keyframes :global(nova-global-slideup-fadein) {
  from {
    opacity: 0;
    transform: translateY(8px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes :global(nova-global-slidedown-fadeout) {
  from {
    opacity: 1;
    transform: translateY(0);
  }
  to {
    opacity: 0;
    transform: translateY(8px);
  }
}

@keyframes :global(nova-global-slideleft-fadein) {
  from {
    opacity: 0;
    transform: translateX(16px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes :global(nova-global-slideleft-fadeout) {
  from {
    opacity: 1;
    transform: translateX(0);
  }
  to {
    opacity: 0;
    transform: translateX(-16px);
  }
}

@keyframes :global(nova-global-slidein-messagetoast) {
  from {
    opacity: 0;
    transform: scale(0);
  }

  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes :global(nova-global-slideout-messagetoast) {
  from {
    transform: scale(1);
  }

  to {
    transform: scale(0);
  }
}

@keyframes :global(nova-global-popin) {
  from {
    opacity: 0;
    transform: scale(0.5);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes :global(nova-global-popout) {
  from {
    opacity: 1;
    transform: scale(1);
  }
  to {
    opacity: 0;
    transform: scale(0.5);
  }
}

@keyframes :global(nova-global-slideinstart) {
  from {
    opacity: 0;
    transform: translateX(-100px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes :global(nova-global-slideoutstart) {
  from {
    opacity: 1;
    transform: translateX(0);
  }

  to {
    opacity: 0;
    transform: translateX(-100px);
  }
}

@keyframes :global(nova-global-slideinend) {
  from {
    opacity: 0;
    transform: translateX(100px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes :global(nova-global-slideoutend) {
  from {
    opacity: 1;
    transform: translateX(0);
  }

  to {
    opacity: 0;
    transform: translateX(100px);
  }
}

@keyframes :global(nova-global-slideintop) {
  from {
    opacity: 0;
    transform: translateY(-100px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes :global(nova-global-slideouttop) {
  from {
    opacity: 1;
    transform: translateY(0);
  }

  to {
    opacity: 0;
    transform: translateY(-100px);
  }
}

@keyframes :global(nova-global-slideinbottom) {
  from {
    opacity: 0;
    transform: translateY(100px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes :global(nova-global-slideoutbottom) {
  from {
    opacity: 1;
    transform: translateY(0);
  }

  to {
    opacity: 0;
    transform: translateY(100px);
  }
}

@keyframes :global(nova-global-bounceinleft) {
  0% {
    opacity: 0;
    transform: translate(0, 0);
  }

  60% {
    opacity: 0.8;
    transform: translate(0, 0);
  }

  80% {
    opacity: 1;
    transform: translate(2px, 0);
  }

  100% {
    transform: none;
  }
}

// NOTE: Due to our post css process, the animation is broken when using css logical operators. So instead of using the
// logical operators, there are two animations and the animation used is based on the :root[dir].

@keyframes :global(nova-appswitcher-menu-slideout-ltr) {
  from {
    transform: translate3d(0, 0, 0);
  }

  to {
    transform: translate3d(-100%, 0, 0);
  }
}

@keyframes :global(nova-appswitcher-menu-slidein-ltr) {
  from {
    transform: translate3d(-100%, 0, 0);
  }

  to {
    transform: translate3d(0, 0, 0);
  }
}

@keyframes :global(nova-appswitcher-menu-slideout-rtl) {
  from {
    transform: translate3d(0, 0, 0);
  }

  to {
    transform: translate3d(100%, 0, 0);
  }
}

@keyframes :global(nova-appswitcher-menu-slidein-rtl) {
  from {
    transform: translate3d(100%, 0, 0);
  }

  to {
    transform: translate3d(0, 0, 0);
  }
}

@keyframes :global(nova-busystate-bar-animation-ltr) {
  0% {
    left: -100%;
  }
  50% {
    left: 0;
  }
  100% {
    left: 100%;
  }
}

@keyframes :global(nova-busystate-bar-animation-rtl) {
  0% {
    right: -100%;
  }
  50% {
    right: 0;
  }
  100% {
    right: 100%;
  }
}

// Dialog's opening animation provides time for children components to execute any measurement calculations
// at the Dialog's full size before scaling down for the opening animation.
@keyframes :global(nova-dialog-open) {
  0% {
    opacity: 0;
    transform: scale(1);
  }
  50% {
    opacity: 0;
    transform: scale(1);
  }
  51% {
    opacity: 0;
    transform: scale(0.5);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes :global(nova-floatingpanel-slidein-ltr) {
  from {
    transform: translateX(var(--floating-panel-width));
  }
  to {
    transform: translateX(0);
  }
}

@keyframes :global(nova-floatingpanel-slideout-ltr) {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(var(--floating-panel-width));
  }
}

@keyframes :global(nova-floatingpanel-slidein-rtl) {
  from {
    transform: translateX(calc(-1 * var(--floating-panel-width)));
  }
  to {
    transform: translateX(0);
  }
}
@keyframes :global(nova-floatingpanel-slideout-rtl) {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(calc(-1 * var(--floating-panel-width)));
  }
}

@keyframes :global(nova-objectmarker-spin) {
  0% {
    transform: rotate(0deg);
    transform-origin: 50%;
  }
  100% {
    transform: rotate(360deg);
    transform-origin: 50%;
  }
}

@keyframes :global(nova-splashscreen-clockwise) {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes :global(nova-welcome-outro) {
  from {
    width: 100vw;
  }
  to {
    width: 40vw;
  }
}
@keyframes :global(nova-welcome-closing) {
  from {
    width: 40vw;
    padding: 5rem;
  }
  to {
    width: 0;
    padding: 0;
  }
}
@keyframes :global(slide-left) {
  from {
    transform: translateX(0);
    opacity: 1;
  }
  to {
    transform: translateX(-50%);
    opacity: 0.4;
  }
}
