.menu-button-container {
  display: flex;
  overflow-x: auto;
  align-items: baseline;
  // vertical scroll bars appear without this when it's in a column flex container that's full.
  flex-shrink: 0;
  flex-wrap: wrap;
}

.read-only-container {
  border: solid 1px transparent;
  padding: 0.25rem;
  white-space: nowrap;
}

.menu-button {
  align-items: baseline;
}

.menu-button-label {
  color: var(--info-fg);
  font-size: var(--info-font-size);
  font-family: var(--info-font-family);
  margin-right: 0.5rem;
}

.italic {
  font-style: italic;
}

.menu-button-value {
  color: var(--info-fg);
  font-size: var(--info-font-size);
  font-family: var(--info-font-family);
  font-weight: bold;
}

.menu-button-arrow {
  margin-left: 0.25rem;
  align-self: center;
}
