// added on sections and visuals right after they are inserted into SlideTransition
.container {
  animation-iteration-count: 1;
  animation-play-state: paused;
  flex-grow: 1;
  display: flex;
  flex-basis: 0;
  overflow: hidden;
}

.base {
  overflow: hidden;
  flex-grow: 1;
  display: flex;
  flex-shrink: 1;
  flex-direction: column;
}

.container-start {
  width: 200%;
  transform: translate3d(-50%, 0, 0);
  pointer-events: none;
}

.container-final {
  transform: translate3d(0, 0, 0);
}

.container-animate {
  animation-play-state: running;
  animation-name: container-animate;
  animation-timing-function: step-end;
  &.fade {
    animation-name: container-animate, fade;
    animation-duration: 0.1s, 0.5s;
    animation-timing-function: step-end, ease-out;
    animation-fill-mode: forwards;
  }
}

@keyframes container-animate {
  from {
    transform: translate3d(0, 0, 0);
  }
  to {
    transform: translate3d(-50%, 0, 0);
  }
}

@keyframes fade {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
