.scale {
  background-size: contain;
  background-repeat: no-repeat;
  width: 100%;
  height: 100%;
}

.fit {
  width: 100%;
  height: 100%;
}

.fit-height {
  height: 100%;
}

.fit-width {
  width: 100%;
}

.tile {
  background-size: auto;
  background-repeat: repeat;
  width: 100%;
  height: 100%;
}

.span {
  display: inline-block;
}

.img {
  border: none;
}

.img,
.span {
  &:focus {
    outline: var(--focus-light-bg);
    // FIXME: The offset here is not necessary on a component level. Adding it here
    // as a workaround just for VA-SDK issue. See details in https://rndjira.sas.com/browse/VASDK-21
    outline-offset: calc(-2px + (-1 * var(--focus-width)));
  }
}

.hidden-img {
  display: none;
}

.clickable {
  cursor: pointer;
}
