.container {
  display: inline-flex;
  border: 1px solid var(--date-picker-bd);
  background: var(--date-picker-bg);
  box-sizing: border-box;
}
.container:hover {
  border-color: var(--date-picker-hover-bd);
  background: var(--date-picker-hover-bg);
}
.container.disabled {
  border-color: var(--date-picker-disabled-bd);
  background: var(--date-picker-disabled-bg);
}
.container.disabled:focus-within {
  border-color: var(--date-picker-disabled-bd);
}
.container.alert {
  border-color: var(--date-picker-alert-bd);
  background: var(--date-picker-alert-bg);
}
.container.warning {
  border-color: var(--date-picker-warning-bd);
  background: var(--date-picker-warning-bg);
}
.container:focus-within {
  border-color: var(--date-picker-active-bd);
  background: var(--date-picker-active-bg);
}

.container-inner {
  display: flex;
  align-items: center;
  min-width: 0;
  width: 100%;
}
.container-inner.focus-visible {
  position: relative;
}
.container-inner.focus-visible:after {
  outline: var(--date-picker-focus-outline);
  outline-offset: var(--date-picker-focus-outline-offset);
  content: "";
  position: absolute;
  inset: 0;
  pointer-events: none;
}

.input {
  flex-grow: 1;
}

.icon-button {
  padding: 0 0.25rem;
  border-start-start-radius: 0;
  border-end-start-radius: 0;
  height: 100%;
}

.editable-icon-button {
  border-inline-start: 0;
}

.footer {
  display: grid;
  grid-template-columns: auto auto;
  column-gap: 0.5rem;
  justify-content: flex-end;
}

.form {
  background-color: var(--datefield-bg);
  border-color: var(--datefield-bd);
}

.input-enabled > input {
  color: var(--datefield-fg);
}
.input-enabled > input:focus {
  border-width: 0;
  outline: none;
}

.input-error > input {
  border-width: 0;
  color: var(--datefield-active-fg);
  outline: none;
}

.form {
  display: inline-flex;
  border-width: 1px;
  border-right: 1px solid var(--datefield-noneditable-bg);
  width: 100%;
}
.form.active {
  border-style: solid;
  outline: var(--datefield-focus-outline);
  outline-offset: var(--datefield-focus-outline-offset);
}
.form.active:not(.disabled) {
  border-color: var(--datefield-active-bd);
}
.form.error {
  border-color: var(--datefield-error-bd);
  border-style: solid;
}
.form.warning {
  border-color: var(--datefield-warning-bd);
  border-style: solid;
}

.disabled {
  background-color: var(--datefield-disabled-bg);
  border-color: var(--datefield-disabled-bd);
  color: var(--datefield-disabled-fg);
  cursor: default;
}

.reset-button {
  flex-shrink: 0;
}

.date-input {
  flex-grow: 1;
}
.date-input > input {
  width: 100%;
  min-height: 1.25rem;
}

.error-button {
  flex-shrink: 0;
}

.message, .warning-message, .alert-message {
  padding: var(--datefield-message-pd-squish-s);
  font-size: var(--datefield-message-font-size-xs);
  white-space: normal;
  overflow-wrap: anywhere;
  word-break: break-word;
  display: block;
}

.alert-message {
  background-color: var(--datefield-message-bg);
  border: var(--datefield-message-alert-bd);
  color: var(--datefield-message-alert-fg);
}

.warning-message {
  background-color: var(--datefield-message-bg);
  border: var(--datefield-message-warning-bd);
  color: var(--datefield-message-warning-fg);
}

.date-time-container {
  justify-content: center;
  align-items: center;
}

.select {
  min-width: 10rem;
}

