@use 'utils/animation';
$button-anim: animation.duration(var(--button-animation-duration-hover))
  var(--button-animation-timing-function-hover) forwards;

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  padding: var(--button-spacing-pd);
  // min-height to support custom fonts and font sizes
  min-block-size: var(--button-spacing-min-block-size);
  font-size: var(--button-font-size);
  line-height: normal;
  cursor: pointer;
  user-select: none;
  white-space: nowrap;
  text-shadow: none;
  border-width: 1px;
  border-style: solid;
  border-radius: 0.125rem;
  color: var(--button-fg);
  background-color: var(--button-bg);
  filter: var(--button-filter, none);
  //fall back to transparent. The legacy tokens do not define border-color for light weight buttons
  border-color: var(--button-bd, transparent);
  transition: animation.duration(var(--button-animation-duration))
    var(--button-animation-timing-function);
  transition-property: background-color, border-color, color;
  // Icon Styling
  .icon-inner :global(.neutral),
  .icon-inner :global(.iconColor) {
    --icon-default-fg: var(--button-fg);
    transition: fill animation.duration(var(--button-animation-duration))
      var(--button-animation-timing-function);
  }

  &.disabled {
    .icon-inner :global(.alert) {
      --icon-alert-fg: var(--button-fg);
    }

    .icon-inner :global(.warning) {
      --icon-warning-fg: var(--button-fg);
    }

    .icon-inner :global(.information) {
      --icon-information-fg: var(--button-fg);
    }

    .icon-inner :global(.success) {
      --icon-success-fg: var(--button-fg);
    }
  }

  &:hover {
    color: var(--button-hover-fg);
    background-color: var(--button-hover-bg);
    //fall back to transparent. The legacy tokens do not define border-color for light weight buttons
    border-color: var(--button-hover-bd, transparent);
    // Icon Styling
    .icon-inner :global(.neutral),
    .icon-inner :global(.iconColor) {
      --icon-default-fg: var(--button-hover-fg);
    }
    transition: $button-anim;
  }
  &.active,
  &:active {
    color: var(--button-active-fg);
    background-color: var(--button-active-bg);
    //fall back to transparent. The legacy tokens do not define border-color for light weight buttons
    border-color: var(--button-active-bd, transparent);
    // Icon Styling
    .icon-inner :global(.neutral),
    .icon-inner :global(.iconColor) {
      --icon-default-fg: var(--button-active-fg);
    }
    transition: $button-anim;
  }
  // Status icon in High contrast. needed to make icon visible/legible
  &.high-contrast:active,
  &.high-contrast.active {
    filter: var(--button-active-filter, none);
    &:not(.disabled) .icon-inner:not(:global(.svgToolTipPane)) {
      --icon-default-override-fg: var(--button-active-fg);
      --icon-neutral-override-fg: var(--button-active-fg);
      --icon-alert-override-fg: var(--button-active-fg);
      --icon-warning-override-fg: var(--button-active-fg);
      --icon-success-override-fg: var(--button-active-fg);
      --icon-information-override-fg: var(--button-active-fg);
    }
  }
}
// .button
.small {
  @extend .button;
  height: 1.375rem;
  // overriding the min-height coming from .button
  min-height: revert;
  font-size: var(--button-font-size-s);

  .icon-inner {
    width: var(--button-font-size-s);
    height: var(--button-font-size-s);
  }
}
.round-icon {
  @extend .button;
  padding: var(--button-spacing-s);
  border-radius: 100%;
}
.text {
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: start;
}
// Disabled Buttons
.disabled {
  cursor: default;
}

.button:focus {
  outline: var(--button-focus-outline);
  outline-offset: var(--button-focus-outline-offset);

  &.active,
  &:active {
    outline: var(--button-focus-outline-active);
  }
}

.icon-with-text {
  margin-inline-end: var(--button-spacing-xs);
  flex-shrink: 0;
}

.normal-button {
  border-radius: var(--button-border-radius-xs);
}

.normal-button-with-text {
  padding-inline: var(--button-spacing-pd-inline);
}
