.content {
  height: 100%;
  width: 100%;
  align-items: stretch;
  overflow: hidden;
}

.list-container {
  flex-grow: 1;
  flex-basis: 100%;
  min-height: 0;
}

.list-parent {
  width: 100%;
  height: 100%;
}

.grid {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  grid-template-rows: auto auto;
  grid-template-areas: 'label label' 'shortcut alt-shortcut';
  gap: var(--keyboard-shortcuts-dialog-spacing-xs);
}

.label {
  grid-area: label;
  display: flex;
  max-width: 100%;
  min-width: 0;
}

.shortcut {
  grid-area: shortcut;
  width: 100%;
}

.alt-shortcut {
  grid-area: alt-shortcut;
  width: 100%;
}
