.container {
  inline-size: 100%;
  display: grid;
  grid-template-areas: 'icon title menu-button';
  grid-template-columns: min-content 1fr auto;
  align-items: center;
}

.with-subtitle {
  grid-template-areas:
    'icon title menu-button'
    'subtitle subtitle menu-button';
  grid-row-gap: var(--tile-header-spacing-xs);
}

.title {
  grid-area: title;
  justify-self: start;
  font-size: var(--tile-header-font-size);
  font-weight: var(--tile-header-font-weight);
  max-inline-size: 100%;
  min-inline-size: 0;
}

.subtitle {
  grid-area: subtitle;
  justify-self: start;
  min-inline-size: 0;
  font-size: var(--tile-header-font-size-s);
}

.menu-button-container {
  grid-area: menu-button;
  margin-inline-start: var(--tile-header-spacing-s);
}

.icon {
  grid-area: icon;
  margin-inline-end: var(--tile-header-spacing-xs);
}

.with-subtitle .icon {
  margin-inline-end: var(--tile-header-spacing-s);
}
