.item {
  cursor: var(--list-row-cursor);
  display: flex;
  align-items: center;
  box-sizing: border-box;
  text-decoration: var(--list-item-text-decoration);
  font-weight: var(--list-item-font-weight-default);
  text-decoration-color: var(--list-item-fg);
  font-style: var(--list-item-font-style);
  font-family: var(--list-item-font-family);
}
.disabled .item {
  text-decoration-color: var(--list-item-disabled-fg);
}
.item.unread {
  font-weight: var(--list-item-font-weight-thick);
}

.double-lines, .single-line {
  box-sizing: border-box;
  display: flex;
  min-height: 0.875rem;
  color: var(--list-item-fg);
  flex-grow: 1;
  min-width: 0;
}

.single-line {
  flex-direction: row;
  align-items: center;
  gap: var(--list-item-gap);
}

.double-lines {
  flex-direction: column;
}

.sub-section {
  display: flex;
  align-items: center;
  font-size: var(--list-item-font-size-s);
}

.base-text, .item-description-text, .item-info-text, .item-text {
  min-width: 1.5rem;
  color: var(--list-item-fg);
}
.disabled .base-text, .disabled .item-description-text, .disabled .item-info-text, .disabled .item-text {
  color: var(--list-item-disabled-fg);
}

.item-text {
  font-size: var(--list-item-font-size-m);
}
.item-text.bold {
  font-weight: var(--list-item-font-weight-thick);
}
.read .item-text.bold {
  font-weight: var(--list-item-font-weight-default);
}
.item-text.description {
  flex: auto;
  flex-grow: 1;
  font-size: var(--list-item-font-size-m);
}
.item-text:not(.wrap) {
  white-space: var(--list-item-white-space, pre);
}
.infoHasPriority .item-text {
  flex-shrink: 99999;
}

.item-info-text {
  font-size: var(--list-item-font-size-s);
  flex-grow: 1;
  flex-basis: min-content;
  flex-shrink: 99999;
  color: var(--list-item-description-fg);
}
.item-info-text:not(.concat) {
  text-align: end;
}
.infoHasPriority .item-info-text {
  flex: 1 1 auto;
}

.item-description-text {
  font-size: var(--list-item-font-size-s);
  color: var(--list-item-description-fg);
}
.infoHasPriority .item-description-text {
  flex-shrink: 99999;
}

.item-text-separator {
  color: var(--list-item-fg);
  flex-shrink: 0;
  padding-inline-start: var(--list-spacing-s);
  padding-inline-end: var(--list-spacing-s);
  font-size: var(--list-item-font-size-s);
}

.item-icon {
  flex-shrink: 0;
}
.item-icon :global(.iconColor) {
  --icon-default-fg: var(--list-item-fg);
}

.item-avatar {
  flex-shrink: 0;
}

.counter {
  flex-shrink: 0;
  color: var(--list-item-fg);
  font-size: var(--list-item-font-size-m);
  margin-inline-start: var(--list-spacing-m);
  position: relative;
}
.disabled .counter {
  color: var(--list-item-disabled-fg);
}

.content-container {
  align-items: center;
  flex-grow: 1;
  min-width: 2rem;
  position: relative;
}
.read .content-container {
  margin-inline-start: calc(0.875rem + var(--list-spacing-xs));
}
.infoHasPriority .content-container {
  min-width: 1.5rem;
  flex-shrink: 99999;
}
.infoHasPriority .content-container.hasItemIcon {
  min-width: 3rem;
}
.infoHasPriority .content-container.hasItemAvatar {
  min-width: 3.5rem;
}

.dot {
  align-self: flex-start;
  margin: var(--list-spacing-xxs) var(--list-spacing-xs) var(--list-spacing-xxs) 0;
  flex-shrink: 0;
}

