.position-top-left {
  position: absolute;
  top: 2px;
  left: 2px;
  z-index: 1;
}

.container {
  display: flex;
  isolation: isolate;
  cursor: auto;
  pointer-events: none;
}

.enable-pointer {
  pointer-events: auto;
}

.button {
  height: calc(1.75rem + 2px);
  border-radius: 0;
  border: 1px solid var(--button-bd);
  padding: 0 0.25rem;
}

.button-container {
  background-color: var(--button-bg);
  transition-property: opacity;
  transition-duration: 0.2s;
  transition-timing-function: ease-in-out;
}

.hidden {
  opacity: 0;
}

.pin-container {
  background-color: var(--button-pin-bg);
  align-self: flex-start;
  position: relative;
  left: -1px;
}

