.container {
  display: flex;
  height: 0.875rem;
  width: 1.75rem;
  padding: calc((1.75rem - 0.875rem) / 2) 0;
}

.switch-button {
  background-color: var(--switch-bg);
  border: 1px solid var(--switch-bd);
  border-radius: 1rem;
  cursor: pointer;
  height: 100%;
  overflow: hidden;
  padding: 0;
  transition: calc(var(--switch-anim-dur) * var(--nova-animation-duration-factor, 1)) var(--switch-anim-tf);
  transition-property: background-color, border-color;
  width: 100%;
}
.switch-button:focus {
  outline: var(--focus-light-bg);
}
.switch-button.readonly {
  cursor: default;
}
.switch-button.disabled {
  background-color: var(--switch-disabled-bg);
  border-color: var(--switch-disabled-bd);
  cursor: default;
}

.switch-inner {
  height: 100%;
  position: relative;
  transition: calc(var(--switch-anim-dur) * var(--nova-animation-duration-factor, 1)) var(--switch-anim-tf);
  transition-property: transform;
  width: 100%;
  display: flex;
  align-items: center;
  padding: 1px;
  box-sizing: border-box;
}

.handle {
  align-items: center;
  background-color: var(--switch-fg);
  border: 1px solid var(--switch-fg);
  border-radius: 50%;
  display: flex;
  height: var(--switch-handle-size);
  transition: calc(var(--switch-anim-dur) * var(--nova-animation-duration-factor, 1)) var(--switch-anim-tf);
  transition-property: background-color, border-color;
  width: var(--switch-handle-size);
}
.disabled .handle {
  background-color: var(--switch-disabled-fg);
  border-color: var(--switch-disabled-bd);
}

.icon-off {
  inset-inline-start: calc(100% - 1.5 * var(--switch-handle-size));
  position: absolute;
}
.icon-off > svg > :global(.iconColor) {
  transition: calc(var(--switch-anim-dur) * var(--nova-animation-duration-factor, 1)) var(--switch-anim-tf);
  transition-property: fill;
}
.icon-on {
  inset-inline-start: calc(-100% + 1.25 * var(--switch-handle-size));
  position: absolute;
}
.icon-on > svg > :global(.iconColor) {
  transition: calc(var(--switch-anim-dur) * var(--nova-animation-duration-factor, 1)) var(--switch-anim-tf);
  transition-property: fill;
}

