.draggable {
  cursor: default;
}
.draggable:hover {
  transition: box-shadow calc(var(--dnd-item-anim-dur) * var(--nova-animation-duration-factor, 1)) var(--dnd-item-trans-dur);
  box-shadow: var(--dnd-item-medium-sh);
}

.dragged-item {
  opacity: var(--dnd-moved-item-opacity);
  box-sizing: border-box;
}

.drag-over {
  background: var(--dnd-drop-zone-bg);
}

.drag-over {
  outline: 1px solid var(--dnd-drop-zone-bd);
  outline-offset: -1px;
}

.drop-line {
  position: relative;
}

.drop-line:before {
  content: "";
  background: var(--dnd-drop-line-bg);
  border: 1px solid var(--dnd-drop-line-loop);
  box-shadow: 0 1px 4px 0 var(--dnd-drop-line-loop-shadow);
  position: absolute;
  width: 6px;
  height: 6px;
  border-radius: 100%;
  z-index: 1;
  pointer-events: none;
  inset-inline-start: 0px;
  inset-block-start: 0;
}

.drop-line.place-at-end:before {
  inset-inline-start: auto;
  inset-inline-end: 0px;
}

.drop-line:after {
  content: "";
  background: var(--dnd-drop-line-bg);
  position: absolute;
  pointer-events: none;
  width: 2px;
  height: 100%;
  inset-inline-start: calc((6px / 2) + 0px);
}

.drop-line.place-at-end:after {
  inset-inline-start: auto;
  inset-inline-end: calc((6px / 2) + 0px);
}

.v-drop-line {
  position: relative;
}

.v-drop-line:before {
  content: "";
  background: var(--dnd-drop-line-bg);
  border: 1px solid var(--dnd-drop-line-loop);
  box-shadow: 0 1px 4px 0 var(--dnd-drop-line-loop-shadow);
  position: absolute;
  width: 6px;
  height: 6px;
  border-radius: 100%;
  z-index: 1;
  pointer-events: none;
  inset-inline-start: 0;
  inset-block-start: 0px;
}

.v-drop-line.place-at-end:before {
  inset-block-start: auto;
  inset-block-end: 0px;
}

.v-drop-line:after {
  content: "";
  background: var(--dnd-drop-line-bg);
  position: absolute;
  pointer-events: none;
  width: 100%;
  height: 2px;
  inset-block-start: calc((6px / 2) + 0px);
  inset-inline-start: 0;
}

.v-drop-line.place-at-end:after {
  inset-block-end: calc((6px / 2) + 0px);
  inset-block-start: auto;
}

