%container {
  position: relative;
  display: flex;
  font-size: var(--slider-grip-label-font-size-m);
  pointer-events: none;
}

.container_horizontal {
  @extend %container;
  padding: var(--slider-grip-label-spacing-xs) 0;
  align-items: flex-end;
  flex-direction: row;
  grid-row: 1;
  grid-column: 3 / 5;
  margin-block-end: var(--slider-grip-label-spacing-xs);
}

.container_vertical {
  @extend %container;
  flex-direction: column;
  grid-row: 3 / 5;
  grid-column: 3;
}

%spacer {
  background-color: transparent;
  display: inline-block;
  min-height: 1px;
  white-space: nowrap;
}

.spacer-center {
  @extend %spacer;
  position: relative;
  pointer-events: auto;
  max-width: 100%;
}

.spacer-start {
  @extend %spacer;
  pointer-events: none;
}

.spacer-end {
  @extend %spacer;
  pointer-events: none;
}

.label {
  user-select: none;
  cursor: text;
  display: block;
  font-size: var(--slider-grip-label-fontSize, var(--slider-grip-label-font-size-m));
  font-weight: var(--slider-grip-label-fontWeight, inherit);
  font-style: var(--slider-grip-label-fontStyle, inherit);
  font-family: var(--slider-grip-label-fontFamily, inherit);
  text-decoration: var(--slider-grip-label-textDecoration, inherit);
  color: var(--slider-grip-label-fg);
}

.input-padding {
  &_horizontal {
    padding: 0 var(--slider-grip-label-spacing-m);
  }

  &_vertical {
    padding: 0 var(--slider-grip-label-spacing-m);
    position: relative;
    inset-block-start: -0.25rem;
  }
}

.label-hide {
  visibility: hidden;
}

.disabled {
  color: var(--slider-grip-label-disabled-fg);
}
