.popover {
  pointer-events: initial;
  z-index: 20;
  border-radius: var(--popover-border-radius);
  font-family: var(--popover-fontFamily);
  font-size: var(--popover-fontSize);
  font-style: var(--popover-fontStyle);
  font-weight: var(--popover-fontWeight);
  text-decoration: var(--popover-textDecoration);
}

.popover-inner {
  display: grid;
  background-color: var(--popover-bg);
  border: 1px solid var(--popover-bd);
  box-shadow: var(--popover-sh);
  border-radius: var(--popover-border-radius);
}

.popover-outer:focus-visible::after {
  content: "";
  display: block;
  position: fixed;
  inset-inline-start: 0;
  inset-block-start: 0;
  inline-size: 100%;
  block-size: 100%;
  pointer-events: none;
  outline: var(--focus-light-bg);
}

.popover-hide[data-popper-reference-hidden=true] {
  visibility: hidden;
  pointer-events: none;
}

.popover-title {
  padding: var(--popover-padding);
  font-size: var(--popover-font-size-m);
  font-weight: var(--popover-title-font-weight);
  font-family: var(--popover-title-font-family);
  color: var(--popover-title-fg);
  overflow: hidden;
  justify-content: center;
}

.popover-pane {
  overflow: auto;
  box-sizing: border-box;
  display: inline-block;
  font-size: var(--popover-font-size-m);
  padding: var(--popover-padding);
  color: var(--popover-fg);
}
.popover-pane.hasTitle {
  padding-block-start: 0;
}
.popover-pane.hasFooter {
  padding-block-end: 0;
}
.popover-pane:focus::after {
  content: "";
  display: block;
  position: fixed;
  inset-inline-start: 0;
  inset-block-start: 0;
  inline-size: 100%;
  block-size: 100%;
  pointer-events: none;
  outline: var(--focus-light-bg);
  outline-offset: calc(-1 * var(--focus-width) - 2px);
}

.popover-footer {
  padding: var(--popover-padding);
  font-size: var(--popover-font-size-m);
  display: flex;
  justify-content: flex-end;
}

.arrow {
  position: absolute;
  display: flex;
  overflow: hidden;
}

.arrow::before {
  content: "";
  position: absolute;
  width: 16px;
  height: 16px;
  background-color: var(--popover-bg);
  transform: rotate(45deg);
}

[data-popper-placement^=top] .arrow,
[data-popper-placement^=bottom] .arrow {
  height: calc(22px - 8px);
  width: calc(22px - 2px);
  justify-content: center;
}

[data-popper-placement^=left] .arrow,
[data-popper-placement^=right] .arrow {
  height: calc(22px - 2px);
  width: calc(22px - 8px);
  align-items: center;
}

[data-popper-placement^=top] .arrow {
  bottom: calc(-1 * 22px + 9px);
  align-items: flex-end;
}
[data-popper-placement^=top] .arrow::before {
  bottom: 50%;
  border-bottom: 1px solid var(--popover-arrow-bd);
  border-right: 1px solid var(--popover-arrow-bd);
}

[data-popper-placement^=bottom] .arrow {
  top: calc(-1 * 22px + 9px);
  align-items: flex-start;
}
[data-popper-placement^=bottom] .arrow::before {
  top: 50%;
  border-top: 1px solid var(--popover-arrow-bd);
  border-left: 1px solid var(--popover-arrow-bd);
}

[data-popper-placement^=left] .arrow {
  right: calc(-1 * 22px + 9px);
  justify-content: flex-end;
}
[data-popper-placement^=left] .arrow::before {
  right: 50%;
  border-top: 1px solid var(--popover-arrow-bd);
  border-right: 1px solid var(--popover-arrow-bd);
}

[data-popper-placement^=right] .arrow {
  left: calc(-1 * 22px + 9px);
  justify-content: flex-start;
}
[data-popper-placement^=right] .arrow::before {
  left: 50%;
  border-bottom: 1px solid var(--popover-arrow-bd);
  border-left: 1px solid var(--popover-arrow-bd);
}

.viewport-overlay {
  position: fixed;
  pointer-events: auto;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
}

.block-pointer-events {
  pointer-events: none;
}

