@use 'utils/animation';

$dropdown-height: 1.5rem;

.current {
  font-weight: var(--breadcrumb-text-font-weight-thick);
}

.interactive {
  cursor: pointer;
  :global {
    animation: fade-in-out 0.3s var(--breadcrumb-text-anim-tf-exit) forwards;
  }
}

.interactive:hover {
  text-decoration: underline;
}

.action-button {
  max-inline-size: 25rem;
}

.action-button:hover,
.action-button:active {
  text-decoration: underline;
  background-color: transparent;
  color: var(--breadcrumb-text-fg);
}

.item-text {
  user-select: none;
  font-size: var(--breadcrumb-font-size-m);
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  text-decoration: none;
  cursor: default;
  color: var(--breadcrumb-text-fg);
  padding: var(--breadcrumb-squish-xs);
  font-family: var(--breadcrumb-text-font-family);
}

.item-text:focus {
  outline: var(--breadcrumb-text-focus);
  outline-offset: calc(-1 * var(--focus-width));
}

.multi-item {
  min-width: auto;
}

// FIXME: prevents Select styles from overriding
.dropdown.dropdown {
  padding: 0;
  background-color: transparent;
  border: solid 1px transparent;
  color: var(--breadcrumb-dropdown-fg);

  // Override Dropdown's min-height style. If we want the breadcrumb to adjust its height
  // based on font size, this will need to be revisited. VAV's breadcrumb was always 1.5rem.
  height: $dropdown-height;
  min-height: $dropdown-height;

  &:hover,
  &:focus {
    color: var(--breadcrumb-dropdown-fg);
  }
}

// FIXME: overrides Dropdown focus style that sets outline to 0
.dropdown.dropdown:focus {
  outline: var(--breadcrumb-dropdown-focus);
  outline-offset: var(--breadcrumb-dropdown-focus-offset);
}

.dropdown-label {
  padding: var(--breadcrumb-squish-xs);
}

.dropdown-arrow {
  display: inline-flex;
  align-items: center;
  padding: var(--breadcrumb-squish-xs);
}
// font animation
@keyframes :global(fade-in-out) {
  from {
    opacity: 0.6;
  }
  to {
    opacity: 1;
  }
}

.exit-animation {
  :global {
    animation: slide-left var(--breadcrumb-anim-dur-enter) var(--breadcrumb-anim-tf-enter);
  }
}
