.cropper.ellipse :global(.cropper-view-box),
.cropper.ellipse :global(.cropper-face) {
  border-radius: 50%;
}

:global .cropper-view-box {
  outline: none;
}
:global .cropper-modal {
  opacity: 0.65;
}
:global .cropper-line {
  background-image: linear-gradient(45deg, #000 25%, transparent 25%), linear-gradient(-45deg, #000 25%, transparent 25%), linear-gradient(45deg, transparent 75%, #000 75%), linear-gradient(-45deg, transparent 75%, #000 75%);
  background-size: 8px 8px;
  background-position: 0 0, 0 4px, 0px, -4px 0px;
  background-color: #fff;
  opacity: 1;
}
:global .cropper-line.line-n {
  height: 1px;
  top: -1px;
}
:global .cropper-line.line-s {
  height: 1px;
  bottom: -1px;
}
:global .cropper-line.line-w {
  width: 1px;
  left: -1px;
}
:global .cropper-line.line-e {
  width: 1px;
  right: -1px;
}
:global .cropper-point,
:global .cropper-point.point-se {
  background: #fff;
  height: 5px;
  width: 5px;
}

.invisible-image {
  display: none;
}

