.title-container {
  width: 100%;
}

.title-container-flex {
  width: 100%;
  display: flex;
  align-items: center;
}

.title {
  color: var(--title-fg);
  user-select: text;
  padding: var(--title-custom-padding, 0 0 0.5rem);
  font-size: var(--title-font-size);
  overflow-wrap: break-word;
}
.title p {
  margin: 0;
  padding: 0;
}

.help-content {
  display: flex;
  flex-direction: column;
  gap: var(--info-popup-content-pd);
  padding-top: var(--info-popup-content-tiny-pd);
  padding-bottom: var(--info-popup-content-small-pd);
  color: var(--info-popup-content-fg);
}

.help-content-header {
  font-weight: var(--info-popup-content-font-weight-thick);
  font-size: var(--info-popup-content-font-size-m);
  color: var(--info-popup-content-title-fg);
  text-align: center;
}

.help-content-text {
  width: 16.5rem;
  margin: 0;
  padding: 0;
  white-space: pre-wrap;
  word-break: break-word;
}

