.container {
  box-sizing: border-box;
  border-width: 1px;
  border-style: solid;
  position: relative;
  background-color: var(--panel-bg);
  border-color: var(--panel-bd);
  box-shadow: var(--panel-sh);
}

.container-inner {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.resizeGrabber {
  position: absolute;
  right: 0;
  bottom: 0;
  cursor: se-resize;
  line-height: 0;
  z-index: 2;
}

