.text {
  overflow-x: hidden;
  overflow-y: auto;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  flex-shrink: 1;
  user-select: text;
  overflow-wrap: break-word;
  font-size: var(--text-font-size);
  a:focus {
    outline: var(--focus-light-bg);
    outline-offset: calc(var(--focus-width) * -1);
  }
  &:focus {
    outline: var(--focus-light-bg);
    outline-offset: calc(var(--focus-width) * -1);
  }
}
