// This is required to fit table outer container inside the splitter section.
.table-content {
  overflow: hidden;
  height: 100%;
  max-width: 100%;
  display: flex;
  flex-direction: column;
}

.table-list {
  flex-grow: 1;
  min-height: 0;

  .table-list-inner {
    height: 100%;
    width: 100%;
  }
}

.label {
  display: flex;
  align-items: center;
}

.text {
  flex-grow: 1;
  user-select: none;
  overflow: hidden;
}

.listIcon {
  flex-shrink: 0;
  margin-inline-end: var(--dualselector-list-icon-mg-inline);
}

.icon {
  flex-shrink: 0;
  margin-inline-start: var(--dualselector-list-icon-mg-inline);
}

.list-search-field {
  margin: var(--dualselector-list-searchfield-spacing);
  display: flex;
  height: 2rem;
  flex-shrink: 0;
}

.list-search-field .search-field {
  width: 100%;
}

.description-text {
  font-size: var(--description-font-size-s);
}

.flow-container {
  width: 100%;
  align-items: center;
}
