.toolbar {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
  position: relative;
  overflow-x: clip;
}
.toolbar > * + * {
  margin-inline-start: var(--toolbar-spacing-xs);
  margin-inline-end: 0;
}

.separator-container {
  display: flex;
  align-self: stretch;
}

.spacer-container {
  display: contents;
  flex-grow: 1;
}

