$cropper-checkerboard-size: 8px;

// .cropper-view-box and .cropper-face are added by react-cropper,
// thus using :global() below
.cropper.ellipse :global(.cropper-view-box),
.cropper.ellipse :global(.cropper-face) {
  border-radius: 50%;
}

// All these classes belong to react-cropper
:global {
  .cropper-view-box {
    outline: none;
  }

  .cropper-modal {
    opacity: 0.65;
  }

  .cropper-line {
    // We use a checkerboard gradient to alternate the colors of the dashed cropper line both horizontally and vertically.
    // We also use px for the size so the pattern doesn't scale with the base font-size.
    // Black and White are hard coded because they shouldn't be tied to the our color token system.
    background-image: linear-gradient(45deg, #000 25%, transparent 25%),
      linear-gradient(-45deg, #000 25%, transparent 25%),
      linear-gradient(45deg, transparent 75%, #000 75%),
      linear-gradient(-45deg, transparent 75%, #000 75%);
    background-size: $cropper-checkerboard-size $cropper-checkerboard-size;
    background-position: 0 0, 0 $cropper-checkerboard-size/2,
      $cropper-checkerboard-size/2 -$cropper-checkerboard-size/2, -$cropper-checkerboard-size/2 0px;
    background-color: #fff;
    opacity: 1;
  }

  .cropper-line.line-n {
    height: 1px;
    top: -1px;
  }

  .cropper-line.line-s {
    height: 1px;
    bottom: -1px;
  }

  .cropper-line.line-w {
    width: 1px;
    left: -1px;
  }

  .cropper-line.line-e {
    width: 1px;
    right: -1px;
  }

  .cropper-point,
  .cropper-point.point-se {
    background: #fff;
    height: 5px;
    width: 5px;
  }
}

.invisible-image {
  display: none;
}
