.container {
  display: grid;
  grid-template-columns: calc(0.875rem + (1px * 2) + var(--checkbox-spacing-s, 0.5rem)) minmax(0, auto) 1fr;
  grid-template-rows: auto auto;
  row-gap: var(--checkbox-row-gap);
  place-items: start;
}
.container.no-label {
  grid-template-columns: auto auto;
}

.main {
  grid-row: 1/2;
  grid-column: 1/3;
  cursor: pointer;
  position: relative;
  height: auto;
  display: flex;
  flex-direction: row-reverse;
  align-items: flex-start;
  width: max-content;
  max-width: 100%;
  box-sizing: border-box;
  padding: logical 1px 1px 1px var(--checkbox-spacing-xs);
}
.no-label .main {
  grid-column: 1/2;
  padding: 1px;
  margin-block-start: var(--checkbox-spacing-xxs);
  margin-block-end: var(--checkbox-spacing-xxs);
}
.main.disabled, .main.read-only {
  cursor: default;
}
.main:focus:after {
  content: "";
  pointer-events: none;
  position: absolute;
  inset-inline-start: 0;
  inset-block-start: 0;
  height: 100%;
  width: 100%;
  outline: var(--focus-light-bg);
  outline-offset: calc(-1 * var(--focus-width));
}

.box {
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  border-radius: 1px;
  border-style: solid;
  border-width: 1px;
  height: 0.875rem;
  width: 0.875rem;
  padding: 0;
  margin-inline-end: var(--checkbox-spacing-s, 0.5rem);
  margin-block-start: var(--checkbox-spacing-xxs);
  margin-block-end: var(--checkbox-spacing-xxs);
  transition: all 0.15s ease;
  flex-shrink: 0;
  background-color: var(--checkbox-bg);
  border-color: var(--checkbox-bd);
}
.no-label .box {
  margin: 0;
}
.box.mixed:after {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  border: solid 0.2rem var(--checkbox-bg);
  background: var(--checkbox-fg);
}
.box.selected {
  background-color: var(--checkbox-selected-bg);
  border-color: var(--checkbox-selected-bd);
}
.disabled .box, .disabled.read-only .box {
  background-color: var(--checkbox-disabled-bg);
  border-color: var(--checkbox-disabled-bd);
}
.disabled .box.mixed:after, .disabled.read-only .box.mixed:after {
  background-color: var(--checkbox-disabled-fg);
}
.disabled .box.selected {
  background-color: var(--checkbox-disabled-selected-bg);
  border-color: var(--checkbox-disabled-selected-bd);
}
.read-only .box {
  background-color: var(--checkbox-read-only-bg);
  border-color: var(--checkbox-read-only-bd);
}
.disabled.read-only .box.selected {
  background-color: var(--checkbox-disabled-bg);
  border-color: var(--checkbox-disabled-bd);
}

.label {
  min-width: 0;
  font-size: var(--checkbox-font-size-m);
}

.help-button {
  grid-row: 1/2;
  grid-column: -2/-1;
  min-height: unset;
  margin-block-start: var(--checkbox-spacing-xxs);
  padding: var(--checkbox-squish-xxs);
}

.description {
  grid-row: 2/3;
  grid-column: -3/-1;
  font-size: var(--checkbox-font-size-xs);
}

