@use 'utils/animation';
@use 'reactDnd/dndstyles';

//Alternate style tab's border width is always 1px for visual consistency.
$alt-tab-border-width: 1px;
//The indicator is positioned over the border for illuminate/inpsire/ignite.
$alt-tab-indicator-length: calc(100% + 2 * #{$alt-tab-border-width});

.horizontal {
  display: flex;
  align-items: center;
  width: 100%;
  box-sizing: border-box;
  border-block-end: var(--tab-bar-bd-bottom-width) solid var(--tab-bar-bd);

  > .bar {
    position: relative;
    inset-block-start: var(--tab-bar-bd-bottom-width);
    width: auto;
    overflow: hidden;
  }
}

.tab {
  position: relative;
  display: inline-flex;
  height: 100%;

  &:focus {
    &::before {
      content: '';
      position: absolute;
      inset: 0;
      outline: var(--tab-focus);
      outline-offset: calc(-1 * var(--focus-width) - 2px);
      pointer-events: none;
      z-index: 1;
    }
  }

  // Tab Icons should be the same size as the font size of the tab.
  .icon {
    flex-shrink: 0;
  }
}

.tab {
  &:after {
    content: '';
    position: absolute;
    background: transparent;
    transition: animation.duration(var(--tab-trans-duration)) var(--tab-trans-tf-ease-in-out);
  }
}

.tab.selected {
  .tab-inner {
    background: var(--tab-bg); //overrides hoverbackground
  }

  //Create a selected indicator pseudo-element. Used in each theme for both styles except alternate/high contrast.
  &:after {
    transition: animation.duration(var(--tab-trans-duration)) var(--tab-trans-tf-ease-in-out);
    background: var(--tab-selected-indicator);
  }
}

//Add a transparent border for sizing consistency across themes.
.alternate {
  border: solid $alt-tab-border-width transparent;

  .horizontal & {
    border-block-end: 0;
  }

  .horizontal .content-list > li:first-child & {
    margin-inline-start: var(--tab-bar-spacing-m);

    // LTR layout in RTL mode, needs to adjust the margin for the first tab.
    [dir='rtl'] [dir='ltr'] & {
      margin-left: var(--tab-bar-spacing-m);
      margin-right: 0;
    }
  }

  //Selected tabs in the alternate style have a background that matches the content area below the tabs.
  &.selected {
    .tab-inner {
      background: var(--tab-bg);
    }

    &:after {
      content: var(--tab-indicator-content);
    }
  }
}

//Tabs aligned to the right (content on the left)
.tab[data-sasrc-tablocation='right'] {
  &.selected:after {
    height: 100%;
    width: var(--tab-indicator-width);
    inset-inline-start: auto;
    inset-inline-end: 0;
    inset-block-start: 0;
  }

  //Set the alternate style selected tab's border color. In high contrast this outlines the tab.
  &.selected.alternate {
    border-block-start-color: var(--tab-bd);
    border-inline-end-color: var(--tab-bd);
    border-block-end-color: var(--tab-bd);

    //Set the position of the indicator pseudo-element. This is not used for alternate/high contrast tabs because <code>content</code> is not set.
    &:after {
      height: $alt-tab-indicator-length;
      inset-inline-end: -$alt-tab-border-width;
      inset-block-start: -$alt-tab-border-width;
      inset-inline-start: auto;
    }
  }
}

//Tabs aligned to the left (content on the right)
.tab[data-sasrc-tablocation='left'] {
  &.selected:after {
    height: 100%;
    width: var(--tab-indicator-width);
    inset-inline-start: 0;
    inset-inline-end: auto;
    inset-block-start: 0;
  }

  //Set the alternate style selected tab's border color. In high contrast this outlines the tab.
  &.selected.alternate {
    border-block-start-color: var(--tab-bd);
    border-inline-start-color: var(--tab-bd);
    border-block-end-color: var(--tab-bd);

    //Set the position of the indicator pseudo-element. This is not used for alternate/high contrast tabs because <code>content</code> is not set.
    &:after {
      height: $alt-tab-indicator-length;
      inset-inline-start: -$alt-tab-border-width;
      inset-block-start: -$alt-tab-border-width;
      inset-inline-end: auto;
    }
  }
}

//Tabs aligned on the bottom (content on the top)
.tab[data-sasrc-tablocation='bottom'] {
  &:after {
    height: var(--tab-indicator-width);
    width: 100%;
    inset-inline-start: 0;
    inset-block-end: 0;
    transition: animation.duration(var(--tab-trans-duration)) var(--tab-trans-tf-ease-in-out);
  }

  //Set the alternate style selected tab's border color. In high contrast this outlines the tab.
  &.selected.alternate {
    border-inline-end-color: var(--tab-bd);
    border-inline-start-color: var(--tab-bd);
    border-bottom-color: var(--tab-bd); // 'border-block-end-color' doesn't work correctly

    //Set the position of the indicator pseudo-element. This is not used for alternate/high contrast tabs because <code>content</code> is not set.
    &:after {
      width: $alt-tab-indicator-length;
      inset-inline-start: -$alt-tab-border-width;
      inset-block-end: -$alt-tab-border-width;
      inset-block-start: auto;
      transition: animation.duration(var(--tab-trans-duration)) var(--tab-trans-tf-ease-in-out);
    }
  }

  //alternate 2 style selected tab border
  &.selected.standard-small:after {
    width: calc(100% - (0.5rem * 2));
    inset-inline-start: 0.5rem;
    inset-inline-end: auto;
    transition: animation.duration(var(--tab-trans-duration)) var(--tab-trans-tf-ease-in-out);
  }
}

//Tabs aligned on the top (content on the bottom)
.tab[data-sasrc-tablocation='top'] {
  &:after {
    height: var(--tab-indicator-width);
    width: 100%;
    inset-inline-start: 0;
    inset-block-start: 0;
    transition: animation.duration(var(--tab-trans-duration)) var(--tab-trans-tf-ease-in-out);
  }

  //Set the alternate style selected tab's border color. In high contrast this outlines the tab.
  &.selected.alternate {
    border-inline-end-color: var(--tab-bd);
    border-inline-start-color: var(--tab-bd);
    border-top-color: var(--tab-bd); // 'border-block-start-color' doesn't work correctly

    //Set the position of the indicator pseudo-element. This is not used for alternate/high contrast tabs because <code>content</code> is not set.
    &:after {
      inset-block-start: -$alt-tab-border-width;
      inset-block-end: auto;
      transition: animation.duration(var(--tab-trans-duration)) var(--tab-trans-tf-ease-in-out);
    }
  }

  //alternate 2 style selected tab border
  &.selected.standard-small:after {
    width: calc(100% - (0.5rem * 2));
    inset-inline-start: auto;
    inset-inline-end: auto;
    transition: animation.duration(var(--tab-trans-duration)) var(--tab-trans-tf-ease-in-out);
  }
}

.tab-inner {
  background: var(--tab-bg);
  color: var(--tab-fg);
  font-size: var(--tab-item-font-size);
  font-weight: var(--tab-item-font-weight);
  min-height: var(--tab-item-min-height); // maintain equal height even when has icon only
  display: inline-flex;
  align-items: center;
  padding: var(--tab-spacing-tab-items);
  position: relative;
  white-space: nowrap;
  box-sizing: border-box;
  gap: var(--tab-bar-spacing-xs);
  width: 100%;

  &:hover {
    //Legacy tokens don't support hover, so fall back to the standard tab background.
    background: var(--tab-hover-bg, var(--tab-bg));
    cursor: pointer;
  }
}

.tab-icon {
  display: inline-flex;
  align-items: center;
}

.text {
  font-size: var(--tab-item-font-size);
  font-weight: var(--tab-item-font-weight);
  color: var(--tab-fg);
  min-width: var(--tab-min-width, 3ch);
  max-width: var(--tab-max-width, 25rem);
}

//Add space between the icon and text when both exist.
.tab-inner .tab-icon + .text {
  margin-inline-start: var(--tab-spacing-items-start);
}

.disabled {
  &,
  &:hover {
    .tab-inner {
      background: var(--tab-disabled-bg);
      cursor: default;
    }

    .text {
      color: var(--tab-disabled-fg);
    }
  }
}

// Alternate 2
.standard-small {
  .tab-inner {
    padding: var(--tab-bar-spacing-squish-s);
    font-size: var(--tab-font-size-s);
    min-height: 2rem;
  }

  .read-only {
    font-size: var(--tab-font-size-s);
  }

  .text {
    font-size: var(--tab-font-size-s);
  }
}

//Add space between the menu icon / close button icon and text when both exist.
.text,
.icon-button {
  // In Vertical tab, This will hide the label when tab is collpased
  .collapsed & {
    display: none;
  }
}

// with menu/close button
.with-menu,
.with-closable {
  .tab-inner {
    padding-inline-end: var(--tab-bar-spacing-s);
  }
}

.icon-button {
  background-color: transparent;
  padding: var(--tab-bar-spacing-xs);
  cursor: pointer;
  min-height: auto;
  border-radius: 2px;
  transition: animation.duration(var(--tab-animation-duration)) var(--tab-animation-timing-function);
  transition-property: background-color, color;
  visibility: hidden;

  &:hover {
    background-color: var(--tab-icon-hover-bg);

    :global(.iconColor) {
      --icon-default-override-fg: var(--tab-icon-hover-fg);
    }
  }

  &.open,
  &:active {
    background-color: var(--tab-icon-active-bg);
    visibility: visible;

    :global(.iconColor) {
      --icon-default-override-fg: var(--tab-icon-active-fg);
    }

    filter: var(--tab-icon-active-filter);
  }

  .disabled & {
    cursor: default;
    pointer-events: none;
  }

  .tab:not(.disabled):hover &,
  .selected & {
    visibility: visible;
  }
}

// Add New Tab button
.add-button-container {
  flex-shrink: 0;

  .horizontal & {
    align-self: center;
  }
}

// Tab over Flow Button
.tab-over-flow {
  flex-shrink: 0;
  align-self: center;

  .horizontal &.end-of-tabBar {
    margin-inline-start: auto;
  }

  .vertical & {
    align-self: flex-start;
  }
}

// Toolbar
.toolbar {
  flex-shrink: 0;
  order: 1;
}

.toolbarInlineSpacing {
  margin-inline-start: auto;
}

// Stretch to fit
.horizontal.stretch-to-fit {
  .bar {
    width: 100%;
  }

  .content-list > li {
    flex-grow: 1;
    flex-basis: 0;
  }

  .tab {
    display: flex;
    width: var(--tab-item-stretched-width);
    min-width: 100%;

    &-inner {
      justify-content: center;
    }
  }
}

// Indicator
.indicator {
  display: inline-flex;
}

.vertical {
  &.collapsed {
    .read-only {
      display: none;
    }
  }
}

// Tab - Vertical orientation
.vertical {
  display: inline-flex;
  flex-direction: column;
  height: 100%;
  box-sizing: border-box;
  border-inline-end: var(--tab-bar-bd-bottom-width) solid var(--tab-bar-bd);

  &.tab-list-height-auto .bar {
    // Note: chagning scrollbale height to 'auto', could lead to error due to overflowing infinite recalculation
    height: auto;
  }

  // Side border when bar placed to container on right/end side
  &.place-end {
    border-inline-end: 0;
    border-inline-start: var(--tab-bar-bd-bottom-width) solid var(--tab-bar-bd);
  }

  .tab {
    display: flex;

    &.selected::before {
      outline: var(--tab-selected-focus);
    }
  }

  .tab-icon:not(.tab-icon-only) {
    min-height: 1.18rem; //This value added to avoide Icon fluctivation in Property pane
  }

  // IconOnly tab
  .icon-only {
    &.with-menu,
    &.with-closable {
      .tab-inner {
        padding: var(--tab-spacing-tab-items);
      }

      .icon-button {
        padding: var(--tab-bar-spacing-xs);
      }
    }
  }

  // menu button
  .icon-button-container {
    margin-inline-start: auto;
    cursor: inherit;
  }

  .icon-button {
    margin-inline-start: var(--tab-bar-spacing-xxs);
  }

  // add new tab button
  .add-button,
  .tab-overflow-button {
    padding: var(--tab-bar-item-spacing);

    > span:last-child {
      margin-inline-start: var(--tab-bar-item-inline-start);
    }
  }

  // Overflow tab button
  .end-of-tabBar {
    margin-block-start: auto;
    align-self: flex-start;

    + .expand-collapse-button {
      margin-block-start: 0;
    }

    .tab-overflow-button {
      padding: var(--tab-bar-spacing-squish-m);

      > span:last-child:not(:empty) {
        margin-inline-start: var(--tab-bar-spacing-xxs);
      }
    }
  }
}

// Expand collpase button this optional for vertical tab only
.expand-collapse-button {
  border-radius: 0;
  flex-shrink: 0;
  margin-block-start: auto;
  justify-content: flex-start;

  &.align-start {
    justify-content: flex-end;
  }

  // When expanded adjust arrow alignment per vertical bar placement(start & end)
  &.expanded {
    &.align-end {
      justify-content: flex-end;
    }

    &.align-start {
      justify-content: flex-start;
    }
  }
}

// DND Styles
.drop-zone {
  @extend %dragZoneBackground;
  @extend %dragZoneBorderColor;
}

.droppable {
  position: relative;

  &:after {
    content: '';
    position: absolute;
    height: 100%;
    // For LTR and RTL has different calculations, hence using directly left/right instead of logical properties.
    right: var(--drop-zone-end-position);
    left: var(--drop-zone-start-position);
    pointer-events: none;
    @extend %dragZoneBackground;
  }

  &.vertical {
    &:after {
      height: auto;
      right: 0;
      left: 0;
      bottom: var(--drop-zone-end-position);
      top: var(--drop-zone-start-position);
    }
  }
}

.dragableTab:hover {
  .text {
    pointer-events: none;
  }
}
