@use 'utils/icon';
// Portable uses the icons font as well and from portable it is referenced as 'sas-icons', therefore
// it is a hard requirement that the font-family name be 'sas-icons'.
// https://gitlab.sas.com/dvr/html/va-sdk/issues/143
@font-face {
  font-family: 'sas-icons';
  font-weight: normal;
  font-style: normal;
  src: url('~@sas/react-svg/font/sas-icons-regular.ttf') format('truetype');
}

.icon {
  display: inline-flex;
  align-items: center;
  speak: none;
  height: icon.$height;
  width: icon.$width;

  // Icons aren't "containers" and as such should not have border/padding factor
  // into its height/width. Since height/width is used to specify the size of the
  // SVG, it is imperative that the height/width is honored and not impacted by
  // border/padding.
  box-sizing: content-box;

  & > svg {
    height: 100%;
    width: 100%;
  }

  &:focus {
    outline: var(--focus-light-bg);
  }

  :global(.alert) {
    fill: var(--icon-alert-override-fg, var(--icon-alert-fg));
  }
  :global(.warning) {
    fill: var(--icon-warning-override-fg, var(--icon-warning-fg));
  }
  :global(.success) {
    fill: var(--icon-success-override-fg, var(--icon-success-fg));
  }
  :global(.information) {
    fill: var(--icon-information-override-fg, var(--icon-information-fg));
  }
  :global(.neutral) {
    fill: var(--icon-neutral-override-fg, var(--icon-neutral-fg));
  }
  :global(.iconColor) {
    fill: var(--icon-default-override-fg, var(--icon-default-fg));
  }
}
