.vertical .scroll-container, .horizontal .scroll-container {
  display: flex;
  min-height: 1.75rem;
}

.vertical .scroll-content, .horizontal .scroll-content {
  overflow: hidden;
  scroll-behavior: smooth;
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
}

.horizontal {
  width: 100%;
}
.horizontal .scroll-content {
  align-items: center;
}
.horizontal .fade-container {
  flex-grow: 1;
}
.horizontal .scroll-button {
  min-width: 2rem;
}
[dir=ltr] .horizontal .scroll-button:first-child {
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
}
[dir=rtl] .horizontal .scroll-button:first-child {
  border-bottom-left-radius: 0;
  border-top-left-radius: 0;
}
[dir=ltr] .horizontal .scroll-button:last-child {
  border-bottom-left-radius: 0;
  border-top-left-radius: 0;
}
[dir=rtl] .horizontal .scroll-button:last-child {
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
}

.vertical {
  height: 100%;
  box-sizing: border-box;
  overflow: hidden;
}
.vertical .scroll-container {
  flex-direction: column;
  height: 100%;
}
.vertical .scroll-content {
  flex-direction: column;
  block-size: 100%;
}
.vertical .scroll-content.show-scroll-bar-x {
  overflow-x: auto;
}
.vertical .scroll-content.show-scroll-bar-y {
  overflow-y: auto;
}
.vertical .fade-container {
  inline-size: 100%;
}
.vertical .scroll-button:first-child {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}
.vertical .scroll-button:first-child {
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}

.fade-container {
  overflow: hidden;
  position: relative;
}

.scroll-button {
  flex-shrink: 0;
}

.show-scroll-bar {
  overflow: auto;
}

