.container {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  inset-block-start: 0;
  inset-block-end: 0;
  inset-inline-start: 0;
  inset-inline-end: 0;
}

.modal-block-layer {
  background-color: var(--modal-bg);
}

.nonmodal-block-layer {
  pointer-events: none;
}

.nonmodal-block-content {
  pointer-events: auto;
}

