.container {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding-top: var(--searchResult-spacing-m);
  box-sizing: border-box;
  overflow: hidden;
}

.body {
  height: 0;
  flex-grow: 1;
  overflow: auto;
}

.item {
  padding: var(--searchResult-spacing-m);
  box-sizing: border-box;
  width: 100%;
}

.content-container {
  height: 0;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.footer {
  padding: var(--searchResult-spacing-m);
  flex-shrink: 0;
  max-height: 100%;
  box-sizing: border-box;
}

.footer-toolbar {
  flex-grow: 1;
  flex-flow: wrap;
  overflow: hidden auto;
}

.footer-toolbar-button {
  flex-direction: column;
  font-size: var(--button-font-size-s);
  gap: var(--button-spacing-s);
}

