.container {
  width: 100%;
  overflow: hidden;
}

.item {
  overflow: hidden;
}
.item.bold-override span {
  font-weight: var(--list-item-font-weight-thick);
}

.hint-icon {
  opacity: 0.5;
}

.icon-large, .item-counter {
  height: 2rem;
  width: 2rem;
}

.item-counter-container {
  position: relative;
}

.item-counter {
  flex: 0 0 auto;
  color: var(--panelListItem-itemCounter-fg);
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: var(--list-item-font-weight-thick);
  position: absolute;
  top: 0;
}

