:root .sas_components-ImageSelector-__internal__-CropperDialog_dialog-content {
  height: 100%;
  width: 100%;
  overflow: hidden;
  display: grid;
  grid-template-areas: "cropper preview" "toolbar preview" "description preview";
  grid-template-columns: minmax(0, 2fr) minmax(auto, 1fr);
  grid-template-rows: minmax(0, 1fr) auto auto;
  grid-column-gap: var(--cropper-dialog-column-gap);
  grid-row-gap: var(--cropper-dialog-row-gap);
}

:root .sas_components-ImageSelector-__internal__-CropperDialog_cropper {
  grid-area: cropper;
  height: 100%;
  width: 100%;
  padding: 1px;
}

:root .sas_components-ImageSelector-__internal__-CropperDialog_preview {
  grid-area: preview;
  overflow: hidden;
  max-width: 100%;
  position: relative;
}
:root .sas_components-ImageSelector-__internal__-CropperDialog_preview > img {
  position: absolute;
  left: 0;
}

:root .sas_components-ImageSelector-__internal__-CropperDialog_ellipse {
