.draggable {
  cursor: default;
}
.draggable:hover {
  transition: box-shadow calc(var(--dnd-item-anim-dur) * var(--nova-animation-duration-factor, 1)) var(--dnd-item-trans-dur);
  box-shadow: var(--dnd-item-medium-sh);
}

.dragged-item {
  opacity: var(--dnd-moved-item-opacity);
  box-sizing: border-box;
}

.drag-over {
  background: var(--dnd-drop-zone-bg);
}

.drag-over {
  outline: 1px solid var(--dnd-drop-zone-bd);
  outline-offset: -1px;
}

.tile.selected .checkbox, .tile:hover .checkbox {
  display: inline-block;
}

.bar {
  display: flex;
  z-index: 10;
  align-items: flex-start;
  position: absolute;
  width: 100%;
}

.checkbox {
  display: none;
  margin-block-start: 2px;
  margin-inline-start: 2px;
}

.content-container {
  flex-basis: 100%;
  flex-grow: 1;
  overflow: auto;
  position: relative;
  z-index: 5;
}

.thumbnail-container {
  overflow: hidden;
  padding-block-end: 56.25%;
  position: relative;
}
.thumbnail-container > img {
  object-fit: cover;
  position: absolute;
  height: 100%;
  width: 100%;
}
.thumbnail-container > span {
  position: absolute;
  height: 100%;
  width: 100%;
}
.thumbnail-container > span > svg {
  height: 100%;
  width: 100%;
}
.thumbnail-container:focus:after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  outline-offset: calc(var(--focus-width) * -1);
  outline: var(--focus-light-bg);
  pointer-events: none;
}

.content {
  padding: var(--tile-content-pd);
}

.footer {
  display: flex;
  align-items: flex-start;
}

.object-marker {
  background-color: var(--tile-object-marker-bg);
  min-block-size: 0;
}

.object-status {
  margin-block-start: 1px;
  margin-inline-start: 1px;
}

.drag-preview {
  margin: 0;
}
.drag-preview .checkbox {
  display: inline-block;
}

.drop-line {
  position: relative;
}

.drop-line:before {
  content: "";
  background: var(--dnd-drop-line-bg);
  border: 1px solid var(--dnd-drop-line-loop);
  box-shadow: 0 1px 4px 0 var(--dnd-drop-line-loop-shadow);
  position: absolute;
  width: 6px;
  height: 6px;
  border-radius: 100%;
  z-index: 1;
  pointer-events: none;
  inset-inline-start: 0px;
  inset-block-start: 0;
}

.drop-line.place-at-end:before {
  inset-inline-start: auto;
  inset-inline-end: 0px;
}

.drop-line:after {
  content: "";
  background: var(--dnd-drop-line-bg);
  position: absolute;
  pointer-events: none;
  width: 2px;
  height: 100%;
  inset-inline-start: calc((6px / 2) + 0px);
}

.drop-line.place-at-end:after {
  inset-inline-start: auto;
  inset-inline-end: calc((6px / 2) + 0px);
}

.drop-container::after {
  top: 0;
}

.metadata-table-input input {
  padding: 0;
}

.metadata-table-form .metadata-table-row > div + div {
  align-self: center;
  padding: 0;
}

