.list {
  padding: 0;
  user-select: none;
  font-size: var(--list-font-size-m);
}

.item {
  cursor: pointer;
  list-style: none;
  box-sizing: border-box;
  border-block: 1px solid transparent;
  display: flex;
  align-items: center;
  min-height: 0.875rem;
  font-family: var(--list-item-font-family, inherit);
  font-size: var(--list-item-font-size, inherit);
  font-style: var(--list-item-font-style, inherit);
  font-weight: var(--list-item-font-weight, inherit);
  text-decoration: var(--list-item-text-decoration, inherit);
}

.item:not(.virtual-item) {
  // virtual list items are set to position: absolute below
  position: relative;
}

.item-inner {
  display: flex;
  align-items: center;
  width: 100%;
  padding: var(--list-spacing-squish-s);
  color: var(--list-item-fg);
  overflow: hidden;
}

.item-separator {
  height: 0;
  border-block-end: 1px solid var(--list-item-separator-bd);
}

.item-top-separator {
  position: relative;
  &:after {
    content: '';
    position: absolute;
    height: 1px;
    background: var(--list-item-separator-bd);
    inset-inline: 0;
    inset-block-start: 0;
  }
}

// FIXME: Icon css comes after List css in va-sdk (but not in nova-commons).
.item-icon.item-icon {
  margin-inline-end: var(--list-spacing-xs);
  flex-shrink: 0;

  // .iconColor is set inside SVGs for themeing purposes and is not specified
  // using `cn.iconColor`. Using :global() avoids this class name from becoming
  // a module (list scoped class name) and will, therefore, use the non-module
  // class name specified.
  :global(.iconColor) {
    --icon-default-fg: var(--list-item-fg, currentColor);
  }
}

.navigation-icon {
  margin-inline-start: var(--list-spacing-s);
  flex-shrink: 0;

  // .iconColor is set inside SVGs for themeing purposes and is not specified
  // using `cn.iconColor`. Using :global() avoids this class name from becoming
  // a module (list scoped class name) and will, therefore, use the non-module
  // class name specified.
  svg :global(.iconColor) {
    --icon-default-fg: var(--list-item-fg, currentColor);
  }
}

.item-spacer {
  flex-grow: 1;
  flex-basis: 0;
}

.navigation-item {
  padding-inline-end: 0;
}

.item-hoverable:hover:not(.disabled) {
  .item-inner {
    color: var(--list-item-hover-fg);
    background-color: var(--list-item-hover-bg);

    > .item-textSeparator,
    > .item-secondaryText {
      color: var(--list-item-hover-fg);
    }
  }

  // .iconColor is set inside SVGs for themeing purposes and is not specified
  // using `cn.iconColor`. Using :global() avoids this class name from becoming
  // a module (list scoped class name) and will, therefore, use the non-module
  // class name specified.
  .item-icon svg :global(.iconColor),
  .navigation-icon svg :global(.iconColor) {
    --icon-default-fg: var(--list-item-hover-fg, currentColor);
  }
}

.focused:focus::before,
.focused.selected:focus::before,
.selected.pseudo-focused::before {
  content: '';
  position: absolute;
  inset: 0;
  pointer-events: none;
  outline-offset: calc(-1 * var(--focus-width));
  z-index: 1;
}

.focused:focus::before {
  outline: var(--list-item-focus-outline);
}

.focused.selected:focus::before {
  outline: var(--list-item-selected-focus-outline);
}

.selected.pseudo-focused::before {
  outline: var(--list-item-selected-focus-outline);
}

.selected:not(.disabled) {
  @extend .item;
  .item-inner {
    color: var(--list-item-selected-fg);
    background-color: var(--list-item-selected-bg);
    filter: var(--list-item-selected-filter);

    > .item-textSeparator,
    > .item-secondaryText {
      color: var(--list-item-selected-fg);
    }
  }

  // .iconColor is set inside SVGs for themeing purposes and is not specified
  // using `cn.iconColor`. Using :global() avoids this class name from becoming
  // a module (list scoped class name) and will, therefore, use the non-module
  // class name specified.
  .item-icon svg :global(.iconColor),
  .navigation-icon svg :global(.iconColor) {
    --icon-default-fg: var(--list-item-selected-fg, currentColor);
  }
}

.item-hoverable:active:not(.disabled) {
  @extend .item;
  .item-inner {
    color: var(--list-item-active-fg);
    background-color: var(--list-item-active-bg);
    filter: var(--list-item-active-filter);

    > .item-textSeparator,
    > .item-secondaryText {
      color: var(--list-item-active-fg);
    }
  }
}

.item-text {
  // only the right side due to potential italic cutoff
  padding-right: 2px;
  white-space: var(--list-item-white-space, pre);
  overflow: hidden;
  min-width: 0;
  text-overflow: ellipsis;
}

.item-secondaryText {
  @extend .item-text;
  color: var(--list-item-secondary-fg);
  font-family: var(--list-item-font-family, inherit);
  font-size: var(--list-item-font-size, var(--list-font-secondary-size-s));
  font-style: var(--list-item-font-style, inherit);
  font-weight: var(--list-item-font-weight, inherit);
  margin-inline-start: var(--list-spacing-s);
  flex-shrink: 0;
  .disabled & {
    color: var(--list-item-disabled-fg);
  }
}

.highlight {
  color: var(--list-item-highlighted-fg);
  background-color: var(--list-item-highlighted-bg);
}

.item-textSeparator {
  @extend .item-secondaryText;
  flex-shrink: 0;
}

.item-actionIcon {
  margin-inline-start: var(--list-spacing-s);
  flex-shrink: 0;
}

.item-selection-control {
  margin-inline-end: var(--list-spacing-xs);
}

.theme-override > .item:not(.disabled) {
  .item-inner {
    > .item-textSeparator,
    > .item-secondaryText {
      color: var(--list-item-fg);
    }
  }

  &.item-hoverable:hover {
    .item-inner {
      > .item-textSeparator,
      > .item-secondaryText {
        color: var(--list-item-hover-fg);
      }
    }
  }

  &.item-hoverable:active {
    .item-inner {
      > .item-textSeparator,
      > .item-secondaryText {
        color: var(--list-item-active-fg);
      }
    }
  }

  &.selected {
    .item-inner {
      > .item-textSeparator,
      > .item-secondaryText {
        color: var(--list-item-selected-fg);
      }
    }
  }
}

.item.indented .item-inner {
  padding-inline-start: calc(var(--list-spacing-xl) * var(--list-item-indentation-level));
}

.item.disabled .item-inner {
  background-color: var(--list-item-disabled-bg);
  color: var(--list-item-disabled-fg);
  cursor: default;
}

.theme-override > .item.disabled {
  // .iconColor is set inside SVGs for themeing purposes and is not specified
  // using `cn.iconColor`. Using :global() avoids this class name from becoming
  // a module (list scoped class name) and will, therefore, use the non-module
  // class name specified.
  .item-icon svg :global(.iconColor),
  .navigation-icon svg :global(.iconColor) {
    --icon-default-fg: var(--list-item-disabled-fg, currentColor);
  }
}

.virtual-item {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}
