.input-container {
  position: relative;
  height: 0.875rem;
  width: 0.875rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  margin-block-start: var(--radio-spacing-xxs);
  margin-inline-end: var(--radio-spacing-s, 0.5rem);
  box-sizing: border-box;
  background-color: var(--radio-bg);
  border: 1px solid var(--radio-bd);
  transition: calc(var(--radio-trans-duration) * var(--nova-animation-duration-factor, 1)) var(--radio-trans-tf-ease-in-out);
  transition-property: background-color, border-color;
}
.input-container:after {
  background-color: var(--radio-bg);
  transition: background-color calc(var(--radio-trans-duration) * var(--nova-animation-duration-factor, 1)) var(--radio-trans-tf-ease-out);
}
.selected .input-container {
  background-color: var(--radio-selected-bg);
  border-color: var(--radio-selected-bd);
}
.selected .input-container:after {
  content: "";
  display: block;
  height: 0.375rem;
  width: 0.375rem;
  border-radius: 50%;
  position: absolute;
  background-color: var(--radio-selected-fg);
  transition: background-color calc(var(--radio-trans-duration) * var(--nova-animation-duration-factor, 1)) var(--radio-trans-tf-ease-in);
}
.disabled .input-container, .disabled.read-only .input-container {
  background-color: var(--radio-disabled-bg);
  border-color: var(--radio-disabled-bd);
}
.disabled.selected .input-container {
  background-color: var(--radio-disabled-selected-bg);
  border-color: var(--radio-disabled-selected-bd);
}
.disabled.selected .input-container:after {
  background-color: var(--radio-disabled-selected-fg);
}
.read-only .input-container {
  background-color: var(--radio-read-only-bg);
  border-color: var(--radio-read-only-bd);
}
.read-only.selected .input-container:after {
  background-color: var(--radio-read-only-fg);
}
.disabled.read-only.selected .input-container {
  background-color: var(--radio-disabled-bg);
  border-color: var(--radio-disabled-bd);
}
.disabled.read-only.selected .input-container:after {
  background-color: var(--radio-disabled-fg);
}

.label {
  min-width: 0;
}

.normal {
  display: flex;
  align-items: flex-start;
  width: max-content;
  max-width: 100%;
  box-sizing: border-box;
  padding: logical 1px 1px 1px var(--radio-spacing-xs);
}

.normal.no-label {
  padding: 1px;
}
.normal.no-label .input-container {
  margin: 0;
}

.focus-visible {
  position: relative;
}
.focus-visible:after {
  content: "";
  pointer-events: none;
  position: absolute;
  inset-inline-start: 0;
  inset-block-start: 0;
  height: 100%;
  width: 100%;
  outline: var(--focus-light-bg);
  outline-offset: calc(var(--focus-width) * -1);
}

.description {
  margin-inline-start: calc(0.875rem + var(--radio-spacing-s, 0.5rem) + (1px * 2));
  margin-block-start: var(--radio-spacing-xs);
}

.description-text {
  font-size: var(--radio-font-size-s);
  vertical-align: top;
}
.description-text:not(.disabled) {
  color: var(--radio-description-fg);
}

.normal .label label,
.normal .input,
.selected .label label,
.selected .input {
  cursor: pointer;
}
.normal .input-container:after,
.selected .input-container:after {
  cursor: pointer;
}

.disabled .label label,
.disabled .input,
.read-only .label label,
.read-only .input {
  cursor: default;
}
.disabled .input-container:after,
.read-only .input-container:after {
  cursor: default;
}

