.container {
  align-items: stretch;
  margin-inline-start: auto;
  margin-inline-end: auto;
}
// Canvas area
.canvas-slider-container {
  margin-inline-start: auto;
  margin-inline-end: auto;
}
.canvas-thumb {
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 100%;
  border: 1px solid var(--canvas-thumb-bd);
  position: absolute;
  background-color: transparent;
  box-shadow: 0 0 0 2px var(--canvas-thumb-inner-bd) inset;
  box-sizing: border-box;
  &:focus {
    outline: var(--canvas-thumb-focus-outline);
  }
}
.color-canvas {
  width: 10.5rem;
  height: 10.5rem;
  position: relative;
  overflow: hidden;
  touch-action: none; // dont remove touch-action property. required for touch events to work properly
  background-color: var(--custom-color-picker-canvas-bg);
  outline: 1px solid var(--custom-color-picker-canvas-bd); // this is used for outer border as border is impacing in JS calculation
  box-sizing: border-box;
  flex-shrink: 0;
}
// hue slider
.hue-slider-track {
  width: 0.5rem;
  background-image: linear-gradient(#f00, #f0f, #00f, #0ff, #0f0, #ff0, #f00);
  background-color: transparent;
  background-size: 100%;
}

// Actions button
.action-button-container {
  justify-content: end;
}
