.container {
  box-sizing: border-box;
  margin-inline-start: auto;
  margin-inline-end: auto;
}

.label {
  width: 100%;
}

.standard-color-chips {
  display: grid;
  grid-template-columns: repeat(7, auto);
  grid-template-rows: repeat(5, auto);
  grid-auto-flow: column;
  gap: 0.25rem;
}

.recent-custom-chips,
.recent-standard-chips {
  display: grid;
  grid-template-columns: repeat(7, auto);
  grid-gap: 0.25rem;
}

.basic-color-container {
  align-items: center;
  inline-size: 11.25rem;
  margin: 0 auto;
}

.defult-color-selection {
  align-items: flex-start;
  inline-size: 100%;
  margin-inline-start: auto;
  margin-inline-end: auto;
}

