.container_horizontal, .container_vertical {
  display: flex;
}

.container_vertical {
  flex-direction: column;
  min-height: var(--splitter-min-size);
}

.container_horizontal {
  flex-direction: row;
  min-width: var(--splitter-min-size);
  overflow: hidden;
}

.container_horizontal:has(.section:last-child.collapsed) {
  justify-content: flex-end;
}

.section {
  overflow: auto;
  box-sizing: border-box;
}
.container_horizontal .section.hasPadding {
  padding: 0 var(--splitter-section-padding);
}
.container_vertical .section.hasPadding {
  padding: var(--splitter-section-padding) 0;
}
.section.collapsed {
  visibility: hidden;
}

.bar_vertical, .bar_horizontal {
  background-color: var(--splitter-bg);
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
}

.bar_vertical:focus, .bar_horizontal:focus {
  outline: var(--focus-light-bg);
}

.bar_vertical::before, .bar_horizontal::before {
  content: "";
  background: transparent;
  position: absolute;
}

.bar_horizontal {
  width: var(--splitter-bar-size);
  cursor: col-resize;
  flex-direction: column;
  align-items: center;
}
.bar_horizontal::before {
  width: 0.5rem;
  inset-block-start: 0;
  inset-block-end: 0;
}

.bar_vertical {
  height: var(--splitter-bar-size);
  cursor: row-resize;
}
.bar_vertical::before {
  height: 0.5rem;
  inset-inline-start: 0;
  inset-inline-end: 0;
}

.hide-splitter-bar {
  background-color: transparent;
}

.resize-icon {
  pointer-events: none;
  position: absolute;
}

.collapse-icon_horizontal {
  cursor: pointer;
  z-index: 1;
  position: absolute;
}
.resize-icon + .collapse-icon_horizontal {
  inset-block-start: calc(50% + 0.5rem);
}

.collapse-icon_vertical {
  height: var(--splitter-icon-size);
  cursor: pointer;
  z-index: 1;
  position: absolute;
}
.resize-icon + .collapse-icon_vertical {
  inset-inline-start: calc(50% + 0.5rem);
}

.resizable {
  cursor: default;
}

