.container {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.nodes-container {
  height: 100%;
  flex-basis: 100%;
  flex-grow: 1;
  overflow: auto;
}

.nodes {
  margin: 0;
  padding: 0;
  backface-visibility: hidden;
  list-style-type: none;
}

.title {
  font-weight: var(--tree-font-weight-thick);
}
